import Api from "./api.js";
import { get, post, put, del } from "./request.js";

//callback (json, error)

const callbacks = (callback) => {
  return {
    ready: (json) => {
      if (json) callback(json, null);
      else
        callback(
          null,
          " Posiblemente acurrio error del servidor!, intentelo mas tarde."
        );
    },
    error: (error) => callback(null, error),
  };
};

export function getPositions(callback) {
  get(Api.getPositions, {
    ...callbacks(callback),
  });
}

export function getPositionById(id, token, callback) {
  get(Api.getPositionById + id, {
    headers: {
      Authorization: `Bearer ${token}`,
      Accept: "application/json",
    },
    ...callbacks(callback),
  });
}

export function newPosition(data, token, callback) {
  post(Api.newPosition, {
    headers: {
      'Content-Type': "application/json",
      Authorization: `Bearer ${token}`,
      Accept: "application/json",
    },
    body: data,
    ...callbacks(callback),
  });
}

export function updatePosition(data, token, callback) {
  put(Api.updatePosition, {
    headers: {
      'Content-Type': "application/json",
      Authorization: `Bearer ${token}`,
      Accept: "application/json",
    },
    body: data,
    ...callbacks(callback),
  });
}

export function deletePosition(id, token, callback) {
  del(Api.deletePosition, {
    headers: {
      'Content-Type': "application/json",
      Authorization: `Bearer ${token}`,
      Accept: "application/json",
    },
    body: { positionId: id },
    ...callbacks(callback),
  });
}