import NavBar from "../../components/NavBar/NavBar";
import Footer from "../../components/Footer/Footer";
import TitlePage from "../../components/TitlePage/TitlePage";
import TableColumn from "../../components/Table/TableColumn.js";
import TableRow from "../../components/Table/TableRow.js";
import Table from "../../components/Table/Table.js";
import ItemEdit from "../../components/Table/ItemEdit";
import Loading from "../../components/Loading/Loading";
import Alert from "../../components/PushMessage/Alert";
import UsersUpdate from "./UsersUpdate";
import { useState, useEffect } from "react";
import { getUsers, search, unsuscribe } from "../../api/users";
import useUser from "../../hooks/auth/useUser";
import api from "../../api/api";
import ItemUnsuscribe from "../../components/Table/ItemUnsuscribe";
import TextFieldLabel from "../../components/TextField/TextFieldLabel";
import SelectField from "../../components/SelectField/SelectField";
import FindButton from "../../components/Buttons/FindButton";
import ModalAccept from "../../components/Modal/ModalAccept";
import { getLevels } from "../../api/levels";
import PrimaryButton from "../../components/Buttons/PrimaryButton";

function Users() {
  const [users, setUsers] = useState(false);
  const [usersSearch, setUsersSearch] = useState(false);
  const [levels, setLevels] = useState(false);
  const [stateAlert, setStateAlert] = useState(false);
  const [stateLoading, setStateLoading] = useState(false);
  const [modalAccept, setModalAccept] = useState(false);
  const [edit, setEdit] = useState(false);
  const [userData, setUserData] = useUser();

  const User = () => {
    setStateAlert(false);
    setStateLoading(true);

    getUsers(userData.token, (json, error) => {
      setStateLoading(false);

      if (json) {
        if (json?.code == "ok") {
          setUsers(json.data);
          //console.log(json.data);
        } else {
          setStateAlert({
            type: "war",
            message: json.message,
          });
        }
      } else {
        setStateAlert({
          type: "err",
          message: "Ocurrio un error!, " + json.message || error,
        });
      }
    });
  };

  const Levels = () => {
    setStateAlert(false);
    setStateLoading(true);

    getLevels((json, error) => {
      setStateLoading(false);

      if (json) {
        if (json?.code == "ok") {
          setLevels(json.data);
          //console.log(json.data);
        } else {
          setStateAlert({
            type: "war",
            message: json.message,
          });
        }
      } else {
        setStateAlert({
          type: "err",
          message: "Ocurrio un error!, " + json.message || error,
        });
      }
    });
  };

  useEffect(() => {
    User();
    Levels();
  }, []);

  const editUser = (id) => {
    setEdit({
      data: users.find((e) => e.id == id),
      title: "Editar usuario",
      buttonText: "Guardar cambios",
      onUpdate: () => {
        User();
      },
      onCancel: () => setEdit(false),
    });
  };

  const unsuscribeUser = (id) => {
    setModalAccept({
      ok: () => {
        setModalAccept(false);
        setStateLoading(true);
        unsuscribe(id, userData.token, (json, error) => {
          setStateLoading(false);
          if (json.code == "ok") {
            setStateAlert({
              type: "ok",
              message: "Usuario dado de baja con exito!",
            });
            setTimeout(() => User(), 1000);
          } else {
            setStateAlert({
              type: "err",
              message: json.message || error,
            });
          }
        });
      },
      cancel: () => setModalAccept(false),
    });
  };

  const searchUser = (form) => {
    form.preventDefault();
    setStateAlert(false);
    const formValues = Object.fromEntries(new FormData(form.target).entries());
    formValues.level = formValues.level && Number(formValues.level);
    if (
      !formValues.name &&
      !formValues.lastName &&
      !formValues.level &&
      !formValues.birth_date
    ) {
      setStateAlert({
        type: "war",
        message: "No hay ningun dato para filtrar!",
      });
      return;
    } else {
      setStateLoading(true);
      search(formValues, userData.token, (json, error) => {
        setStateLoading(false);
        if (json?.code == "ok") {
          if (json.data.length) {
            setUsersSearch(json.data);
            setStateAlert({
              type: "ok",
              message: "Jugadores encontrados!",
            });
          } else {
            setStateAlert({
              type: "war",
              message: "No se encontro nada!.",
            });
          }
        } else {
          setStateAlert({
            type: "err",
            message: "Ocurrio un error!, " + json.message || error,
          });
        }
      });
    }
  };

  const renderUsers = (item, index) => (
    <tr key={index}>
      <TableRow>
        <div className="rounded-full bg-gray-200 w-14 h-14">
          <img
            className="w-14 h-14 rounded-full"
            src={api.resources + item.avatar}
            alt=" "
            style={{ objectFit: "cover" }}
          />
        </div>
      </TableRow>
      <TableRow>
        <div className="font-semibold text-gray-800 text-sm">{item.name}</div>
      </TableRow>
      <TableRow>
        <div className="text-gray-800 text-sm">{item.last_name}</div>
      </TableRow>
      <TableRow>
        <div className="font-semibold text-blue-800 text-sm">{item.phone}</div>
      </TableRow>
      <TableRow>
        <div className="font-semibold text-red-800 text-sm">{item.email}</div>
      </TableRow>
      <TableRow>
        <div className="font-semibold text-blue-800 text-sm">
          {item.birth_date}
        </div>
      </TableRow>
      <TableRow>
        <div className="font-semibold text-red-800 text-sm">
          {item.low ? "Si" : "No"}
        </div>
      </TableRow>
      <TableRow>
        <div className="flex">
          <ItemEdit id={item.id} onClick={editUser} />
          <ItemUnsuscribe id={item.id} onClick={unsuscribeUser} />
        </div>
      </TableRow>
    </tr>
  );

  return (
    <>
      {stateLoading && <Loading />}
      {stateAlert && <Alert {...stateAlert} />}
      {edit && <UsersUpdate {...edit} />}
      {modalAccept && <ModalAccept {...modalAccept} />}
      <NavBar />
      <div className="pl-2 pr-2 block h-full text-center bg-gray-100 pt-10 pb-20">
        <TitlePage text="Admin - Jugadores" />
        <br></br>
        <div className="w-full p-2 pb-4">
          <form onSubmit={searchUser}>
            <div className="max-w-[300px] m-auto">
              <div className="mb-4">
                <span className="text-md text-gray-800 font-semibold">
                  Buscar jugadores
                </span>
              </div>
              <div className="w-full">
                <TextFieldLabel
                  title="Nombre"
                  type="text"
                  name="name"
                  placeholder="Nombre de usuario"
                />
              </div>
              <div className="w-full mt-3">
                <TextFieldLabel
                  title="Apellidos"
                  type="text"
                  name="lastName"
                  placeholder="Apellidos del usuario"
                />
              </div>
              <div className="w-full mt-3">
                <SelectField
                  title="Nivel de padel"
                  name="level"
                  default="Seleccione el nivel de padel"
                  body={
                    levels
                      ? levels.map((item) => ({
                          value: item.id,
                          label: item.level,
                        }))
                      : [{ value: 0, label: "Cargando..." }]
                  }
                />
              </div>
              <div className="w-full mt-3">
                <TextFieldLabel
                  title="Fecha de nacimiento"
                  type="date"
                  name="birth_date"
                  placeholder="Fecha de nacimiento"
                />
              </div>
              <div className="flex justify-center gap-4">
                <div className="w-max mt-8">
                  <FindButton type="submit" />
                </div>
                <div hidden={!usersSearch} className="w-max mt-8">
                  <div onClick={() => setUsersSearch(false)}>
                    <PrimaryButton
                      type="button"
                      className="rounded-full"
                      text="Todos los usuarios"
                    />
                  </div>
                </div>
              </div>
            </div>
          </form>
        </div>
        <Table
          title="Todos los jugadores"
          columns={
            <TableColumn
              columns={[
                "Foto",
                "Nombre",
                "Apellidos",
                "Teléfono",
                "Correo electrónico",
                "Fecha de nacimiento",
                "Baja",
                "Acciones",
              ]}
            />
          }
          imageHeader={
            <svg
              xmlns="http://www.w3.org/2000/svg"
              fill="none"
              viewBox="0 0 24 24"
              strokeWidth={1.5}
              stroke="currentColor"
              className="w-6 h-6"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                d="M17.982 18.725A7.488 7.488 0 0012 15.75a7.488 7.488 0 00-5.982 2.975m11.963 0a9 9 0 10-11.963 0m11.963 0A8.966 8.966 0 0112 21a8.966 8.966 0 01-5.982-2.275M15 9.75a3 3 0 11-6 0 3 3 0 016 0z"
              />
            </svg>
          }
        >
          {(usersSearch && usersSearch.map(renderUsers)) ||
            (users && users.map(renderUsers))}
        </Table>
      </div>
      <Footer />
    </>
  );
}

export default Users;
