import NavBar from "../../components/NavBar/NavBar";
import Footer from "../../components/Footer/Footer";
import TitlePage from "../../components/TitlePage/TitlePage";
import SelectField from "../../components/SelectField/SelectField";
import TableColumn from "../../components/Table/TableColumn.js";
import TableRow from "../../components/Table/TableRow.js";
import Table from "../../components/Table/Table.js";
import ItemEdit from "../../components/Table/ItemEdit";
import Loading from "../../components/Loading/Loading";
import Alert from "../../components/PushMessage/Alert";
import FindButton from "../../components/Buttons/FindButton";
import PrimaryButton from "../../components/Buttons/PrimaryButton";
import TextFieldLabel from "../../components/TextField/TextFieldLabel";
import ParejasUpdate from "./ParejasUpdate";
import { useState, useEffect, useRef } from "react";
import { getEditions, getEditionWithCampuses } from "../../api/editions";
import useUser from "../../hooks/auth/useUser";
import api from "../../api/api";
import ItemCouple from "../../components/Table/ItemCouple";
import {
  getCouplesByEditionId,
  searchCouple,
  updateCouple,
  cloneCouple
} from "../../api/couples";
import { getUsers } from "../../api/users";
import { getCategories } from "../../api/categories";
import { getGroups } from "../../api/groups";
import ItemClone from "../../components/Table/ItemClone";

function Parejas() {
  const [editions, setEditions] = useState(false);
  //const [editionIdSelected, setEditionIdSelected] = useState(false);
  //const [couples, setCouples] = useState(false);
  const [couplesSearch, setCouplesSearch] = useState(false);
  const [users, setUsers] = useState(false);
  const [categories, setCategories] = useState(false);
  //const [groups, setGroups] = useState(false);
  const [stateAlert, setStateAlert] = useState(false);
  const [stateLoading, setStateLoading] = useState(false);
  const [edit, setEdit] = useState(false);
  const [userData, setUserData] = useUser();
  const seachRef = useRef();

  const Editions = () => {
    setStateAlert(false);
    setStateLoading(true);

    getEditions((json, error) => {
      setStateLoading(false);

      if (json) {
        if (json.code == "ok") {
          setEditions(json.data);
          //console.log(json);
        } else {
          setStateAlert({
            type: "war",
            message: json.message,
          });
        }
      } else {
        setStateAlert({
          type: "err",
          message: "Ocurrio un error!, " + error,
        });
      }
    });
  };

  const Users = () => {
    setStateAlert(false);
    setStateLoading(true);

    getUsers(userData.token, (json, error) => {
      setStateLoading(false);

      if (json) {
        if (json.code == "ok") {
          setUsers(json.data);
          //console.log(json);
        } else {
          setStateAlert({
            type: "war",
            message: json.message,
          });
        }
      } else {
        setStateAlert({
          type: "err",
          message: "Ocurrio un error!, " + error,
        });
      }
    });
  };

  const Categories = () => {
    setStateAlert(false);
    setStateLoading(true);

    getCategories((json, error) => {
      setStateLoading(false);

      if (json) {
        if (json.code == "ok") {
          setCategories(json.data);
          //console.log(json);
        } else {
          setStateAlert({
            type: "war",
            message: json.message,
          });
        }
      } else {
        setStateAlert({
          type: "err",
          message: "Ocurrio un error!, " + error,
        });
      }
    });
  };

  /* const Groups = (edition_id, category_id) => {
    setStateAlert(false);
    setStateLoading(true);

    getGroups(edition_id, category_id, userData.token, (json, error) => {
      setStateLoading(false);

      if (json) {
        if (json.code == "ok") {
          setGroups(json.data);
          //console.log(json);
        } else {
          setStateAlert({
            type: "war",
            message: json.message,
          });
        }
      } else {
        setStateAlert({
          type: "err",
          message: "Ocurrio un error!, " + error,
        });
      }
    });
  }; */

  /* const Couples = (id) => {
    setStateAlert(false);
    setStateLoading(true);
    getCouplesByEditionId(id, userData.token, (json, error) => {
      setStateLoading(false);
      if (json) {
        if (json.code == "ok") {
          setCouples(json.data);
        } else {
          setStateAlert({
            type: "war",
            message: json.message,
          });
        }
      } else {
        setStateAlert({
          type: "err",
          message: "Ocurrio un error!, " + error,
        });
      }
    });
  }; */

  useEffect(() => {
    Editions();
    Users();
    Categories();
  }, []);

  const editCouple = (id) => {
    setStateAlert(false);
    setEdit({
      data: {
        couple:
          //couples && couples.find((e) => e.id == id) ||
          couplesSearch && couplesSearch.find((e) => e.id == id),
        users: users,
      },
      onSubmit: (data) => {
        setStateAlert(false);
        setStateLoading(true);
        updateCouple(data, userData.token, (json, error) => {
          setStateLoading(false);
          if (json?.code == "ok") {
            setStateAlert({
              type: "ok",
              message: "Pareja actualizada con exito!",
            });
            setTimeout(() => {
              Editions();
              Users();
              searchCouples({
                preventDefault: () => {},
                target: seachRef.current,
              });
              setStateAlert(false);
              setEdit(false);
            }, 1000);
          } else {
            setStateAlert({
              type: "err",
              message: "Ocurrio un error!, " + json.message || error,
            });
          }
        });
      },
      onCancel: () => setEdit(false),
    });
  };

  const cloneCouples = (edition_id, couple_id) => {
    setStateAlert(false);
    setStateLoading(true);
    cloneCouple({
      editionId: edition_id,
      enrollmentId: couple_id
    }, userData.token, (json, error) => {
      setStateLoading(false);
      if (json?.code == "ok") {
        setStateAlert({
          type: "ok",
          message: "Pareja clonada con exito!",
        });
        setTimeout(() => {
          Editions();
          Users();
          searchCouples({
            preventDefault: () => {},
            target: seachRef.current,
          });
          setStateAlert(false);
        }, 1000);
      } else {
        setStateAlert({
          type: "err",
          message: "Ocurrio un error!, " + json.message || error,
        });
      }
    });
  };

  //const editionSelected = (e) => {
  //  const edition_id = e.target.value;
  //  if (!edition_id) return;
  //
  //  setCouplesSearch(false);
  //  setEditionIdSelected(edition_id);
  //
  //  Couples(edition_id);
  //};

  const renderCouples = (item, index) => (
    <tr key={index}>
      <ItemCouple
        name={item.name}
        image={item.avatar ? api.resources + item.avatar : null}
      />
      <TableRow>
        <div className="font-semibold text-gray-800 text-sm">
          {item?.user_one?.name}
        </div>
      </TableRow>
      <TableRow>
        <div className="font-semibold text-gray-800 text-sm">
          {item?.user_two?.name}
        </div>
      </TableRow>
      <TableRow>
        <div className="font-semibold text-blue-600 text-sm">
          {item.categoryName}
        </div>
      </TableRow>
      <TableRow>
        <div className="font-semibold text-gray-600 text-sm">
          {item.active ? "Si" : "No"}
        </div>
      </TableRow>
      <TableRow>
        <div className="flex">
          <ItemEdit id={item.id} onClick={editCouple} />
          <ItemClone
            id={item.id}
            onClick={() => cloneCouples(item.edition_id, item.id)}
          />
        </div>
      </TableRow>
    </tr>
  );

  const searchCouples = (form) => {
    form.preventDefault();
    const formValues = convertToNumbers(
      Object.fromEntries(new FormData(form.target).entries())
    );
    setStateAlert(false);
    setStateLoading(true);
    searchCouple(formValues, userData.token, (json, error) => {
      setStateLoading(false);
      if (json?.code == "ok") {
        if (json.data.length) {
          setCouplesSearch(json.data);
          setStateAlert({
            type: "ok",
            message: "Parejas encontradas!",
          });
        } else {
          setStateAlert({
            type: "war",
            message: "No se encontro nada!.",
          });
        }
      } else {
        setStateAlert({
          type: "err",
          message: "Ocurrio un error!, " + json.message || error,
        });
      }
    });
  };

  const convertToNumbers = (formValues) => {
    const result = {};
    for (const [key, value] of Object.entries(formValues)) {
      result[key] = isNaN(value) ? value : Number(value);
    }
    return result;
  };

  //const editionSearchSelected = (e) => {
  //  const edition_id = e.target.value;
  //  if (!edition_id) return;
  //  Groups(edition_id);
  //};

  return (
    <>
      {stateLoading && <Loading />}
      {stateAlert && <Alert {...stateAlert} />}
      {edit && <ParejasUpdate {...edit} />}
      <NavBar />
      <div className="pl-2 pr-2 block h-full text-center bg-gray-100 pt-10 pb-20">
        <TitlePage text="Admin - Parejas de las ligas" />
        <br></br>
        <div className="w-full p-2 pb-6">
          <form ref={seachRef} onSubmit={searchCouples}>
            <div className="max-w-[300px] m-auto">
              <div className="mb-4">
                <span className="text-md text-gray-800 font-semibold">
                  Buscar parejas
                </span>
              </div>
              <div className="w-full">
                <TextFieldLabel
                  title="Nombre de jugador"
                  type="text"
                  name="playerName"
                  placeholder="Nombre del jugador"
                />
              </div>
              <div className="w-full mt-3">
                <SelectField
                  title="Todas las ligas"
                  name="editionId"
                  default="Seleccione una liga"
                  //onChange={editionSearchSelected}
                  body={
                    editions &&
                    editions.map((item) => ({
                      value: item.id,
                      label: item.name,
                    }))
                  }
                />
              </div>
              <div className="w-full mt-3">
                <SelectField
                  title="Todas las categorias"
                  name="categoryId"
                  default="Seleccione una categoria"
                  body={
                    categories &&
                    categories.map((item) => ({
                      value: item.id,
                      label: item.name,
                    }))
                  }
                />
              </div>
              {
                //<div className="w-full mt-3">
                //<SelectField
                //  title="Todas los grupos"
                //  name="groupId"
                //  default="Seleccione un grupo"
                //  body={
                //    groups &&
                //    groups.map((item) => ({
                //      value: item.id,
                //      label: item.name,
                //    }))
                //  }
                ///>
                //</div>
              }
              <div className="w-full mt-3">
                <TextFieldLabel
                  title="Nombre de la pareja"
                  type="text"
                  name="coupleName"
                  placeholder="Nombre de la pareja"
                />
              </div>
              <div className="flex justify-center gap-4">
                <div className="w-max mt-8">
                  <FindButton type="submit" />
                </div>
              </div>
            </div>
          </form>
        </div>
        <Table
          title="Todas las parejas"
          columns={
            <TableColumn
              columns={[
                "Pareja",
                "Jugador 1",
                "Jugador 2",
                "Categoria",
                "Activa",
                "Acciones",
              ]}
            />
          }
          imageHeader={
            <svg
              xmlns="http://www.w3.org/2000/svg"
              fill="none"
              viewBox="0 0 24 24"
              strokeWidth={1.5}
              stroke="currentColor"
              className="w-6 h-6"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                d="M15 19.128a9.38 9.38 0 002.625.372 9.337 9.337 0 004.121-.952 4.125 4.125 0 00-7.533-2.493M15 19.128v-.003c0-1.113-.285-2.16-.786-3.07M15 19.128v.106A12.318 12.318 0 018.624 21c-2.331 0-4.512-.645-6.374-1.766l-.001-.109a6.375 6.375 0 0111.964-3.07M12 6.375a3.375 3.375 0 11-6.75 0 3.375 3.375 0 016.75 0zm8.25 2.25a2.625 2.625 0 11-5.25 0 2.625 2.625 0 015.25 0z"
              />
            </svg>
          }
        >
          {
            couplesSearch && couplesSearch.map(renderCouples) //||
            //  (couples && couples.map(renderCouples))
          }
        </Table>
      </div>
      <Footer />
    </>
  );
}

export default Parejas;
