import Api from "./api.js";
import { get, post } from "./request.js";

//Funciones de autentificacion del usuario
//callback (json, error)

/* -*- Register User -*-
  municipalityCode: 4
  genderId: 1
  positionId: 1
  levelId: 1
  documentId: 2
  documentValue: 95081645466
  email: jhonny@gmail.com
  name: Jhonny
  lastName: Espanna
  phone: 12345678
  password: Admin1234
  acceptPP: 1
  referenceCode: 
  birthDate 1995-08-16
*/

const callbacks = (callback) => {
  return {
    ready: (json) => {
      if (json) callback(json, null);
      else
        callback(
          null,
          " Posiblemente acurrio error del servidor!, intentelo mas tarde."
        );
    },
    error: (error) => callback(null, error),
  };
};

export function login(user, pass, callback) {
  post(Api.login, {
    body: {
      email: user,
      password: pass,
    },
    ...callbacks(callback),
  });
}

export function register(data, callback) {
  post(Api.register, {
    body: data,
    ...callbacks(callback),
  });
}

export function logout(token, callback) {
  get(Api.logout, {
    headers: {
      Authorization: `Bearer ${token}`,
      Accept: "application/json",
    },
    ...callbacks(callback),
  });
}

export function forgotPassword(email, callback) {
  post(Api.forgotPassword, {
    body: {
      email: email,
    },
    ...callbacks(callback),
  });
}

export function resetPassword(email, pin, callback) {
  post(Api.resetPassword, {
    body: {
      email: email,
      pin: pin,
    },
    ...callbacks(callback),
  });
}
