import { useState } from "react";

const sessionData = () => JSON.parse(localStorage.getItem("session_data"));
const defaultState = {
  token: "",
  permissions: "",
};

function useUser() {
  const [user, setUser] = useState(sessionData() || defaultState);
  const setUserSession = (data) => {
    if(!data) return;
    localStorage.setItem("session_data", JSON.stringify(data));
    setUser(data);
  }
  return [user, setUserSession];
}

export default useUser;
