import TitlePage from "../../components/TitlePage/TitlePage";
import ModalAccept from "../../components/Modal/ModalAccept";
import Alert from "../../components/PushMessage/Alert";
import SelectField from "../../components/SelectField/SelectField";
import PrimaryButton from "../../components/Buttons/PrimaryButton";
import DownloadButton from "../../components/Buttons/DownloadButton";
import { stopEdition } from "../../api/editions";
import Loading from "../../components/Loading/Loading";
import useUser from "../../hooks/auth/useUser";
import { useState } from "react";

function LigasStop(props) {
  const [statistics, setStatistics] = useState(false);
  const [modalAccept, setModalAccept] = useState(false);
  const [stateAlert, setStateAlert] = useState(false);
  const [stateLoading, setStateLoading] = useState(false);
  const [userData, setUserData] = useUser();

  const submit = (form) => {
    form.preventDefault();
    const formValues = Object.fromEntries(new FormData(form.target).entries());
    setModalAccept({
      ok: () => {
        setModalAccept(false);
        setStateLoading(true);
        stopEdition(
          props?.data?.id,
          formValues.type,
          userData.token,
          (json, error) => {
            setStateLoading(false);
            console.log(json);
            if (json.code == "ok") {
              setStateAlert({
                type: "ok",
                message: "Liga detenida con exito!",
              });
              setTimeout(() => setStateAlert(false), 1000);
              setStatistics(json.data);
              //setTimeout(() => props.onCancel(), 1000);
            } else {
              setStateAlert({
                type: "err",
                message: json.message || error,
              });
            }
          }
        );
      },
      cancel: () => setModalAccept(false),
    });
  };

  return (
    <>
      {modalAccept && <ModalAccept {...modalAccept} />}
      {stateAlert && <Alert {...stateAlert} />}
      {stateLoading && <Loading {...stateLoading} />}
      <div className="align-center absolute pl-2 pr-2 w-full h-screen text-center bg-gray-100 pb-20">
        <div className="flex justify-end items-end mt-4 mr-4">
          <svg
            xmlns="http://www.w3.org/2000/svg"
            fill="none"
            viewBox="0 0 24 24"
            strokeWidth={1.5}
            stroke="currentColor"
            className="w-6 h-6 cursor-pointer"
            onClick={props.onCancel}
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              d="M6 18L18 6M6 6l12 12"
            />
          </svg>
        </div>
        <TitlePage
          text={"Detener liga y generar estadisticas: " + props.title}
        />
        <div className="w-screen mt-10 Xflex flex-wrap justify-center Xgap-4">
          <div className="m-auto max-w-md w-full" hidden={statistics}>
            <form onSubmit={submit}>
              <div className="p-4">
                <SelectField
                  title="Tipo de archivo"
                  name="type"
                  default="Seleccione tipo de la descarga"
                  body={[
                    { value: 0, label: "Archivo Excel" },
                    { value: 1, label: "Archivo Pdf" },
                  ]}
                  required
                  noVoid
                />
              </div>
              <div className="w-full mt-6">
                <PrimaryButton
                  type="submit"
                  className="rounded-lg"
                  text="Detener y generar estadísticas"
                />
              </div>
            </form>
          </div>
          <div hidden={!statistics}>
            <div className="m-auto grid grid-cols-2 max-[600px]:grid-cols-1 gap-4 Xw-full max-w-xl p-4">
              <div className="flex p-4 bg-gray-600 rounded-md text-white">
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  fill="none"
                  viewBox="0 0 24 24"
                  strokeWidth={1.5}
                  stroke="currentColor"
                  className="w-6 h-6"
                >
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    d="M18 18.72a9.094 9.094 0 003.741-.479 3 3 0 00-4.682-2.72m.94 3.198l.001.031c0 .225-.012.447-.037.666A11.944 11.944 0 0112 21c-2.17 0-4.207-.576-5.963-1.584A6.062 6.062 0 016 18.719m12 0a5.971 5.971 0 00-.941-3.197m0 0A5.995 5.995 0 0012 12.75a5.995 5.995 0 00-5.058 2.772m0 0a3 3 0 00-4.681 2.72 8.986 8.986 0 003.74.477m.94-3.197a5.971 5.971 0 00-.94 3.197M15 6.75a3 3 0 11-6 0 3 3 0 016 0zm6 3a2.25 2.25 0 11-4.5 0 2.25 2.25 0 014.5 0zm-13.5 0a2.25 2.25 0 11-4.5 0 2.25 2.25 0 014.5 0z"
                  />
                </svg>
                <label className="pl-3 font-semibold">
                  {"Número de grupos: " + statistics?.numberOfGroups}
                </label>
              </div>
              <div className="flex p-4 bg-gray-600 rounded-md text-white">
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  fill="none"
                  viewBox="0 0 24 24"
                  strokeWidth={1.5}
                  stroke="currentColor"
                  className="w-6 h-6"
                >
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    d="M9.568 3H5.25A2.25 2.25 0 003 5.25v4.318c0 .597.237 1.17.659 1.591l9.581 9.581c.699.699 1.78.872 2.607.33a18.095 18.095 0 005.223-5.223c.542-.827.369-1.908-.33-2.607L11.16 3.66A2.25 2.25 0 009.568 3z"
                  />
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    d="M6 6h.008v.008H6V6z"
                  />
                </svg>
                <label className="pl-3 font-semibold">
                  {"Número de categorías: " + statistics?.numberOfCategories}
                </label>
              </div>
              <div className="flex p-4 bg-gray-600 rounded-md text-white">
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  fill="none"
                  viewBox="0 0 24 24"
                  strokeWidth={1.5}
                  stroke="currentColor"
                  className="w-6 h-6"
                >
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    d="M15 19.128a9.38 9.38 0 002.625.372 9.337 9.337 0 004.121-.952 4.125 4.125 0 00-7.533-2.493M15 19.128v-.003c0-1.113-.285-2.16-.786-3.07M15 19.128v.106A12.318 12.318 0 018.624 21c-2.331 0-4.512-.645-6.374-1.766l-.001-.109a6.375 6.375 0 0111.964-3.07M12 6.375a3.375 3.375 0 11-6.75 0 3.375 3.375 0 016.75 0zm8.25 2.25a2.625 2.625 0 11-5.25 0 2.625 2.625 0 015.25 0z"
                  />
                </svg>
                <label className="pl-3 font-semibold">
                  {"Número de parejas: " + statistics?.numberOfCouples}
                </label>
              </div>
              <div className="flex p-4 bg-gray-600 rounded-md text-white">
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  fill="none"
                  viewBox="0 0 24 24"
                  strokeWidth={1.5}
                  stroke="currentColor"
                  className="w-6 h-6"
                >
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    d="M5.25 5.653c0-.856.917-1.398 1.667-.986l11.54 6.348a1.125 1.125 0 010 1.971l-11.54 6.347a1.125 1.125 0 01-1.667-.985V5.653z"
                  />
                </svg>
                <label className="pl-3 font-semibold">
                  {"Partidos jugados: " + statistics?.numberOfMatches}
                </label>
              </div>
            </div>
            <div className="pt-3 m-auto">
              <a target="_blank" href={statistics?.download_link}>
                <DownloadButton
                  text={
                    statistics?.download_link?.includes("pdf")
                      ? "Descargar pdf"
                      : "Descargar xlsx"
                  }
                />
              </a>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default LigasStop;
