import NewsCard from "../../components/Card/NewsCard";
import NavBar from "../../components/NavBar/NavBar";
import Footer from "../../components/Footer/Footer";
import TitlePage from "../../components/TitlePage/TitlePage";
import TableColumn from "../../components/Table/TableColumn.js";
import TableRow from "../../components/Table/TableRow.js";
import Table from "../../components/Table/Table.js";
import ItemEdit from "../../components/Table/ItemEdit";
import ItemDelete from "../../components/Table/ItemDelete";
import AddButton from "../../components/Buttons/AddButton";
import Loading from "../../components/Loading/Loading";
import Alert from "../../components/PushMessage/Alert";
import NoticiasUpdate from "./NoticiasUpdate";
import { useState, useEffect } from "react";
import useUser from "../../hooks/auth/useUser";
import ModalAccept from "../../components/Modal/ModalAccept";
import { deleteNews, getNews, newNews, updateNews, uploadNewImage } from "../../api/news";
import api from "../../api/api";

function Noticias() {
  const [news, setNews] = useState(false);
  const [stateAlert, setStateAlert] = useState(false);
  const [stateLoading, setStateLoading] = useState(false);
  const [edit, setEdit] = useState(false);
  const [modalAccept, setModalAccept] = useState(false);
  const [userData, setUserData] = useUser();

  const News = () => {
    setStateAlert(false);
    setStateLoading(true);

    getNews(userData.token, (json, error) => {
      setStateLoading(false);

      if (json) {
        if (json.code == "ok") {
          setNews(json.data);
        } else {
          setStateAlert({
            type: "war",
            message: json.message,
          });
        }
      } else {
        setStateAlert({
          type: "err",
          message: "Ocurrio un error!, " + error,
        });
      }
    });
  };

  useEffect(() => {
    News();
  }, []);

  const addNoticia = () => {
    setStateAlert(false);
    setEdit({
      title: "Noticia nueva",
      buttonText: "Crear nueva",
      onSubmit: (data) => {
        setStateAlert(false);
        setStateLoading(true);

        const image = data.image;
        delete data.image;

        newNews(data, userData.token, (json, error) => {
          setStateLoading(false);
          if (json.code == "ok") {
            setStateAlert({
              type: "ok",
              message: "Noticia creada con exito!",
            });
            if (image?.name == "") {
              setTimeout(() => News(), 1000);
              setEdit(false);
            } else uploadImage(json.news.id, image);
          } else {
            setStateAlert({
              type: "err",
              message: json.message || error,
            });
          }
        });
      },
      onCancel: () => setEdit(false),
    });
  };

  const editNoticia = (id) => {
    setStateAlert(false);
    setEdit({
      title: "Editar noticia",
      data: {
        notice: news.find((e) => e.id == id),
      },
      buttonText: "Guardar cambios",
      onSubmit: (dataUpdate) => {
        setStateAlert(false);
        setStateLoading(true);

        const image = dataUpdate.image;
        delete dataUpdate.image;

        updateNews(dataUpdate, userData.token, (json, error) => {
          setStateLoading(false);
          if (json.code == "ok") {
            setStateAlert({
              type: "ok",
              message: "Noticia actualizado con exito!",
            });
            if (image?.name == "") {
              setTimeout(() => News(), 1000);
              setEdit(false);
            } else uploadImage(dataUpdate.newsId, image);
          } else {
            setStateAlert({
              type: "err",
              message: json.message || error,
            });
          }
        });
      },
      onCancel: () => setEdit(false),
    });
  };

  const removeNoticia = (id) => {
    setModalAccept({
      ok: () => {
        setStateLoading(true);
        setModalAccept(false);
        deleteNews(id, userData.token, (json, error) => {
          setStateLoading(false);
          if (json.code == "ok") {
            setStateAlert({
              type: "ok",
              message: "Eliminado con exito!",
            });
            setTimeout(() => News(), 1000);
          } else {
            setStateAlert({
              type: "err",
              message: json.message || error,
            });
          }
        });
      },
      cancel: () => setModalAccept(false),
    });
  };

  const uploadImage = (id, image) => {
    const formData = new FormData();
    formData.append("newsId", id);
    formData.append("photo", image);
    //console.log(formData);
    //console.log(id, cover);
    setStateAlert(false);
    setStateLoading(true);
    uploadNewImage(formData, userData.token, (json, error) => {
      setStateLoading(false);
      if (json.code == "ok") {
        setStateAlert({
          type: "ok",
          message: "Foto de noticia subida con exito!",
        });
        setTimeout(() => News(), 1000);
        setEdit(false);
      } else {
        setStateAlert({
          type: "err",
          message: json.message || error,
        });
      }
      //console.log(json);
    });
  };

  return (
    <>
      {stateLoading && <Loading />}
      {stateAlert && <Alert {...stateAlert} />}
      {edit && <NoticiasUpdate {...edit} />}
      {modalAccept && <ModalAccept {...modalAccept} />}
      <NavBar />
      <div className="pl-2 pr-2 block h-full text-center bg-gray-100 pt-10 pb-20">
        <TitlePage text="Admin - Noticias" />
        <div className="flex justify-end items-end lg:pr-32 mb-6">
          <div className="w-max h-max" onClick={addNoticia}>
            <AddButton />
          </div>
        </div>
        <Table
          title="Todas las noticias"
          columns={
            <TableColumn
              columns={["Imagen", "Titulo", "Detalles", "Estado", "Acciones"]}
            />
          }
          imageHeader={
            <svg
              xmlns="http://www.w3.org/2000/svg"
              fill="none"
              viewBox="0 0 24 24"
              strokeWidth={1.5}
              stroke="currentColor"
              className="w-6 h-6"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                d="M12 7.5h1.5m-1.5 3h1.5m-7.5 3h7.5m-7.5 3h7.5m3-9h3.375c.621 0 1.125.504 1.125 1.125V18a2.25 2.25 0 01-2.25 2.25M16.5 7.5V18a2.25 2.25 0 002.25 2.25M16.5 7.5V4.875c0-.621-.504-1.125-1.125-1.125H4.125C3.504 3.75 3 4.254 3 4.875V18a2.25 2.25 0 002.25 2.25h13.5M6 7.5h3v3H6v-3z"
              />
            </svg>
          }
        >
          {news &&
            news.map((item, index) => (
              <tr key={index}>
                <TableRow>
                  <div className="rounded-lg bg-gray-200 w-16 h-16">
                    <img
                      className="w-16 h-16 rounded-lg"
                      src={api.resources + item.photo}
                      alt=" "
                      style={{ objectFit: "cover" }}
                    />
                  </div>
                </TableRow>
                <TableRow>
                  <div className="p-3 font-semibold text-gray-800 text-sm">
                    {item.title.length > 26
                      ? item.title.substring(0, 26) + "..."
                      : item.title}
                  </div>
                </TableRow>
                <TableRow>
                  <div className="p-3 text-gray-800 text-sm">
                    {item.details.length > 40
                      ? item.details.substring(0, 40) + "..."
                      : item.details}
                  </div>
                </TableRow>
                <TableRow>
                  <div className="p-3 font-semibold text-sm">
                    {item.status ? (
                      <div className="text-blue-600">Activa</div>
                    ) : (
                      <div className="text-red-600">Desactiva</div>
                    )}
                  </div>
                </TableRow>
                <TableRow>
                  <div className="flex">
                    <ItemEdit id={item.id} onClick={editNoticia} />
                    <ItemDelete id={item.id} onClick={removeNoticia} />
                  </div>
                </TableRow>
              </tr>
            ))}
        </Table>
      </div>
      <Footer />
    </>
  );
}

export default Noticias;

function XNoticias() {
  return (
    <div>
      <NewsCard
        image="https://padelamigos.es/wp-content/uploads/2022/09/IMG_2874-300x200.jpg"
        title="Noticia title"
        des="Noticia description dfaf gfa gadf fadfad fad fdaf ad eqffefafd"
        time="21/32/23"
      />
    </div>
  );
}

