import TitlePage from "../../components/TitlePage/TitlePage";
import PrimaryButton from "../../components/Buttons/PrimaryButton.js";
import ModalAccept from "../../components/Modal/ModalAccept";
import Alert from "../../components/PushMessage/Alert";
import { useEffect, useState } from "react";
import { updateCampusesByEditionId } from "../../api/editions";
import Loading from "../../components/Loading/Loading";
import useUser from "../../hooks/auth/useUser";
import CheckField from "../../components/CheckField/CheckField";
import Table from "../../components/Table/Table";
import TableColumn from "../../components/Table/TableColumn";
import TableRow from "../../components/Table/TableRow";
import { getCampuses } from "../../api/campuses";
import api from "../../api/api";

function LigasSedesUpdate(props) {
  const [campuses, setCampuses] = useState(false);
  const [campusesSelected, setCampusesSelected] = useState(false);
  const [modalAccept, setModalAccept] = useState(false);
  const [stateAlert, setStateAlert] = useState(false);
  const [stateLoading, setStateLoading] = useState(false);
  const [userData, setUserData] = useUser();

  const Campuses = () => {
    setStateLoading(true);
    getCampuses((json, error) => {
      setStateLoading(false);
      if (json.code == "ok") {
        setCampuses(json.data);
        const data = json.data.map((item) => ({
          id: item.id,
          state: props.data.campuses.find((e) => e.id == item.id)
            ? true
            : false,
        }));
        setCampusesSelected(data);
        //console.log(JSON.stringify(json.data));
        //console.log(props.data.campuses);
      } else {
        setStateAlert({
          type: "err",
          message: json.message || error,
        });
      }
    });
  };

  useEffect(() => {
    Campuses();
    //console.log(JSON.stringify(props.data.campuses));
  }, []);

  const submit = () => {
    const data = campusesSelected
      .filter((obj) => obj.state === true)
      .map((obj) => obj.id);
    const requestData = { ids: data, edition_id: props.data.edition_id };
    console.log(requestData);
    setModalAccept({
      ok: () => {
        setModalAccept(false);
        setStateLoading(true);
        updateCampusesByEditionId(
          requestData,
          userData.token,
          (json, error) => {
            setStateLoading(false);
            //console.log(json);
            if (json.code == "ok") {
              setStateAlert({
                type: "ok",
                message: "Sedes de la liga actualizadas con exito!",
              });
              setTimeout(() => setStateAlert(false), 1000);
              props.onUpdate();
              //setTimeout(() => props.onCancel(), 1000);
            } else {
              setStateAlert({
                type: "err",
                message: json.message || error,
              });
            }
          }
        );
      },
      cancel: () => setModalAccept(false),
    });
  };

  const onCheckBoxChange = (id) => {
    if (campusesSelected) {
      const data = campusesSelected;
      data.map((item) => {
        if (item.id == id) item.state = !item.state;
      });
      setCampusesSelected(data);
      //console.log(data);
    }
  };

  return (
    <>
      {modalAccept && <ModalAccept {...modalAccept} />}
      {stateAlert && <Alert {...stateAlert} />}
      {stateLoading && <Loading {...stateLoading} />}
      <div className="align-center absolute pl-2 pr-2 w-full h-full text-center bg-gray-100 pb-20">
        <div className="flex justify-end items-end mt-4 mr-4">
          <svg
            xmlns="http://www.w3.org/2000/svg"
            fill="none"
            viewBox="0 0 24 24"
            strokeWidth={1.5}
            stroke="currentColor"
            className="w-6 h-6 cursor-pointer"
            onClick={props.onCancel}
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              d="M6 18L18 6M6 6l12 12"
            />
          </svg>
        </div>
        <TitlePage text={"Sedes de la liga: " + props.title} />
        <div className="w-screen mt-8 Xflex flex-wrap justify-center Xgap-4">
          <div>
            <Table
              title="Todas las sedes"
              columns={
                <TableColumn columns={["Portada", "Nombre", "Activa"]} />
              }
              imageHeader={
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  fill="none"
                  viewBox="0 0 24 24"
                  strokeWidth={1.5}
                  stroke="currentColor"
                  className="w-6 h-6"
                >
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    d="M3.75 21h16.5M4.5 3h15M5.25 3v18m13.5-18v18M9 6.75h1.5m-1.5 3h1.5m-1.5 3h1.5m3-6H15m-1.5 3H15m-1.5 3H15M9 21v-3.375c0-.621.504-1.125 1.125-1.125h3.75c.621 0 1.125.504 1.125 1.125V21"
                  />
                </svg>
              }
            >
              {campuses &&
                campuses.map((item, index) => (
                  <tr key={index}>
                    <TableRow>
                      <div className="rounded-lg bg-gray-200 w-16 h-16 mt-1 mb-1">
                        <img
                          className="w-16 h-16 rounded-lg"
                          src={item.cover ? api.resources + item.cover : null}
                          alt=" "
                          style={{ objectFit: "cover" }}
                        />
                      </div>
                    </TableRow>
                    <TableRow>
                      <div className="p-3 font-semibold text-gray-800 text-sm">
                        {item.name}
                      </div>
                    </TableRow>
                    <TableRow>
                      <div className="flex">
                        <CheckField
                          selected={props.data.campuses.find(
                            (e) => e.id == item.id
                          )}
                          onChange={() => onCheckBoxChange(item.id)}
                        />
                      </div>
                    </TableRow>
                  </tr>
                ))}
            </Table>
          </div>
          <div className="w-full mt-6">
            <div onClick={submit}>
              <PrimaryButton className="rounded-lg" text={props.buttonText} />
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default LigasSedesUpdate;
