import React from "react"
import Slider5 from "../../../assets/slider 5.png";
import PrimaryButton from "../../../components/Buttons/PrimaryButton";
import CheckField from "../../../components/CheckField/CheckField";
import { Link } from "react-router-dom";
import SecundaryButton from "../../../components/Buttons/SecundaryButton";
import Image from "../../../components/Image";

const Checkout = ({ setOptionPay }) => {

    const handlePayClick = ({ currentTarget }) => {
        setOptionPay(currentTarget.id == 'payNow' ? 1 : 2);
    }
    return <div className="grid grid-cols-1 md:grid-cols-2 gap-3">
        <figure className="responsive-image">
            <div className="shadow-lg overflow-hidden rounded-md">
                <Image src={Slider5} alt="" />
            </div>
        </figure>
        <div className="flex flex-col justify-center">
           
           
            <div className="">
                <PrimaryButton
                    type="submit"
                    id="payNow"
                    className="rounded-lg w-full mb-4"
                    text={'Inscribirme y pagar mi cuota'}
                    handleCLick={handlePayClick}
                />
                <SecundaryButton
                    type="submit"
                    id="payLater"
                    className="rounded-lg w-full mb-4"
                    text={'Inscribirme y pagar despuès'}
                    handleClick={handlePayClick}
                />
            </div>
            
            <CheckField
                    id="acceptPP"
                    name="acceptPP"
                    text={
                        <>
                            Al inscribirse al torneo acepta nuestra politica de protección de datos!{" "}
                            <Link
                                to={"/policy"}
                                className="font-medium text-primary-600 hover:underline dark:text-primary-500"
                            >
                                ver
                            </Link>
                        </>
                    }
                    required
                />
        </div>
    </div>
}


export default Checkout;