import NavBar from "../../components/NavBar/NavBar";
import Footer from "../../components/Footer/Footer";
import TitlePage from "../../components/TitlePage/TitlePage";
import TableColumn from "../../components/Table/TableColumn.js";
import TableRow from "../../components/Table/TableRow.js";
import Table from "../../components/Table/Table.js";
import ItemEdit from "../../components/Table/ItemEdit";
import ItemDelete from "../../components/Table/ItemDelete";
import AddButton from "../../components/Buttons/AddButton";
import Loading from "../../components/Loading/Loading";
import Alert from "../../components/PushMessage/Alert";
import AccidentInsurancesUpdate from "./AccidentInsurancesUpdate";
import { useState, useEffect } from "react";
import useUser from "../../hooks/auth/useUser";
import ModalAccept from "../../components/Modal/ModalAccept";
import {
  deleteAccidentInsurance,
  getAccidentInsurances,
  newAccidentInsurance,
  updateAccidentInsurance,
} from "../../api/accidentInsurances";

function AccidentInsurances() {
  const [accidentInsurances, setAccidentInsurances] = useState(false);
  const [stateAlert, setStateAlert] = useState(false);
  const [stateLoading, setStateLoading] = useState(false);
  const [edit, setEdit] = useState(false);
  const [modalAccept, setModalAccept] = useState(false);
  const [userData, setUserData] = useUser();

  const AccidentInsurance = () => {
    setStateAlert(false);
    setStateLoading(true);

    getAccidentInsurances((json, error) => {
      setStateLoading(false);

      if (json) {
        if (json.code == "ok") {
          setAccidentInsurances(json.data);
        } else {
          setStateAlert({
            type: "war",
            message: json.message,
          });
        }
      } else {
        setStateAlert({
          type: "err",
          message: "Ocurrio un error!, " + error,
        });
      }
    });
  };

  useEffect(() => {
    AccidentInsurance();
  }, []);

  const addAccidentInsurance = () => {
    setEdit({
      title: "Seguro de accidente nuevo",
      buttonText: "Crear nuevo",
      onSubmit: (data) => {
        setStateLoading(true);
        newAccidentInsurance(data, userData.token, (json, error) => {
          setStateLoading(false);
          if (json.code == "ok") {
            setStateAlert({
              type: "ok",
              message: "Seguro de accidente creado con exito!",
            });
            setTimeout(() => AccidentInsurance(), 1000);
            setEdit(false);
          } else {
            setStateAlert({
              type: "err",
              message: json.message || error,
            });
          }
        });
      },
      onCancel: () => setEdit(false),
    });
  };

  const editAccidentInsurance = (id) => {
    let data = accidentInsurances.find((e) => e.id == id);
    setEdit({
      title: "Editar seguro de accidente",
      data: data,
      buttonText: "Guardar cambios",
      onSubmit: (dataUpdate) => {
        setStateLoading(true);
        updateAccidentInsurance(dataUpdate, userData.token, (json, error) => {
          setStateLoading(false);
          if (json.code == "ok") {
            setStateAlert({
              type: "ok",
              message: "Seguro de accidente actualizado con exito!",
            });
            setTimeout(() => AccidentInsurance(), 1000);
            setEdit(false);
          } else {
            setStateAlert({
              type: "err",
              message: json.message || error,
            });
          }
        });
      },
      onCancel: () => setEdit(false),
    });
  };

  const removeAccidentInsurance = (id) => {
    setModalAccept({
      ok: () => {
        setStateLoading(true);
        setModalAccept(false);
        deleteAccidentInsurance(id, userData.token, (json, error) => {
          setStateLoading(false);
          if (json.code == "ok") {
            setStateAlert({
              type: "ok",
              message: "Eliminado con exito!",
            });
            setTimeout(() => AccidentInsurance(), 1000);
            setEdit(false);
          } else {
            setStateAlert({
              type: "err",
              message: json.message || error,
            });
          }
        });
      },
      cancel: () => setModalAccept(false),
    });
  };

  return (
    <>
      {stateLoading && <Loading />}
      {stateAlert && <Alert {...stateAlert} />}
      {edit && <AccidentInsurancesUpdate {...edit} />}
      {modalAccept && <ModalAccept {...modalAccept} />}
      <NavBar />
      <div className="pl-2 pr-2 block h-full text-center bg-gray-100 pt-10 pb-20">
        <TitlePage text="Admin - Seguro de accidentes" />
        <div className="flex justify-end items-end lg:pr-32 mb-6">
          <div className="w-max h-max" onClick={addAccidentInsurance}>
            <AddButton />
          </div>
        </div>
        <Table
          title="Seguros de accidentes"
          columns={
            <TableColumn
              columns={[
                "Nombre",
                "Teléfono",
                "Fecha de creación",
                "Fecha de actualización",
                "Acciones",
              ]}
            />
          }
          imageHeader={
            <svg
              xmlns="http://www.w3.org/2000/svg"
              fill="none"
              viewBox="0 0 24 24"
              strokeWidth={1.5}
              stroke="currentColor"
              className="w-6 h-6"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                d="M9 12.75L11.25 15 15 9.75m-3-7.036A11.959 11.959 0 013.598 6 11.99 11.99 0 003 9.749c0 5.592 3.824 10.29 9 11.623 5.176-1.332 9-6.03 9-11.622 0-1.31-.21-2.571-.598-3.751h-.152c-3.196 0-6.1-1.248-8.25-3.285z"
              />
            </svg>
          }
        >
          {accidentInsurances &&
            accidentInsurances.map((item, index) => (
              <tr key={index}>
                <TableRow>
                  <div className="p-3 font-semibold text-gray-800 text-sm">
                    {item.name}
                  </div>
                </TableRow>
                <TableRow>
                  <div className="p-3 font-semibold text-gray-700 text-sm">
                    {item.phone}
                  </div>
                </TableRow>
                <TableRow>
                  <div className="font-semibold text-blue-500 text-sm">
                    {new Date(item.created_at).toLocaleString()}
                  </div>
                </TableRow>
                <TableRow>
                  <div className="font-semibold text-red-500 text-sm">
                    {new Date(item.updated_at).toLocaleString()}
                  </div>
                </TableRow>
                <TableRow>
                  <div className="flex">
                    <ItemEdit id={item.id} onClick={editAccidentInsurance} />
                    <ItemDelete
                      id={item.id}
                      onClick={removeAccidentInsurance}
                    />
                  </div>
                </TableRow>
              </tr>
            ))}
        </Table>
      </div>
      <Footer />
    </>
  );
}

export default AccidentInsurances;
