import React from "react"
import api from "../../../api/api.js";
import IconUser from "../../../assets/user.png";
import PrimaryButton from "../../../components/Buttons/PrimaryButton.js";


const Item = ({ title, value }) =>
(<div className="mb-4">
    <p className="mb-2 font-semibold text-sm">{title}: <span>{value}</span></p>
    <hr />
</div>
)


const UserDetailsForm = ({ playerTwo: { name, avatar, email, phone, birth_date }, handleClick }) =>
(
    <div className="user-data">
        <h1 className="title">
            Datos del jugador
        </h1>
        <div className="flex flex-col sm:flex-row justify-between items-center">
            <figure className="rounded-full overflow-hidden mx-2">
                <img src={avatar ? api.resources + avatar : IconUser} alt="" className="w-36 h-36" />
            </figure>
            <div className="grow">
                <Item title={'Nombre'} value={name} />
                <Item title={'Email'} value={email} />
                <Item title={'Teléfono'} value={phone} />
                {birth_date && <Item title={'Fecha de nacimiento'} value={birth_date} />}
            </div>
        </div>
        <PrimaryButton
            type="button"
            className="w-full focus:ring-4 focus:outline-none focus:ring-primary-300 font-medium rounded-lg text-sm px-5 py-2.5 mt-8"
            text="Aceptar"
            handleCLick={handleClick}
        />
    </div>
);

export default UserDetailsForm;