import Api from "./api.js";
import { get, post, put, del } from "./request.js";

//callback (json, error)

const callbacks = (callback) => {
  return {
    ready: (json) => {
      if (json) callback(json, null);
      else
        callback(
          null,
          " Posiblemente acurrio error del servidor!, intentelo mas tarde."
        );
    },
    error: (error) => callback(null, error),
  };
};

export function getMunicipalities(callback) {
  get(Api.getMunicipalities, {
    ...callbacks(callback),
  });
}

export function getMunicipalitiesByProvinceId(id, callback) {
  get(Api.getMunicipalitiesByProvinceId + id, {
    ...callbacks(callback),
  });
}

export function getMunicipalityById(id, token, callback) {
  get(Api.getMunicipalityById + id, {
    headers: {
      Authorization: `Bearer ${token}`,
      Accept: "application/json",
      'Content-Type': "application/json",
    },
    ...callbacks(callback),
  });
}

export function newMunicipality(data, token, callback) {
  post(Api.newMunicipality, {
    headers: {
      Authorization: `Bearer ${token}`,
      Accept: "application/json",
      'Content-Type': "application/json",
    },
    body: data,
    ...callbacks(callback),
  });
}

export function updateMunicipality(data, token, callback) {
  put(Api.updateMunicipality, {
    headers: {
      Authorization: `Bearer ${token}`,
      Accept: "application/json",
      'Content-Type': "application/json",
    },
    body: data,
    ...callbacks(callback),
  });
}

export function deleteMunicipality(id, token, callback) {
  del(Api.deleteMunicipality, {
    headers: {
      Authorization: `Bearer ${token}`,
      Accept: "application/json",
      'Content-Type': "application/json",
    },
    body: { municipalityId: id },
    ...callbacks(callback),
  });
}
