import NavBar from "../../components/NavBar/NavBar";
import Footer from "../../components/Footer/Footer";
import ItemEdit from "../../components/Table/ItemEdit";
import ModalAccept from "../../components/Modal/ModalAccept";
import PrimaryButton from "../../components/Buttons/PrimaryButton";
import TextFieldLabel from "../../components/TextField/TextFieldLabel.js";
import SelectField from "../../components/SelectField/SelectField";
import { useState, useEffect } from "react";
import useUser from "../../hooks/auth/useUser";
import { getMyInfo, updateAvatarImage, updateMyInfo } from "../../api/userInfo";
import Loading from "../../components/Loading/Loading";
import Alert from "../../components/PushMessage/Alert";
import { getMunicipalities } from "../../api/municipalities";
import { getGenders } from "../../api/genders";
import { getPositions } from "../../api/positions";
import { getLevels } from "../../api/levels";
import api from "../../api/api";
import { getDocuments } from "../../api/documents";

function MyInfo() {
  const [me, setMe] = useState(false);
  const [municipalities, setMunicipalities] = useState(false);
  const [documents, setDocuments] = useState(false);
  const [genders, setGenders] = useState(false);
  const [positions, setPositions] = useState(false);
  const [levels, setLevels] = useState(false);
  const [modalAccept, setModalAccept] = useState(false);
  const [stateAlert, setStateAlert] = useState(false);
  const [stateLoading, setStateLoading] = useState(false);
  const [imgAvatar, setImgAvatar] = useState(false);
  const [userData, setUserData] = useUser();

  const Me = () => {
    setStateAlert(false);
    setStateLoading(true);
    getMyInfo(userData.token, (json, error) => {
      setStateLoading(false);

      if (json) {
        if (json.code == "ok") {
          setMe(json.data);
          console.log(json.data);
        } else {
          setStateAlert({
            type: "war",
            message: json.message,
          });
        }
      } else {
        setStateAlert({
          type: "err",
          message: "Ocurrio un error!, " + error,
        });
      }
    });
  };

  const Municipalities = () => {
    setStateAlert(false);
    setStateLoading(true);
    getMunicipalities((json, error) => {
      setStateLoading(false);

      if (json) {
        if (json.code == "ok") {
          setMunicipalities(json.data);
          //console.log(json.data);
        } else {
          setStateAlert({
            type: "war",
            message: json.message,
          });
        }
      } else {
        setStateAlert({
          type: "err",
          message: "Ocurrio un error!, " + error,
        });
      }
    });
  };

  const Genders = () => {
    setStateAlert(false);
    setStateLoading(true);
    getGenders((json, error) => {
      setStateLoading(false);

      if (json) {
        if (json.code == "ok") {
          setGenders(json.data);
          //console.log(json.data);
        } else {
          setStateAlert({
            type: "war",
            message: json.message,
          });
        }
      } else {
        setStateAlert({
          type: "err",
          message: "Ocurrio un error!, " + error,
        });
      }
    });
  };

  const Documents = () => {
    setStateAlert(false);
    setStateLoading(true);
    getDocuments((json, error) => {
      setStateLoading(false);

      if (json) {
        if (json.code == "ok") {
          setDocuments(json.data);
          //console.log(json.data);
        } else {
          setStateAlert({
            type: "war",
            message: json.message,
          });
        }
      } else {
        setStateAlert({
          type: "err",
          message: "Ocurrio un error!, " + error,
        });
      }
    });
  };

  const Positions = () => {
    setStateAlert(false);
    setStateLoading(true);
    getPositions((json, error) => {
      setStateLoading(false);

      if (json) {
        if (json.code == "ok") {
          setPositions(json.data);
          //console.log(json.data);
        } else {
          setStateAlert({
            type: "war",
            message: json.message,
          });
        }
      } else {
        setStateAlert({
          type: "err",
          message: "Ocurrio un error!, " + error,
        });
      }
    });
  };

  const Levels = () => {
    setStateAlert(false);
    setStateLoading(true);
    getLevels((json, error) => {
      setStateLoading(false);

      if (json) {
        if (json.code == "ok") {
          setLevels(json.data);
          //console.log(json.data);
        } else {
          setStateAlert({
            type: "war",
            message: json.message,
          });
        }
      } else {
        setStateAlert({
          type: "err",
          message: "Ocurrio un error!, " + error,
        });
      }
    });
  };

  useEffect(() => {
    Me();
    Municipalities();
    Genders();
    Positions();
    Levels();
    Documents();
  }, []);

  const submit = (form) => {
    form.preventDefault();
    const formValues = convertToNumbers(
      Object.fromEntries(new FormData(form.target).entries())
    );
    formValues.documentId = me?.document?.id || null;
    setModalAccept({
      ok: () => {
        setModalAccept(false);
        setStateLoading(true);
        updateMyInfo(formValues, userData.token, (json, error) => {
          setStateLoading(false);
          if (json.code == "ok") {
            setStateAlert({
              type: "ok",
              message: "Perfil actualizado con exito!",
            });
            if (formValues?.image?.name == "") {
              setTimeout(() => Me(), 3000);
            } else uploadImage(me.id, formValues.image);
          } else {
            setStateAlert({
              type: "err",
              message: json.message || error,
            });
          }
        });
      },
      cancel: () => setModalAccept(false),
    });
  };

  const convertToNumbers = (formValues) => {
    const result = {};
    for (const [key, value] of Object.entries(formValues)) {
      result[key] = isNaN(value) ? value : Number(value);
    }
    return result;
  };

  const imgChange = (e) => {
    const file = e.target.files[0];
    const reader = new FileReader();

    reader.onload = () => setImgAvatar(reader.result);

    if (file) reader.readAsDataURL(file);
  };

  const uploadImage = (id, image) => {
    const formData = new FormData();
    formData.append("id", id);
    formData.append("avatar", image);
    setStateLoading(true);
    updateAvatarImage(formData, userData.token, (json, error) => {
      setStateLoading(false);
      if (json.code == "ok") {
        setStateAlert({
          type: "ok",
          message: "Foto de perfil subida con exito!",
        });
        setTimeout(() => Me(), 1000);
      } else {
        setStateAlert({
          type: "err",
          message: json.message || error,
        });
      }
    });
  };

  return (
    <>
      {stateLoading && <Loading />}
      {stateAlert && <Alert {...stateAlert} />}
      {modalAccept && <ModalAccept {...modalAccept} />}
      <NavBar />
      <div className="pl-2 pr-2 block h-max text-center bg-gray-100 pt-10 pb-20">
        <div className="flex justify-center">
          <form onSubmit={submit}>
            <div className="p-2">
              <div className="bg-gray-200 rounded-full w-40 h-40">
                <img
                  className="rounded-full w-40 h-40"
                  style={{ objectFit: "cover" }}
                  src={imgAvatar || api.resources + me?.avatar}
                  alt=" "
                />
              </div>
              <div className="-mt-10 ml-28 flex items-center justify-center relative bg-white shadow-md w-10 h-10 rounded-full">
                <label htmlFor="file">
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    viewBox="0 0 24 24"
                    fill="currentColor"
                    className="w-6 h-6 text-gray-600 cursor-pointer"
                  >
                    <path
                      fillRule="evenodd"
                      d="M11.078 2.25c-.917 0-1.699.663-1.85 1.567L9.05 4.889c-.02.12-.115.26-.297.348a7.493 7.493 0 00-.986.57c-.166.115-.334.126-.45.083L6.3 5.508a1.875 1.875 0 00-2.282.819l-.922 1.597a1.875 1.875 0 00.432 2.385l.84.692c.095.078.17.229.154.43a7.598 7.598 0 000 1.139c.015.2-.059.352-.153.43l-.841.692a1.875 1.875 0 00-.432 2.385l.922 1.597a1.875 1.875 0 002.282.818l1.019-.382c.115-.043.283-.031.45.082.312.214.641.405.985.57.182.088.277.228.297.35l.178 1.071c.151.904.933 1.567 1.85 1.567h1.844c.916 0 1.699-.663 1.85-1.567l.178-1.072c.02-.12.114-.26.297-.349.344-.165.673-.356.985-.57.167-.114.335-.125.45-.082l1.02.382a1.875 1.875 0 002.28-.819l.923-1.597a1.875 1.875 0 00-.432-2.385l-.84-.692c-.095-.078-.17-.229-.154-.43a7.614 7.614 0 000-1.139c-.016-.2.059-.352.153-.43l.84-.692c.708-.582.891-1.59.433-2.385l-.922-1.597a1.875 1.875 0 00-2.282-.818l-1.02.382c-.114.043-.282.031-.449-.083a7.49 7.49 0 00-.985-.57c-.183-.087-.277-.227-.297-.348l-.179-1.072a1.875 1.875 0 00-1.85-1.567h-1.843zM12 15.75a3.75 3.75 0 100-7.5 3.75 3.75 0 000 7.5z"
                      clipRule="evenodd"
                    />
                  </svg>
                </label>
                <input
                  id="file"
                  name="image"
                  type="file"
                  className="hidden"
                  onChange={imgChange}
                  accept="image/*"
                />
              </div>
            </div>
            <div className="pt-6 grid grid-cols-2 max-[600px]:grid-cols-1 max-[600px]:p-4 XXmd:grid-cols-3 md:max-w-xl gap-4 w-screen max-w-md">
              <div className="w-full">
                <TextFieldLabel
                  title="Nombre"
                  type="text"
                  name="name"
                  placeholder="Nombre de usuario"
                  text={me.name}
                  required
                  noVoid
                />
              </div>
              <div className="w-full">
                <TextFieldLabel
                  title="Apellidos"
                  type="text"
                  name="lastName"
                  placeholder="Apellidos del usuario"
                  text={me.last_name}
                  required
                  noVoid
                />
              </div>
              <div className="w-full">
                <TextFieldLabel
                  title="Correo electrónico"
                  type="text"
                  name="email"
                  placeholder="Correo del usuario"
                  text={me.email}
                  required
                  noVoid
                />
              </div>
              <div className="w-full">
                <TextFieldLabel
                  title="Teléfono"
                  type="phone"
                  name="phone"
                  placeholder="Teléfono del usuario"
                  text={me.phone}
                  required
                  noVoid
                />
              </div>
              <div className="w-full">
                <TextFieldLabel
                  title="Fecha de nacimiento"
                  type="date"
                  name="birthDate"
                  placeholder="Fecha de nacimiento"
                  text={me.birth_date}
                  required
                  noVoid
                />
              </div>
              <div className="w-full">
                <SelectField
                  title="Municipio"
                  name="municipalityId"
                  default="Seleccione el municipio"
                  body={
                    municipalities
                      ? municipalities.map((item) => ({
                          value: item.id,
                          label: item.name,
                          selected: item.id === me.municipality_id,
                        }))
                      : [{ value: 0, label: "Cargando..." }]
                  }
                  required
                />
              </div>
              <div className="w-full">
                <SelectField
                  title="Género"
                  name="genderId"
                  default="Seleccione el género"
                  body={
                    genders
                      ? genders.map((item) => ({
                          value: item.id,
                          label: item.name,
                          selected: item.id === me.gender_id,
                        }))
                      : [{ value: 0, label: "Cargando..." }]
                  }
                  required
                />
              </div>
              <div className="w-full">
                <SelectField
                  title="Posición pádel"
                  name="positionId"
                  default="Seleccione la posición de padel"
                  body={
                    positions
                      ? positions.map((item) => ({
                          value: item.id,
                          label: item.position,
                          selected: item.id === me.position_id,
                        }))
                      : [{ value: 0, label: "Cargando..." }]
                  }
                  required
                />
              </div>
              <div className="w-full">
                <SelectField
                  title="Nivel de padel"
                  name="levelId"
                  default="Seleccione el nivel de padel"
                  body={
                    levels
                      ? levels.map((item) => ({
                          value: item.id,
                          label: item.level,
                          selected: item.id === me.level_id,
                        }))
                      : [{ value: 0, label: "Cargando..." }]
                  }
                  required
                />
              </div>
              <div hidden={me?.document} className="w-full">
                <SelectField
                  title="Tipo de documento"
                  name="documentTypeId"
                  default="Seleccione el tipo de documento"
                  body={
                    documents
                      ? documents.map((item) => ({
                          value: item.id,
                          label: item.type,
                          selected: item.id === me?.document?.document_type_id,
                        }))
                      : [{ value: 0, label: "Cargando..." }]
                  }
                  required
                />
              </div>
              <div className="w-full">
                <TextFieldLabel
                  title="Documento de identidad"
                  type="text"
                  name="documentValue"
                  placeholder="Documento de identidad"
                  text={me?.document?.value}
                  required
                  noVoid
                />
              </div>
            </div>
            <div className="w-full mt-10">
              <PrimaryButton
                type="submit"
                className="rounded-lg"
                text="Guardar cambios"
              />
            </div>
          </form>
        </div>
      </div>
      <Footer />
    </>
  );
}

export default MyInfo;
