import { useState, useRef, useEffect } from "react";

function TextField(props) {
  const [visible, setVisible] = useState(false);
  const inputRef = useRef(null);

  useEffect(() => {
    if (props.text != undefined) inputRef.current.value = props.text;
    //console.log(props.text);
  }, [props.text]);

  const changeText = (e) => {
    if (!props.noVoid) return;
    e.target.value === "" ? setVisible(true) : setVisible(false);
    props.onChange && props.onChange(e);
  };

  return (
    <>
      <input
        ref={inputRef}
        className={
          "bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg block w-full p-2.5 XXdark:bg-gray-700 XXdark:border-gray-600 " +
          props.className
        }
        type={props.type}
        name={props.name}
        id={props.id}
        text={props.text}
        placeholder={props.placeholder}
        required={props.required}
        onChange={changeText}
      />
      <p className="mt-2 text-red-500 text-xs italic" hidden={!visible}>
        Este campo es obligatorio!
      </p>
    </>
  );
}

export default TextField;
