import React, { useEffect } from "react";
import CheckboxCard from "./CheckboxCard";
import EmptySection from "../../../components/EmptySection";

const Gift = ({ selectedGifts1, setSelectedGifts1, selectedGifts2, setSelectedGifts2, gifts }) => {

    return <div>
        <h1 className="my-7 text-2xl text-blue-500 font-bold">Regalos del jugador 1</h1>
        <div className="grid-gallery">
            {
                gifts && gifts.length ? gifts.map((el) =>
                    <CheckboxCard src={el.photo} key={el.id} id={el.id} title={el.name} subtitle={`${el.price} $`} data={selectedGifts1} setData={setSelectedGifts1} limit={1} />)
                    : <EmptySection text={'No hay sedes disponibles actualmente'} />
            }
        </div>
        <h1 className="my-7 text-2xl text-blue-500 font-bold">Regalos del jugador 2</h1>
        <div className="grid-gallery">
            {
                gifts && gifts.length ? gifts.map((el) =>
                    <CheckboxCard src={el.photo} key={el.id} id={el.id} title={el.name} subtitle={`${el.price} $`} data={selectedGifts2} setData={setSelectedGifts2} limit={1} />)
                    : <EmptySection text={'No hay sedes disponibles actualmente'} />
            }
        </div>
    </div>
}

export default Gift;