function SecundaryButton({ handleClick, id, type, text, className }) {
  return (
    <button
      type={type}
      className={"truncate bg-transparent hover:bg-blue-500 text-blue-500 font-semibold hover:text-white py-2 px-4 border border-blue-500 hover:border-transparent transition-all duration-300 ease-in-out " + className}
      style={{ border: '5' }}
      id={id}
      onClick={handleClick}
    >
      {text}
    </button>
  );
}

export default SecundaryButton;