import TitlePage from "../../components/TitlePage/TitlePage";
import TextFieldLabel from "../../components/TextField/TextFieldLabel.js";
import TextArea from "../../components/TextField/TextArea";
import PrimaryButton from "../../components/Buttons/PrimaryButton.js";
import ModalAccept from "../../components/Modal/ModalAccept";
import { useState } from "react";

function AccidentInsurancesUpdate(props) {
  const [modalAccept, setModalAccept] = useState(false);

  const submit = (form) => {
    form.preventDefault();
    const formValues = Object.fromEntries(new FormData(form.target).entries());
    for (let propiedad in formValues) {
      if (formValues[propiedad] === "") {
        delete formValues[propiedad];
      }
    }
    formValues.accidentInsuranceId = props?.data?.id;
    setModalAccept({
      ok: () => {
        setModalAccept(false);
        props.onSubmit(formValues);
      },
      cancel: () => setModalAccept(false),
    });
  };

  return (
    <>
      {modalAccept && <ModalAccept {...modalAccept} />}
      <div className="align-center absolute pl-2 pr-2 w-full h-screen text-center bg-gray-100 pb-20">
        <div className="flex justify-end items-end mt-4 mr-4">
          <svg
            xmlns="http://www.w3.org/2000/svg"
            fill="none"
            viewBox="0 0 24 24"
            strokeWidth={1.5}
            stroke="currentColor"
            className="w-6 h-6 cursor-pointer"
            onClick={props.onCancel}
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              d="M6 18L18 6M6 6l12 12"
            />
          </svg>
        </div>
        <TitlePage text={props.title} />
        <form onSubmit={submit} className="flex justify-center">
          <div>
            <div className="mt-4 p-4 grid grid-cols-2 max-[600px]:grid-cols-1 gap-6 w-screen max-w-xl">
              <div>
                <TextFieldLabel
                  title="Nombre"
                  type="text"
                  name="name"
                  placeholder="Nombre del seguro de accidente"
                  text={props.data?.name}
                  required={!props.data}
                  noVoid
                />
              </div>
              <div>
                <TextFieldLabel
                  title="Teléfono"
                  type="number"
                  name="phone"
                  placeholder="Teléfono del seguro de accidente"
                  text={props.data?.phone}
                  required={!props.data}
                  noVoid
                />
              </div>
              <div>
                <TextFieldLabel
                  title="Url del seguro"
                  type="text"
                  name="url"
                  placeholder="Url del seguro de accidente"
                  text={props.data?.url}
                  required={!props.data}
                  noVoid
                />
              </div>
              <div>
                <TextFieldLabel
                  title="Nombre de contacto"
                  type="text"
                  name="contactName"
                  placeholder="Nombre de contacto del seguro"
                  text={props.data?.contact_name}
                  required={!props.data}
                  noVoid
                />
              </div>
              <div>
                <TextFieldLabel
                  title="Cargo de contacto"
                  type="text"
                  name="contactCharge"
                  placeholder="Cargo de contacto"
                  text={props.data?.contact_charge}
                  required={!props.data}
                  noVoid
                />
              </div>
              <div>
                <TextFieldLabel
                  title="Email de contacto"
                  type="text"
                  name="contactEmail"
                  placeholder="Email de contacto"
                  text={props.data?.contact_email}
                  required={!props.data}
                  noVoid
                />
              </div>
              <div>
                <TextFieldLabel
                  title="Teléfono de contacto"
                  type="text"
                  name="contactPhone"
                  placeholder="Teléfono de contacto"
                  text={props.data?.contact_phone}
                  required={!props.data}
                  noVoid
                />
              </div>
              <div>
                <TextArea
                  name="observations"
                  rows={4}
                  placeholder="Observaciones..."
                  text={props.data?.observations}
                  required={!props.data}
                  noVoid
                />
              </div>
            </div>
            <div className="flex justify-center w-full mt-8">
              <PrimaryButton
                type="submit"
                className="rounded-lg"
                text={props.buttonText}
              />
            </div>
          </div>
        </form>
      </div>
    </>
  );
}

export default AccidentInsurancesUpdate;