import Api from "./api.js";
import { post } from "./request.js";

const callbacks = (callback) => {
    return {
      ready: (json) => {
        if (json) callback(json, null);
        else
          callback(
            null,
            " Posiblemente acurrio error del servidor!, intentelo mas tarde."
          );
      },
      error: (error) => callback(null, error),
    };
  };

export function processPayment(data, enrollmentId, token, callback) {
    post(Api.processPayment + enrollmentId, {
        headers: {
            'Content-Type': "application/json",
            Authorization: `Bearer ${token}`,
            Accept: "application/json",
        },
        body: data,
        ...callbacks(callback),
    });
}
