import Api from "./api.js";
import { get, post, put, del } from "./request.js";

//callback (json, error)

const callbacks = (callback) => {
  return {
    ready: (json) => {
      if (json) callback(json, null);
      else
        callback(
          null,
          " Posiblemente acurrio error del servidor!, intentelo mas tarde."
        );
    },
    error: (error) => callback(null, error),
  };
};

export function getLevels(callback) {
  get(Api.getLevels, {
    ...callbacks(callback),
  });
}

export function getLevelById(id, token, callback) {
  get(Api.getLevelById + id, {
    headers: {
      Authorization: `Bearer ${token}`,
      Accept: "application/json",
    },
    ...callbacks(callback),
  });
}

export function newLevel(data, token, callback) {
  post(Api.newLevel, {
    headers: {
      'Content-Type': "application/json",
      Authorization: `Bearer ${token}`,
      Accept: "application/json",
    },
    body: data,
    ...callbacks(callback),
  });
}

export function updateLevel(data, token, callback) {
  put(Api.updateLevel, {
    headers: {
      'Content-Type': "application/json",
      Authorization: `Bearer ${token}`,
      Accept: "application/json",
    },
    body: data,
    ...callbacks(callback),
  });
}

export function deleteLevel(id, token, callback) {
  del(Api.deleteLevel, {
    headers: {
      'Content-Type': "application/json",
      Authorization: `Bearer ${token}`,
      Accept: "application/json",
    },
    body: { levelId: id },
    ...callbacks(callback),
  });
}