import NavBar from "../../components/NavBar/NavBar";
import Footer from "../../components/Footer/Footer";
import TitlePage from "../../components/TitlePage/TitlePage";
import TableColumn from "../../components/Table/TableColumn.js";
import TableRow from "../../components/Table/TableRow.js";
import Table from "../../components/Table/Table.js";
import ItemView from "../../components/Table/ItemView";
import ItemEdit from "../../components/Table/ItemEdit";
import ItemCalendar from "../../components/Table/ItemCalendar";
import ItemStop from "../../components/Table/ItemStop";
import ItemDelete from "../../components/Table/ItemDelete";
import ItemClone from "../../components/Table/ItemClone";
import AddButton from "../../components/Buttons/AddButton";
import Loading from "../../components/Loading/Loading";
import Alert from "../../components/PushMessage/Alert";
import LigasUpdate from "./LigasUpdate";
import LigasFechas from "./LigasFechas";
import ModalAccept from "../../components/Modal/ModalAccept";
import { useState, useEffect } from "react";
import {
  getEditions,
  updateEdition,
  newEdition,
  updateEditionCover,
  deleteEdition,
  getEditionsMatchesDates,
  cloneEdition,
  stopEdition,
} from "../../api/editions";
import { getAccidentInsurances } from "../../api/accidentInsurances";
import useUser from "../../hooks/auth/useUser";
import api from "../../api/api";
import LigasStop from "./LigasStop";

function Ligas() {
  const [editions, setEditions] = useState(false);
  const [accidentInsurances, setAccidentInsurances] = useState(false);
  const [stateAlert, setStateAlert] = useState(false);
  const [stateLoading, setStateLoading] = useState(false);
  const [editEdition, setEditEdition] = useState(false);
  const [ligasFechas, setLigasFechas] = useState(false);
  const [ligasStop, setLigasStop] = useState(false);
  const [modalAccept, setModalAccept] = useState(false);
  const [userData, setUserData] = useUser();

  const Editions = () => {
    setStateAlert(false);
    setStateLoading(true);

    getEditions((json, error) => {
      setStateLoading(false);

      if (json) {
        if (json.code != "error") {
          setEditions(json.data);
          //console.log(json);
        } else {
          setStateAlert({
            type: "war",
            message: json.message,
          });
        }
      } else {
        //Error login
        setStateAlert({
          type: "err",
          message: "Ocurrio un error!, " + error,
        });
      }
    });
  };

  const AccidentInsurances = () => {
    setStateAlert(false);
    setStateLoading(true);

    getAccidentInsurances((json, error) => {
      setStateLoading(false);

      if (json) {
        if (json.code != "error") {
          setAccidentInsurances(json.data);
          //console.log(json.data);
        } else {
          setStateAlert({
            type: "war",
            message: json.message,
          });
        }
      } else {
        //Error login
        setStateAlert({
          type: "err",
          message: "Ocurrio un error!, " + error,
        });
      }
    });
  };

  useEffect(() => {
    Editions();
    AccidentInsurances();
  }, []);

  const addLiga = () => {
    setEditEdition({
      title: "Nueva liga",
      data: { accidents_insurance: accidentInsurances },
      buttonText: "Crear nueva liga",
      onSubmit: (data) => {
        setStateLoading(true);

        const image = data.image;
        delete data.image;

        newEdition(data, userData.token, (json, error) => {
          setStateLoading(false);
          if (json.code == "ok") {
            setStateAlert({
              type: "ok",
              message: "Liga creada con exito!",
            });
            if (image?.name == "") {
              setTimeout(() => Editions(), 1000);
              setEditEdition(false);
            } else uploadImageLiga(json.id, image);
          } else {
            setStateAlert({
              type: "err",
              message: json.message || error,
            });
          }
        });
      },
      onCancel: () => setEditEdition(false),
    });
  };

  const editLiga = (id) => {
    //console.log(id);
    let data = editions.find((e) => e.id == id);
    data.accidents_insurance = accidentInsurances;
    setEditEdition({
      data: data,
      buttonText: "Guardar cambios",
      onSubmit: (dataUpdate) => {
        setStateLoading(true);

        const image = dataUpdate.image;
        delete dataUpdate.image;

        updateEdition(data.id, dataUpdate, userData.token, (json, error) => {
          setStateLoading(false);
          if (json.code == "ok") {
            setStateAlert({
              type: "ok",
              message: "Liga actualizada con exito!",
            });
            if (image?.name == "") {
              setTimeout(() => Editions(), 1000);
              setEditEdition(false);
            } else uploadImageLiga(data.id, image);
          } else {
            setStateAlert({
              type: "err",
              message: json.message || error,
            });
          }
        });
      },
      onCancel: () => setEditEdition(false),
    });
  };

  const removeLiga = (id) => {
    setModalAccept({
      ok: () => {
        setStateLoading(true);
        setModalAccept(false);
        deleteEdition(id, userData.token, (json, error) => {
          setStateLoading(false);
          if (json.code == "ok") {
            setStateAlert({
              type: "ok",
              message: "Eliminada con exito!",
            });
            setTimeout(() => Editions(), 1000);
            setEditEdition(false);
          } else {
            setStateAlert({
              type: "err",
              message: json.message || error,
            });
          }
        });
      },
      cancel: () => setModalAccept(false),
    });
  };

  const activeLiga = (id) => {
    setStateLoading(true);
    setStateAlert(false);

    const stateFinal = !editions.find((e) => e.id == id).status;
    const editionUpdate = {
      status: stateFinal,
    };

    updateEdition(id, editionUpdate, userData.token, (json, error) => {
      setStateLoading(false);
      if (json.code == "ok") {
        setStateAlert({
          type: "ok",
          message: "Cambiado de estado con exito!",
        });
        Editions();
      } else {
        setStateAlert({
          type: "err",
          message: json.message || error,
        });
      }
    });
  };

  const stopLiga = (id) => {
    setLigasStop({
      title: editions.find((e) => e.id == id).name,
      data: { id },
      onCancel: () => setLigasStop(false),
    });
  };

  const calendarLiga = (id) => {
    setStateLoading(true);
    getEditionsMatchesDates(id, userData.token, (json, error) => {
      setStateLoading(false);
      if (json.code == "ok") {
        setLigasFechas({
          editionId: id,
          data: json.data,
          onCancel: () => setLigasFechas(false),
        });
      } else {
        setStateAlert({
          type: "err",
          message: json.message || error,
        });
      }
    });
  };

  const cloneLiga = (id) => {
    setModalAccept({
      ok: () => {
        setStateLoading(true);
        setModalAccept(false);
        cloneEdition(id, userData.token, (json, error) => {
          setStateLoading(false);
          if (json.code == "ok") {
            setStateAlert({
              type: "ok",
              message: "Clonada con exito!",
            });
            setTimeout(() => Editions(), 1000);
            setEditEdition(false);
          } else {
            setStateAlert({
              type: "err",
              message: json.message || error,
            });
          }
        });
      },
      cancel: () => setModalAccept(false),
    });
  };

  const uploadImageLiga = (id, cover) => {
    const formData = new FormData();
    formData.append("id", id);
    formData.append("cover", cover);
    //console.log(formData);
    //console.log(id, cover);
    setStateLoading(true);
    updateEditionCover(formData, userData.token, (json, error) => {
      setStateLoading(false);
      if (json.code == "ok") {
        setStateAlert({
          type: "ok",
          message: "Portada de la liga subida con exito!",
        });
        setTimeout(() => Editions(), 1000);
        setEditEdition(false);
      } else {
        setStateAlert({
          type: "err",
          message: json.message || error,
        });
      }
      //console.log(json);
    });
  };

  return (
    <>
      {stateLoading && <Loading />}
      {stateAlert && <Alert {...stateAlert} />}
      {editEdition && <LigasUpdate {...editEdition} />}
      {ligasFechas && <LigasFechas {...ligasFechas} />}
      {ligasStop && <LigasStop {...ligasStop} />}
      {modalAccept && <ModalAccept {...modalAccept} />}
      <NavBar />
      <div className="pl-2 pr-2 block h-full text-center bg-gray-100 pt-10 pb-20">
        <TitlePage text="Admin - Ligas" />
        <div className="flex justify-end items-end lg:pr-32 mb-6">
          <div className="w-max h-max" onClick={addLiga}>
            <AddButton />
          </div>
        </div>
        <Table
          title="Todas las Ligas"
          columns={
            <TableColumn
              columns={[
                "Portada",
                "Nombre",
                "Inicio",
                "Clausura",
                "Activa",
                "Acciones",
              ]}
            />
          }
          imageHeader={
            <svg
              xmlns="http://www.w3.org/2000/svg"
              fill="none"
              viewBox="0 0 24 24"
              strokeWidth={1.5}
              stroke="currentColor"
              className="w-6 h-6"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                d="M6 6.878V6a2.25 2.25 0 012.25-2.25h7.5A2.25 2.25 0 0118 6v.878m-12 0c.235-.083.487-.128.75-.128h10.5c.263 0 .515.045.75.128m-12 0A2.25 2.25 0 004.5 9v.878m13.5-3A2.25 2.25 0 0119.5 9v.878m0 0a2.246 2.246 0 00-.75-.128H5.25c-.263 0-.515.045-.75.128m15 0A2.25 2.25 0 0121 12v6a2.25 2.25 0 01-2.25 2.25H5.25A2.25 2.25 0 013 18v-6c0-.98.626-1.813 1.5-2.122"
              />
            </svg>
          }
        >
          {editions &&
            editions.map((item, index) => (
              <tr key={index}>
                <TableRow>
                  <div className="rounded-lg bg-gray-200 w-16 h-16">
                    <img
                      className="w-16 h-16 rounded-lg"
                      src={api.resources + item.cover}
                      alt=" "
                      style={{ objectFit: "cover" }}
                    />
                  </div>
                </TableRow>
                <TableRow>
                  <div className="font-semibold text-gray-800 text-sm">
                    {item.name}
                  </div>
                </TableRow>
                <TableRow>
                  <div className="font-semibold text-blue-500 text-sm">
                    {item.leagues_start_date}
                  </div>
                </TableRow>
                <TableRow>
                  <div className="font-semibold text-red-500 text-sm">
                    {item.end_date}
                  </div>
                </TableRow>
                <ItemView
                  id={item.id}
                  onClick={activeLiga}
                  state={item.status}
                />
                <TableRow>
                  <div className="flex">
                    <ItemEdit id={item.id} onClick={editLiga} />
                    <ItemClone id={item.id} onClick={cloneLiga} />
                    <ItemDelete id={item.id} onClick={removeLiga} />
                    <ItemCalendar id={item.id} onClick={calendarLiga} />
                    <ItemStop id={item.id} onClick={stopLiga} />
                  </div>
                </TableRow>
              </tr>
            ))}
        </Table>
      </div>
      <Footer />
    </>
  );
}

export default Ligas;
