import Api from "./api.js";
import { get, post, put, del } from "./request.js";

//callback (json, error)

const callbacks = (callback) => {
  return {
    ready: (json) => {
      if (json) callback(json, null);
      else
        callback(
          null,
          " Posiblemente acurrio error del servidor!, intentelo mas tarde."
        );
    },
    error: (error) => callback(null, error),
  };
};

export function getCouples(callback) {
  get(Api.getCouples, {
    ...callbacks(callback),
  });
}

export function getCouplesByCategory(id, callback) {
  get(Api.getCouplesByCategory + id, {
    ...callbacks(callback),
  });
}

export function getCouplesByEditionId(editionId, token, callback) {
  get(`${Api.getCouplesByEditionId}${editionId}`, {
    headers: {
      Authorization: `Bearer ${token}`,
      Accept: "application/json",
    },
    ...callbacks(callback),
  });
}

export function searchCouple(data, token, callback) {
  post(Api.searchCouple, {
    headers: {
      "Content-Type": "application/json",
      Authorization: `Bearer ${token}`,
      Accept: "application/json",
    },
    body: data,
    ...callbacks(callback),
  });
}

export function getDetails(editionId, coupleId, token, callback) {
  get(`${Api.getDetails}${editionId}/${coupleId}`, {
    headers: {
      Authorization: `Bearer ${token}`,
      Accept: "application/json",
    },
    ...callbacks(callback),
  });
}

export function updateCouple(data, token, callback) {
  post(Api.updateCouple, {
    headers: {
      "Content-Type": "application/json",
      Authorization: `Bearer ${token}`,
      Accept: "application/json",
    },
    body: data,
    ...callbacks(callback),
  });
}

export function updateCoupleImage(data, token, callback) {
  post(Api.updateCoupleImage, {
    format: "form-data",
    headers: {
      Authorization: `Bearer ${token}`,
      Accept: "application/json",
    },
    body: data,
   ...callbacks(callback),
  });
}

export function cloneCouple(data, token, callback) {
  post(Api.cloneCouple, {
    headers: {
      "Content-Type": "application/json",
      Authorization: `Bearer ${token}`,
      Accept: "application/json",
    },
    body: data,
    ...callbacks(callback),
  });
}