import React, { useRef } from "react"
import "../../css/index.css"
import defaultImage from "../../../../assets/placeholder.png";
import api from "../../../../api/api";
import Image from "../../../../components/Image";
import { toast } from "react-toastify";


export default ({ src, id, title, data, setData, limit, subtitle }) => {

    const handleClick = (e) => {
        e.stopPropagation();
        const target = e.currentTarget;
        const classList = ["border-solid", "border-blue-500", "border-2"];
        if (limit && !e.currentTarget.classList.contains('border-solid') && data.length >= limit) {
            toast.error(`Solo se permite seleccionar ${limit} opci${limit == 1 ? 'ón' : 'ones'}`)
            return
        };

        if (target.classList.contains('border-solid')) {
            setData(data.filter(el => el != id))
            target.classList.remove(...classList)
        } else {
            setData([id, ...data])
            target.classList.add(...classList)
        }

    }

    return (<figure className="grid-gallery__item relative  p-0 overflow-hidden rounded-md cursor-pointer shadow-md" onClick={handleClick}
    >
        <Image src={src ? api.resources + src : defaultImage} alt="" className="h-full w-full  transition-all duration-300 ease-in-out object-cover object-top" />
        <div className="layer" >
            <h3>
                {title}
            </h3>

            <p>
                {
                    subtitle
                }
            </p>
        </div>

    </figure>)
}
