import { Link, useNavigate } from "react-router-dom";
import { useState, useEffect } from "react";
import PrimaryButton from "../../components/Buttons/PrimaryButton.js";
import Loading from "../../components/Loading/Loading.js";
import Alert from "../../components/PushMessage/Alert.js";
import FormAccount from "../../components/Form/FormAccount.js";
import TextFieldLabel from "../../components/TextField/TextFieldLabel.js";
import SelectField from "../../components/SelectField/SelectField.js";
import WisardMenu from "../../components/WisardMenu/WisardMenu.js";
import CheckField from "../../components/CheckField/CheckField.js";
import { register } from "../../api/auth.js";
import { getGenders } from "../../api/genders.js";
import { getDocuments } from "../../api/documents.js";
import { getMunicipalities } from "../../api/municipalities.js";
import { getLevels } from "../../api/levels.js";
import { getPositions } from "../../api/positions.js";
import useUser from "../../hooks/auth/useUser.js";

function Registro() {
  const [stateAlert, setStateAlert] = useState(false);
  const [stateLoading, setStateLoading] = useState(false);
  const [userData, setUserData] = useUser();
  const navigate = useNavigate();
  //Get api data
  const [genders, setGenders] = useState(false);
  const [documents, setDocuments] = useState(false);
  const [municipalities, setMunicipalities] = useState(false);
  const [levels, setLevels] = useState(false);
  const [positions, setPositions] = useState(false);

  useEffect(() => {
    setStateAlert(false);
    getGenders(fetchCallback(setGenders));
    getDocuments(fetchCallback(setDocuments));
    getMunicipalities(fetchCallback(setMunicipalities));
    getLevels(fetchCallback(setLevels));
    getPositions(fetchCallback(setPositions));
  }, []);

  const fetchCallback = (action) => {
    return (json, error) => {
      if (json) {
        if (json.code != "error") {
          action(json.data);
          //console.log(json.data);
        } else {
          setStateAlert({
            type: "war",
            message: json.message,
          });
        }
      } else {
        setStateAlert({
          type: "err",
          message: "Ocurrio un error!, " + error,
        });
      }
    };
  };

  const submit = (form) => {
    setStateAlert(false);
    form.preventDefault();
    //Recuperar data de el form
    const formValues = Object.fromEntries(new FormData(form.target).entries());
    const data = {
      name: formValues.name,
      lastName: formValues.lastName,
      password: formValues.password,
      genderId: formValues.genderId,
      birthDate: formValues.birthDate,
      phone: formValues.phone,
      email: formValues.email, 
      documentId: formValues.documentId,
      documentValue: formValues.documentValue,
      municipalityId: formValues.municipalityId,
      levelId: formValues.levelId,
      positionId: formValues.positionId,
      referenceCode: formValues.referenceCode,
      acceptPP: formValues.acceptPP ? 1 : 0,
    };

    //console.log(data);

    //Loading start
    setStateLoading(true);

    register(data, (json, error) => {
      //Loading stop
      setStateLoading(false);

      if (json) {
        if (json.code != "error") {
          //Register successfull
          setStateAlert({
            type: "ok",
            message: "Usuario registrado correctamente!",
          });
          //Guardando token
          setUserData({
            token: json.data.access_token,
            permissions: json.data.user.roles.includes("Admin")
              ? "admin"
              : "user",
          });
          //Ir al home
          setTimeout(function () {
            //navigate("/");
            window.location.href = "/";
          }, 2000);
        } else {
          setStateAlert({
            type: "war",
            message: json.message,
          });
        }
      } else {
        //Error register
        setStateAlert({
          type: "err",
          message: "Ocurrio un error!, " + error,
        });
      }
    });
  };

  return (
    <div className="bg-gray-50">
      {stateAlert && <Alert {...stateAlert} />}
      {stateLoading && <Loading />}
      <FormAccount id="registro" title="Crear cuenta" submit={submit}>
        <WisardMenu
          sections={[
            <div className="grid gap-2">
              <TextFieldLabel
                title="Nombre"
                type="text"
                name="name"
                id="name"
                placeholder="Tu nombre"
                noVoid
                required
              />
              <TextFieldLabel
                title="Apellidos"
                type="text"
                name="lastName"
                id="lastName"
                placeholder="Tus apellidos"
                noVoid
                required
              />
              <TextFieldLabel
                title="Contraseña para la cuenta"
                type="password"
                name="password"
                id="password"
                placeholder="No se permiten @!*.%"
                noVoid
                required
              />
            </div>,
            <div className="grid gap-2">
              <SelectField
                title="Género"
                name="genderId"
                id="genderId"
                default="Seleccione el género"
                body={
                  genders
                    ? genders.map((item) => ({
                        value: item.id,
                        label: item.name,
                      }))
                    : [{ value: 0, label: "Cargando..." }]
                }
                required
              />
              <TextFieldLabel
                title="Fecha de nacimiento"
                type="date"
                name="birthDate"
                id="birthDate"
                noVoid
                required
              />
              <TextFieldLabel
                title="Número de teléfono"
                type="tel"
                name="phone"
                id="phone"
                placeholder="Ejemplo 832 234 2424"
                noVoid
                required
              />
              <TextFieldLabel
                title="Correo electrónico"
                type="email"
                name="email"
                id="email"
                placeholder="ejemplo@correo.com"
                noVoid
                required
              />
            </div>,
            <div className="grid gap-2">
              <SelectField
                title="Documento de identificación"
                name="documentId"
                id="documentId"
                default="Seleccione el tipo de documento"
                body={
                  documents
                    ? documents.map((item) => ({
                        value: item.id,
                        label: item.type,
                      }))
                    : [{ value: 0, label: "Cargando..." }]
                }
                required
              />
              <TextFieldLabel
                title="Numero de documento"
                type="documentValue"
                name="documentValue"
                id="documentValue"
                placeholder="Ejemplo 2572-3624-923-1"
                noVoid
                required
              />
              <SelectField
                title="Municipio"
                name="municipalityId"
                id="municipalityId"
                default="Seleccione el municipio"
                body={
                  municipalities
                    ? municipalities.map((item) => ({
                        value: item.id,
                        label: item.name,
                      }))
                    : [{ value: 0, label: "Cargando..." }]
                }
                required
              />
            </div>,
            <div className="grid gap-2">
              <SelectField
                title="Nivel de padel"
                name="levelId"
                id="levelId"
                default="Seleccione el nivel de padel"
                body={
                  levels
                    ? levels.map((item) => ({
                        value: item.id,
                        label: item.level,
                      }))
                    : [{ value: 0, label: "Cargando..." }]
                }
                required
              />
              <SelectField
                title="Posición pádel"
                name="positionId"
                id="positionId"
                default="Seleccione la posición de padel"
                body={
                  positions
                    ? positions.map((item) => ({
                        value: item.id,
                        label: item.position,
                      }))
                    : [{ value: 0, label: "Cargando..." }]
                }
                required
              />
            </div>,
            <div className="grid gap-2">
              <TextFieldLabel
                title="Tu codigo de referencia"
                type="text"
                name="referenceCode"
                id="referenceCode"
                placeholder="Codigo de referencia"
              />
            </div>,
          ]}
        />
        <CheckField
          id="acceptPP"
          name="acceptPP"
          text={
            <>
              Acepto las politicas!,{" "}
              <Link
                to={"/policy"}
                className="font-medium text-primary-600 hover:underline dark:text-primary-500"
              >
                ver
              </Link>
            </>
          }
          required
        />
        <PrimaryButton
          type="submit"
          className="w-full focus:ring-4 focus:outline-none focus:ring-primary-300 font-medium rounded-lg text-sm px-5 py-2.5"
          text="Registrame"
        />
        <p className="text-sm font-light text-gray-500 dark:text-gray-400">
          Quiero iniciar a mi cuenta!{" "}
          <Link
            to={"/login"}
            className="font-medium text-primary-600 hover:underline dark:text-primary-500"
          >
            Iniciar
          </Link>
        </p>
      </FormAccount>
    </div>
  );
}

export default Registro;
