function SelectField(props) {
  return (
    <div>
      <div hidden={props.disabledTitle}>
        <label
          htmlFor={props.name}
          className="block text-start mb-2 text-sm font-medium text-gray-900 dark:text-gray-900"
        >
          {props.required || props.noVoid ? (
            <span className="text-red-600">* </span>
          ) : (
            ""
          )}
          {props.title || "Seleccione una opción"}
        </label>
      </div>
      <select
        id={props.id}
        name={props.name}
        className="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5"
        required={props.required}
        onChange={props.onChange}
      >
        {props.default && <option value="">{props.default}</option>}
        {props.body &&
          props.body.map((item, index) => (
            <option key={index} selected={item.selected} value={item.value}>
              {item.label}
            </option>
          ))}
      </select>
    </div>
  );
}

export default SelectField;
