function NewsCard(props) {
  return (
    <div className="cursor-pointer max-w-[340px] w-full h-full hover:shadow-xl hover:transition duration-75 rounded-md overflow-hidden border shadow-md m-3 pb-2">
      <div className="w-full max-h-[240px] h-screen bg-gray-200">
        <img
          className="w-full Xmax-h-[240px] h-full"
          style={{ objectFit: "cover" }}
          src={props?.image}
          alt=" "
        />
      </div>
      <div className="px-6 py-4 h-auto">
        <div className="font-bold text-lg mb-2 text-gray-600">
          {props.title}
        </div>
        <div className="mb-2 text-gray-400">{props.time}</div>
        <p className="text-gray-700 text-base">{props.des}</p>
      </div>
    </div>
  );
}

export default NewsCard;
