import { useEffect } from "react";
import Registro from "./Registro";

function RegistroReferencia() {
  const params = new URLSearchParams(window.location.search);
  const referenceCode = params.get("code");
  const name = params.get("name");

  useEffect(() => {
    if (!referenceCode || !name) alert("Enlace no es valido!");
  }, []);

  return (
    <>
      <div className="w-full flex justify-center pt-10">
        Enlace de referencia de<strong>&nbsp;{name || "No es valido!"}</strong>.
      </div>
      <Registro code={referenceCode} />
    </>
  );
}

export default RegistroReferencia;
