import TitlePage from "../../components/TitlePage/TitlePage";
import TextFieldLabel from "../../components/TextField/TextFieldLabel.js";
import SelectField from "../../components/SelectField/SelectField.js";
import TextArea from "../../components/TextField/TextArea";
import PrimaryButton from "../../components/Buttons/PrimaryButton.js";
import ModalAccept from "../../components/Modal/ModalAccept";
import Alert from "../../components/PushMessage/Alert";
import { useEffect, useState } from "react";
import { getCategories } from "../../api/categories";
import {
  getEditionById,
  updateCategoriesByEditionId,
  updateEditionGiftsById,
  updateMatchesDates,
} from "../../api/editions";
import Loading from "../../components/Loading/Loading";
import useUser from "../../hooks/auth/useUser";
import CheckField from "../../components/CheckField/CheckField";
import Table from "../../components/Table/Table";
import TableColumn from "../../components/Table/TableColumn";
import TableRow from "../../components/Table/TableRow";
import { getGifts } from "../../api/gifts";
import api from "../../api/api";

function LigasRegalosUpdate(props) {
  const [gifts, setGifts] = useState(false);
  const [giftsSelected, setGiftsSelected] = useState(false);
  const [modalAccept, setModalAccept] = useState(false);
  const [stateAlert, setStateAlert] = useState(false);
  const [stateLoading, setStateLoading] = useState(false);
  const [userData, setUserData] = useUser();

  const Gifts = () => {
    setStateLoading(true);
    getGifts((json, error) => {
      setStateLoading(false);
      if (json.code == "ok") {
        setGifts(json.data);
        const data = json.data.map((item) => ({
          id: item.id,
          state: props.data.gifts.find((e) => e.gift.id == item.id)
            ? true
            : false,
        }));
        setGiftsSelected(data);
        //console.log(data)
      } else {
        setStateAlert({
          type: "err",
          message: json.message || error,
        });
      }
    });
  };

  useEffect(() => {
    Gifts();
  }, []);

  const submit = () => {
    const data = giftsSelected
      .filter((obj) => obj.state === true)
      .map((obj) => obj.id);
    const requestData = { ids: data, edition_id: props.data.edition_id };
    //console.log(requestData);
    setModalAccept({
      ok: () => {
        setModalAccept(false);
        setStateLoading(true);
        updateEditionGiftsById(
          requestData,
          userData.token,
          (json, error) => {
            setStateLoading(false);
            //console.log(json);
            if (json.code == "ok") {
              setStateAlert({
                type: "ok",
                message: "Regalos de la liga actualizadas con exito!",
              });
              setTimeout(() => setStateAlert(false), 1000);
              props.onUpdate();
              //setTimeout(() => props.onCancel(), 1000);
            } else {
              setStateAlert({
                type: "err",
                message: json.message || error,
              });
            }
          }
        );
      },
      cancel: () => setModalAccept(false),
    });
  };

  const onCheckBoxChange = (id) => {
    if (giftsSelected) {
      const data = giftsSelected;
      data.map((item) => {
        if (item.id == id) item.state = !item.state;
      });
      setGiftsSelected(data);
      //console.log(data);
    }
  };

  return (
    <>
      {modalAccept && <ModalAccept {...modalAccept} />}
      {stateAlert && <Alert {...stateAlert} />}
      {stateLoading && <Loading {...stateLoading} />}
      <div className="align-center absolute pl-2 pr-2 w-full h-full text-center bg-gray-100 pb-20">
        <div className="flex justify-end items-end mt-4 mr-4">
          <svg
            xmlns="http://www.w3.org/2000/svg"
            fill="none"
            viewBox="0 0 24 24"
            strokeWidth={1.5}
            stroke="currentColor"
            className="w-6 h-6 cursor-pointer"
            onClick={props.onCancel}
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              d="M6 18L18 6M6 6l12 12"
            />
          </svg>
        </div>
        <TitlePage text={"Regalos de la liga: " + props.title} />
        <div className="w-screen mt-8 Xflex flex-wrap justify-center Xgap-4">
          <div>
            <Table
              title="Todas los regalos"
              columns={
                <TableColumn columns={["Foto", "Nombre", "Precio", "Activa"]} />
              }
              imageHeader={
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  fill="none"
                  viewBox="0 0 24 24"
                  strokeWidth={1.5}
                  stroke="currentColor"
                  className="w-6 h-6"
                >
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    d="M21 11.25v8.25a1.5 1.5 0 01-1.5 1.5H5.25a1.5 1.5 0 01-1.5-1.5v-8.25M12 4.875A2.625 2.625 0 109.375 7.5H12m0-2.625V7.5m0-2.625A2.625 2.625 0 1114.625 7.5H12m0 0V21m-8.625-9.75h18c.621 0 1.125-.504 1.125-1.125v-1.5c0-.621-.504-1.125-1.125-1.125h-18c-.621 0-1.125.504-1.125 1.125v1.5c0 .621.504 1.125 1.125 1.125z"
                  />
                </svg>
              }
            >
              {gifts &&
                gifts.map((item, index) => (
                  <tr key={index}>
                    <TableRow>
                      <div className="rounded-lg bg-gray-200 w-16 h-16 mt-1 mb-1">
                        <img
                          className="w-16 h-16 rounded-lg"
                          src={item.photo ? api.resources + item.photo : null}
                          alt=" "
                          style={{ objectFit: "cover" }}
                        />
                      </div>
                    </TableRow>
                    <TableRow>
                      <div className="p-3 font-semibold text-gray-800 text-sm">
                        {item.name}
                      </div>
                    </TableRow>
                    <TableRow>
                      <div className="p-3 font-semibold text-green-600 text-sm">
                        {item.price}
                      </div>
                    </TableRow>
                    <TableRow>
                      <div className="flex">
                        <CheckField
                          selected={props.data.gifts.find(
                            (e) => e.gift.id == item.id
                          )}
                          onChange={() => onCheckBoxChange(item.id)}
                        />
                      </div>
                    </TableRow>
                  </tr>
                ))}
            </Table>
          </div>
          <div className="w-full mt-6">
            <div onClick={submit}>
              <PrimaryButton className="rounded-lg" text={props.buttonText} />
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default LigasRegalosUpdate;
