import Api from "./api.js";
import { get, post, put, del } from "./request.js";

//callback (json, error)

const callbacks = (callback) => {
  return {
    ready: (json) => {
      if (json) callback(json, null);
      else
        callback(
          null,
          " Posiblemente acurrio error del servidor!, intentelo mas tarde."
        );
    },
    error: (error) => callback(null, error),
  };
};

export function getGifts(callback) {
  get(Api.getGifts, {
    ...callbacks(callback),
  });
}

export function findGiftsByUsers(data, callback) {
  post(Api.findGiftsByUsers, {
    headers: {
      'Content-Type': "application/json",
      Accept: "application/json",
    },
    body: data,
   ...callbacks(callback),
  });
}

export function getGiftById(id, token, callback) {
  get(Api.getGiftById + id, {
    headers: {
      Authorization: `Bearer ${token}`,
      Accept: "application/json",
    },
    ...callbacks(callback),
  });
}

export function newGift(data, token, callback) {
  post(Api.newGift, {
    headers: {
      'Content-Type': "application/json",
      Authorization: `Bearer ${token}`,
      Accept: "application/json",
    },
    body: data,
    ...callbacks(callback),
  });
}

export function updateGift(data, token, callback) {
  put(Api.updateGift, {
    headers: {
      'Content-Type': "application/json",
      Authorization: `Bearer ${token}`,
      Accept: "application/json",
    },
    body: data,
    ...callbacks(callback),
  });
}

export function deleteGift(id, token, callback) {
  del(Api.deleteGift, {
    headers: {
      'Content-Type': "application/json",
      Authorization: `Bearer ${token}`,
      Accept: "application/json",
    },
    body: { giftId: id },
    ...callbacks(callback),
  });
}

export function uploadGiftImage(data, token, callback) {
  post(Api.uploadGiftImage, {
    format: "form-data",
    headers: {
      Authorization: `Bearer ${token}`,
      Accept: "application/json",
    },
    body: data,
   ...callbacks(callback),
  });
}