import TitlePage from "../../components/TitlePage/TitlePage";
import TextFieldLabel from "../../components/TextField/TextFieldLabel.js";
import SelectField from "../../components/SelectField/SelectField";
import TextArea from "../../components/TextField/TextArea";
import PrimaryButton from "../../components/Buttons/PrimaryButton.js";
import ModalAccept from "../../components/Modal/ModalAccept";
import { useState } from "react";

function AwardsUpdate(props) {
  const [modalAccept, setModalAccept] = useState(false);

  const submit = (form) => {
    form.preventDefault();
    const formValues = convertToNumbers(
      Object.fromEntries(new FormData(form.target).entries())
    );
    formValues.awardId = props?.data?.id;
    setModalAccept({
      ok: () => {
        setModalAccept(false);
        props.onSubmit(formValues);
      },
      cancel: () => setModalAccept(false),
    });
  };

  const convertToNumbers = (formValues) => {
    const result = {};
    for (const [key, value] of Object.entries(formValues)) {
      result[key] = isNaN(value) ? value : Number(value);
    }
    return result;
  };

  return (
    <>
      {modalAccept && <ModalAccept {...modalAccept} />}
      <div className="mt-20 fixed inset-0 flex items-center justify-center bg-black bg-opacity-50">
        <div className="rounded-md p-2 m-2 bg-white w-full h-max max-w-md">
          <div className="block">
            <div className="flex justify-end items-end mt-3 mr-3">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                fill="none"
                viewBox="0 0 24 24"
                strokeWidth={1.5}
                stroke="currentColor"
                className="w-6 h-6 cursor-pointer"
                onClick={props.onCancel}
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  d="M6 18L18 6M6 6l12 12"
                />
              </svg>
            </div>
            <TitlePage text={props?.title} />
            <form onSubmit={submit}>
              <div className="p-4">
                <div className="mt-3">
                  <SelectField
                    title="Tipo de premio"
                    name="awardTypeId"
                    default="Seleccione el tipo de premio"
                    body={
                      props.data.awardsTypes &&
                        props.data.awardsTypes.map((item) => ({
                          value: item.id,
                          label: item.type,
                          selected: item.id == props?.data?.award_type_id,
                        }))
                    }
                    required
                    noVoid
                  />
                </div>
                <div className="mt-3">
                  <SelectField
                    title="Categoria"
                    name="categoryId"
                    default="Seleccione una categoria"
                    body={
                      props.data.categories &&
                        props.data.categories.map((item) => ({
                          value: item.id,
                          label: item.name,
                          selected: item.id == props?.data?.category_id,
                        }))
                    }
                    required
                    noVoid
                  />
                </div>
                <div className="mt-3">
                  <SelectField
                    title="Edicion"
                    name="edition_id"
                    default="Seleccione una edicion"
                    body={
                      props.data.editions &&
                        props.data.editions.map((item) => ({
                          value: item.id,
                          label: item.name,
                          selected: item.id == props?.data?.edition_id,
                        }))
                    }
                    required
                    noVoid
                  />
                </div>
                <div className="mt-6">
                  <TextArea
                    name="details"
                    rows={4}
                    placeholder="Detalles..."
                    text={props.data?.details}
                    required
                    noVoid
                  />
                </div>
                <div className="flex justify-center w-full mt-8">
                  <PrimaryButton
                    type="submit"
                    className="rounded-lg"
                    text={props.buttonText}
                  />
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>
    </>
  );
}

export default AwardsUpdate;
