import TitlePage from "../../components/TitlePage/TitlePage";
import TextFieldLabel from "../../components/TextField/TextFieldLabel.js";
import SelectField from "../../components/SelectField/SelectField.js";
import TextArea from "../../components/TextField/TextArea";
import PrimaryButton from "../../components/Buttons/PrimaryButton.js";
import ModalAccept from "../../components/Modal/ModalAccept";
import { useState } from "react";
import api from "../../api/api";

function LigasUpdate(props) {
  const [imgCover, setImgCover] = useState(false);
  const [modalAccept, setModalAccept] = useState(false);

  const imgChange = (e) => {
    const file = e.target.files[0];
    const reader = new FileReader();

    reader.onload = () => setImgCover(reader.result);

    if (file) reader.readAsDataURL(file);
  };

  const submit = (form) => {
    form.preventDefault();
    const formValues = convertToNumbers(
      Object.fromEntries(new FormData(form.target).entries())
    );
    setModalAccept({
      ok: () => {
        setModalAccept(false);
        props.onSubmit(formValues);
      },
      cancel: () => setModalAccept(false),
    });
  };

  const convertToNumbers = (formValues) => {
    const result = {};
    for (const [key, value] of Object.entries(formValues)) {
      result[key] = isNaN(value) ? value : Number(value);
    }
    return result;
  };

  const convertToBase64 = (image, callback) => {
    const reader = new FileReader();
    reader.readAsDataURL(image);
    reader.onload = () => {
      const base64Image = reader.result;
      callback(base64Image);
    };
    reader.onerror = () => callback();
  };

  return (
    <>
      {modalAccept && <ModalAccept {...modalAccept} />}
      <div className="align-center absolute pl-2 pr-2 w-full h-max text-center bg-gray-100 pb-20">
        <div className="flex justify-end items-end mt-4 mr-4">
          <svg
            xmlns="http://www.w3.org/2000/svg"
            fill="none"
            viewBox="0 0 24 24"
            strokeWidth={1.5}
            stroke="currentColor"
            className="w-6 h-6 cursor-pointer"
            onClick={props.onCancel}
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              d="M6 18L18 6M6 6l12 12"
            />
          </svg>
        </div>
        <TitlePage
          text={props.data ? "Liga: " + props.data?.name : props.title}
        />
        <form onSubmit={submit}>
          <div className="flex justify-center w-full p-2 pt-6">
            <div className="grid grid-cols-2 max-[600px]:grid-cols-1 gap-6 XXw-full max-w-xl">
              <div className="h-max">
                <div className="flex bg-gray-500 p-2 pl-3 rounded-md text-white text-start">
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    fill="none"
                    viewBox="0 0 24 24"
                    strokeWidth={1.5}
                    stroke="currentColor"
                    className="w-6 h-6 mr-2 mb-0.5"
                  >
                    <path
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      d="M6 6.878V6a2.25 2.25 0 012.25-2.25h7.5A2.25 2.25 0 0118 6v.878m-12 0c.235-.083.487-.128.75-.128h10.5c.263 0 .515.045.75.128m-12 0A2.25 2.25 0 004.5 9v.878m13.5-3A2.25 2.25 0 0119.5 9v.878m0 0a2.246 2.246 0 00-.75-.128H5.25c-.263 0-.515.045-.75.128m15 0A2.25 2.25 0 0121 12v6a2.25 2.25 0 01-2.25 2.25H5.25A2.25 2.25 0 013 18v-6c0-.98.626-1.813 1.5-2.122"
                    />
                  </svg>
                  Datos de la liga
                </div>
                <div className="pt-3">
                  <TextFieldLabel
                    title="Nombre"
                    type="text"
                    name="name"
                    id="name"
                    placeholder="Nombre de la liga"
                    text={props.data?.name}
                    required
                    noVoid
                  />
                </div>
                <div className="pt-3">
                  <SelectField
                    title="Modo"
                    name="mode"
                    id="mode"
                    default="Seleccione el modo"
                    body={[
                      {
                        value: "Parejas",
                        label: "Parejas",
                        selected: props.data?.mode === "Parejas",
                      },
                      {
                        value: "Empresas",
                        label: "Empresas",
                        selected: props.data?.mode === "Empresas",
                      },
                    ]}
                    required
                    noVoid
                  />
                </div>
                <div className="pt-3">
                  <SelectField
                    title="Estado"
                    name="status"
                    id="status"
                    default="Seleccione el estado"
                    body={[
                      {
                        value: 0,
                        label: "Desactivada",
                        selected: !props.data?.status,
                      },
                      {
                        value: 1,
                        label: "Activada",
                        selected: props.data?.status,
                      },
                    ]}
                    required
                    noVoid
                  />
                </div>
                <div className="pt-3">
                  <SelectField
                    title="Fases"
                    name="phase"
                    id="phase"
                    default="Seleccione estado de fase"
                    body={[
                      {
                        value: 0,
                        label: "No",
                        selected: !props.data?.phase,
                      },
                      {
                        value: 1,
                        label: "Si",
                        selected: props.data?.phase,
                      },
                    ]}
                    required
                    noVoid
                  />
                </div>
              </div>
              <div className="h-max">
                <div className="flex bg-cyan-600 p-2 pl-3 rounded-md text-white text-start">
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    fill="none"
                    viewBox="0 0 24 24"
                    strokeWidth={1.5}
                    stroke="currentColor"
                    className="w-6 h-6 mr-2 mb-0.5"
                  >
                    <path
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      d="M18 18.72a9.094 9.094 0 003.741-.479 3 3 0 00-4.682-2.72m.94 3.198l.001.031c0 .225-.012.447-.037.666A11.944 11.944 0 0112 21c-2.17 0-4.207-.576-5.963-1.584A6.062 6.062 0 016 18.719m12 0a5.971 5.971 0 00-.941-3.197m0 0A5.995 5.995 0 0012 12.75a5.995 5.995 0 00-5.058 2.772m0 0a3 3 0 00-4.681 2.72 8.986 8.986 0 003.74.477m.94-3.197a5.971 5.971 0 00-.94 3.197M15 6.75a3 3 0 11-6 0 3 3 0 016 0zm6 3a2.25 2.25 0 11-4.5 0 2.25 2.25 0 014.5 0zm-13.5 0a2.25 2.25 0 11-4.5 0 2.25 2.25 0 014.5 0z"
                    />
                  </svg>
                  Jugadores por equipo
                </div>
                <CardPuntos
                  inputs={[
                    {
                      title: "Mínimo",
                      name: "minPlayers",
                      id: "minPlayers",
                      placeholder: "Mínimo de jugadores",
                      text: props.data?.min_players,
                      required: true,
                      noVoid: false,
                    },
                    {
                      title: "Máximo",
                      name: "maxPlayers",
                      id: "maxPlayers",
                      placeholder: "Máximo de jugadores",
                      text: props.data?.max_players,
                      required: true,
                      noVoid: false,
                    },
                  ]}
                />
                <div className="h-max mt-6">
                  <div className="flex bg-gray-600 p-2 pl-3 rounded-md text-white text-start">
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      fill="none"
                      viewBox="0 0 24 24"
                      strokeWidth={1.5}
                      stroke="currentColor"
                      className="w-6 h-6 mr-2 mb-0.5"
                    >
                      <path
                        strokeLinecap="round"
                        strokeLinejoin="round"
                        d="M8.625 12a.375.375 0 11-.75 0 .375.375 0 01.75 0zm0 0H8.25m4.125 0a.375.375 0 11-.75 0 .375.375 0 01.75 0zm0 0H12m4.125 0a.375.375 0 11-.75 0 .375.375 0 01.75 0zm0 0h-.375M21 12c0 4.556-4.03 8.25-9 8.25a9.764 9.764 0 01-2.555-.337A5.972 5.972 0 015.41 20.97a5.969 5.969 0 01-.474-.065 4.48 4.48 0 00.978-2.025c.09-.457-.133-.901-.467-1.226C3.93 16.178 3 14.189 3 12c0-4.556 4.03-8.25 9-8.25s9 3.694 9 8.25z"
                      />
                    </svg>
                    Observaciones
                  </div>
                  <div className="mt-4">
                    <TextArea
                      id="observations"
                      name="observations"
                      rows={4}
                      placeholder="Observaciones..."
                      text={props.data?.observations}
                      required
                      noVoid
                    />
                  </div>
                </div>
              </div>
              <div className="h-max">
                <div className="flex bg-gray-600 p-2 pl-3 rounded-md text-white text-start">
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    fill="none"
                    viewBox="0 0 24 24"
                    strokeWidth={1.5}
                    stroke="currentColor"
                    className="w-6 h-6 mr-2 mb-0.5"
                  >
                    <path
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      d="M6.75 3v2.25M17.25 3v2.25M3 18.75V7.5a2.25 2.25 0 012.25-2.25h13.5A2.25 2.25 0 0121 7.5v11.25m-18 0A2.25 2.25 0 005.25 21h13.5A2.25 2.25 0 0021 18.75m-18 0v-7.5A2.25 2.25 0 015.25 9h13.5A2.25 2.25 0 0121 11.25v7.5m-9-6h.008v.008H12v-.008zM12 15h.008v.008H12V15zm0 2.25h.008v.008H12v-.008zM9.75 15h.008v.008H9.75V15zm0 2.25h.008v.008H9.75v-.008zM7.5 15h.008v.008H7.5V15zm0 2.25h.008v.008H7.5v-.008zm6.75-4.5h.008v.008h-.008v-.008zm0 2.25h.008v.008h-.008V15zm0 2.25h.008v.008h-.008v-.008zm2.25-4.5h.008v.008H16.5v-.008zm0 2.25h.008v.008H16.5V15z"
                    />
                  </svg>
                  Fechas generales liga
                </div>
                <div className="mt-4 p-2 rounded-md shadow">
                  <div className="pl-2 pr-2 pb-3 text-sm text-start gap-3">
                    <div className="pt-2 w-full">
                      <TextFieldLabel
                        title="Inicio de inscripción"
                        type="date"
                        name="enrollmentStartDate"
                        placeholder="Fecha inicio inscripción"
                        text={props.data?.enrollment_start_date}
                        required
                        noVoid
                      />
                    </div>
                    <div className="pt-2 w-full">
                      <TextFieldLabel
                        title="Final de inscripción"
                        type="date"
                        name="enrollmentEndDate"
                        placeholder="Fecha final inscripción"
                        text={props.data?.enrollment_end_date}
                        required
                        noVoid
                      />
                    </div>
                    <div className="pt-2 w-full">
                      <TextFieldLabel
                        title="Inicio liguilla"
                        type="date"
                        name="leaguesStartDate"
                        placeholder="Fecha inicio liguilla"
                        text={props.data?.leagues_start_date}
                        required
                        noVoid
                      />
                    </div>
                    <div className="pt-2 w-full">
                      <TextFieldLabel
                        title="Final liguilla"
                        type="date"
                        name="leaguesEndDate"
                        placeholder="Fecha final liguilla"
                        text={props.data?.league_end_date}
                        required
                        noVoid
                      />
                    </div>
                    <div className="pt-2 w-full">
                      <TextFieldLabel
                        title="Clausura"
                        type="date"
                        name="endDate"
                        placeholder="Fecha clausura"
                        text={props.data?.end_date}
                        required
                        noVoid
                      />
                    </div>
                  </div>
                </div>
                <div className="mt-4 p-2 rounded-md shadow">
                  <div className="pl-2 pr-2 pb-3 text-sm text-start flex gap-3">
                    <div className="pt-2 w-full">
                      <SelectField
                        title="Mostrar categorías"
                        name="showCategories"
                        default="Seleccione el estado"
                        body={[
                          {
                            value: 0,
                            label: "No",
                            selected: !props.data?.show_categories,
                          },
                          {
                            value: 1,
                            label: "Si",
                            selected: props.data?.show_categories,
                          },
                        ]}
                        required
                        noVoid
                      />
                    </div>
                    <div className="pt-2 w-full">
                      <SelectField
                        title="Mostrar calendarios"
                        name="showCalendars"
                        default="Seleccione el estado"
                        body={[
                          {
                            value: 0,
                            label: "No",
                            selected: !props.data?.show_calendars,
                          },
                          {
                            value: 1,
                            label: "Si",
                            selected: props.data?.show_calendars,
                          },
                        ]}
                        required
                        noVoid
                      />
                    </div>
                  </div>
                </div>
              </div>
              <div className="h-max">
                <div className="flex bg-cyan-600 p-2 pl-3 rounded-md text-white text-start">
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    fill="none"
                    viewBox="0 0 24 24"
                    strokeWidth={1.5}
                    stroke="currentColor"
                    className="w-6 h-6 mr-2 mb-0.5"
                  >
                    <path
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      d="M6 13.5V3.75m0 9.75a1.5 1.5 0 010 3m0-3a1.5 1.5 0 000 3m0 3.75V16.5m12-3V3.75m0 9.75a1.5 1.5 0 010 3m0-3a1.5 1.5 0 000 3m0 3.75V16.5m-6-9V3.75m0 3.75a1.5 1.5 0 010 3m0-3a1.5 1.5 0 000 3m0 9.75V10.5"
                    />
                  </svg>
                  Puntuaciones
                </div>
                <CardPuntos
                  title="Puntos ganador"
                  inputs={[
                    {
                      title: "2 Sets",
                      name: "pointsWin2Sets",
                      placeholder: "Puntos",
                      text: props.data?.points_win_2_sets,
                      required: true,
                      noVoid: true,
                    },
                    {
                      title: "3 Sets",
                      name: "pointsWin3Sets",
                      placeholder: "Puntos",
                      text: props.data?.points_win_3_sets,
                      required: true,
                      noVoid: true,
                    },
                  ]}
                />
                <CardPuntos
                  title="Puntos perdedor"
                  inputs={[
                    {
                      title: "2 Sets",
                      name: "pointsLoss2Sets",
                      placeholder: "Puntos",
                      text: props.data?.points_loss_2_sets,
                      required: true,
                      noVoid: true,
                    },
                    {
                      title: "3 Sets",
                      name: "pointsLoss3Sets",
                      placeholder: "Puntos",
                      text: props.data?.points_loss_3_sets,
                      required: true,
                      noVoid: true,
                    },
                  ]}
                />
                <CardPuntos
                  title="Puntos empate"
                  inputs={[
                    {
                      title: "Puntos",
                      name: "pointsTie",
                      placeholder: "Puntos",
                      text: props.data?.points_tie,
                      required: true,
                      noVoid: true,
                    },
                  ]}
                />
                <CardPuntos
                  title="Puntos wo"
                  inputs={[
                    {
                      title: "Ganador",
                      name: "pointsWoWin",
                      placeholder: "Puntos",
                      text: props.data?.points_wo_win,
                      required: true,
                      noVoid: true,
                    },
                    {
                      title: "Perdedor",
                      name: "pointsWoLoss",
                      placeholder: "Puntos",
                      text: props.data?.points_wo_loss,
                      required: true,
                      noVoid: true,
                    },
                  ]}
                />
              </div>
              <div className="h-max">
                <div className="flex bg-gray-600 p-2 pl-3 rounded-md text-white text-start">
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    fill="none"
                    viewBox="0 0 24 24"
                    strokeWidth={1.5}
                    stroke="currentColor"
                    className="w-6 h-6 mr-2 mb-0.5"
                  >
                    <path
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      d="M15 19.128a9.38 9.38 0 002.625.372 9.337 9.337 0 004.121-.952 4.125 4.125 0 00-7.533-2.493M15 19.128v-.003c0-1.113-.285-2.16-.786-3.07M15 19.128v.106A12.318 12.318 0 018.624 21c-2.331 0-4.512-.645-6.374-1.766l-.001-.109a6.375 6.375 0 0111.964-3.07M12 6.375a3.375 3.375 0 11-6.75 0 3.375 3.375 0 016.75 0zm8.25 2.25a2.625 2.625 0 11-5.25 0 2.625 2.625 0 015.25 0z"
                    />
                  </svg>
                  Parejas por grupo
                </div>
                <CardPuntos
                  inputs={[
                    {
                      title: "Cantidad de parejas",
                      name: "couplesByGroup",
                      placeholder: "Cantidad de parejas",
                      text: props.data?.couples_by_group,
                      required: true,
                      noVoid: true,
                    },
                  ]}
                />
              </div>
              <div className="h-max">
                <div className="mt-4 p-2 rounded-md shadow">
                  <div className="pl-2 pr-2 pb-3 text-sm text-start gap-3">
                    <div className="pt-2 w-full">
                      <SelectField
                        title="Categoría mixta gratis"
                        name="freeMixedCategory"
                        default="Seleccione el estado"
                        body={[
                          {
                            value: 0,
                            label: "No",
                            selected: !props.data?.free_mixed_category,
                          },
                          {
                            value: 1,
                            label: "Si",
                            selected: props.data?.free_mixed_category,
                          },
                        ]}
                        required
                        noVoid
                      />
                    </div>
                    <div className="pt-2 w-full">
                      <SelectField
                        title="Seguro de accidente"
                        name="accidentInsuranceId"
                        default="Seleccione el seguro de accidente"
                        body={
                          props.data?.accidents_insurance &&
                          props.data?.accidents_insurance.map((item) => ({
                            value: item.id,
                            label: item.name,
                            selected:
                              item.id === props.data?.accident_insurance_id,
                          }))
                        }
                        required
                        noVoid
                      />
                    </div>
                  </div>
                </div>
              </div>
              <div className="h-max">
                <div className="flex bg-gray-600 p-2 pl-3 rounded-md text-white text-start">
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    fill="none"
                    viewBox="0 0 24 24"
                    strokeWidth={1.5}
                    stroke="currentColor"
                    className="w-6 h-6 mr-2 mb-0.5"
                  >
                    <path
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      d="M12 6v12m-3-2.818l.879.659c1.171.879 3.07.879 4.242 0 1.172-.879 1.172-2.303 0-3.182C13.536 12.219 12.768 12 12 12c-.725 0-1.45-.22-2.003-.659-1.106-.879-1.106-2.303 0-3.182s2.9-.879 4.006 0l.415.33M21 12a9 9 0 11-18 0 9 9 0 0118 0z"
                    />
                  </svg>
                  Crédito
                </div>
                <div className="pt-3">
                  <TextFieldLabel
                    title="Crédito de inscripción"
                    type="number"
                    name="enrollmentCredit"
                    placeholder="Cantidad de crédito"
                    text={props.data?.enrollment_credit}
                    //required
                    //noVoid
                  />
                </div>
                <div className="pt-3">
                  <TextFieldLabel
                    title="Referencia de crédito"
                    type="number"
                    name="referenceCredit"
                    placeholder="Referencia de crédito"
                    text={props.data?.reference_credit}
                    //required
                    //noVoid
                  />
                </div>
                <div className="pt-3">
                  <TextFieldLabel
                    title="Descuento de reinscripción"
                    type="number"
                    name="discountReEnrollment"
                    placeholder="Descuento de reinscripción"
                    text={props.data?.discount_reenrollment}
                    //required
                    //noVoid
                  />
                </div>
              </div>
              <div className="h-max">
                <div className="h-max mt-6">
                  <div className="flex bg-gray-600 p-2 pl-3 rounded-md text-white text-start">
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      fill="none"
                      viewBox="0 0 24 24"
                      strokeWidth={1.5}
                      stroke="currentColor"
                      className="w-6 h-6 mr-2 mb-0.5"
                    >
                      <path
                        strokeLinecap="round"
                        strokeLinejoin="round"
                        d="M11.25 11.25l.041-.02a.75.75 0 011.063.852l-.708 2.836a.75.75 0 001.063.853l.041-.021M21 12a9 9 0 11-18 0 9 9 0 0118 0zm-9-3.75h.008v.008H12V8.25z"
                      />
                    </svg>
                    Política
                  </div>
                  <div className="mt-4">
                    <TextArea
                      name="policy"
                      rows={4}
                      placeholder="Política..."
                      text={props.data?.policy}
                      required
                      noVoid
                    />
                  </div>
                  <div className="mt-4">
                    <div className="mt-4 p-2 rounded-md shadow">
                      <div className="text-start pl-2">Foto de portada</div>
                      <div className="flex mt-2">
                        <div className="w-20 h-20 rounded-md m-2">
                          <img
                            src={imgCover || api.resources + props.data?.cover}
                            className="w-20 h-20 rounded-md"
                            alt=" "
                          />
                        </div>
                        <div className="m-auto bg-blue-500 rounded-md p-2 w-max h-max pr-3 pl-3">
                          <label
                            htmlFor="file"
                            className="text-white font-semibold cursor-pointer flex"
                          >
                            <svg
                              xmlns="http://www.w3.org/2000/svg"
                              viewBox="0 0 24 24"
                              fill="currentColor"
                              className="w-6 h-6 pr-2 text-white font-medium"
                            >
                              <path
                                fillRule="evenodd"
                                d="M11.47 2.47a.75.75 0 011.06 0l4.5 4.5a.75.75 0 01-1.06 1.06l-3.22-3.22V16.5a.75.75 0 01-1.5 0V4.81L8.03 8.03a.75.75 0 01-1.06-1.06l4.5-4.5zM3 15.75a.75.75 0 01.75.75v2.25a1.5 1.5 0 001.5 1.5h13.5a1.5 1.5 0 001.5-1.5V16.5a.75.75 0 011.5 0v2.25a3 3 0 01-3 3H5.25a3 3 0 01-3-3V16.5a.75.75 0 01.75-.75z"
                                clipRule="evenodd"
                              />
                            </svg>
                            Subir archivo
                          </label>
                          <input
                            id="file"
                            name="image"
                            type="file"
                            className="hidden"
                            onChange={imgChange}
                            accept="image/*"
                          />
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className="w-full mt-6">
            <PrimaryButton
              type="submit"
              className="rounded-lg"
              text={props.buttonText}
            />
          </div>
        </form>
      </div>
    </>
  );
}

function CardPuntos(props) {
  return (
    <div className="mt-4 p-2 rounded-md shadow">
      <div className="text-start pl-2">{props.title}</div>
      <div className={"pl-2 pr-2 pb-3 text-sm text-start flex gap-3"}>
        {props.inputs &&
          props.inputs.map((item) => (
            <div className="pt-2 w-full">
              <TextFieldLabel
                title={item.title}
                type="number"
                name={item.name}
                id={item.id}
                placeholder={item.placeholder}
                text={item.text}
                required={item.required}
                noVoid={item.noVoid}
              />
            </div>
          ))}
      </div>
    </div>
  );
}

export default LigasUpdate;

