import TitlePage from "../../components/TitlePage/TitlePage";
import TextFieldLabel from "../../components/TextField/TextFieldLabel.js";
import TextArea from "../../components/TextField/TextArea";
import PrimaryButton from "../../components/Buttons/PrimaryButton.js";
import ModalAccept from "../../components/Modal/ModalAccept";
import { useState } from "react";
import api from "../../api/api";

function CampusesUpdate(props) {
  const [modalAccept, setModalAccept] = useState(false);
  const [imgCover, setImgCover] = useState(false);

  const submit = (form) => {
    form.preventDefault();
    const formValues = Object.fromEntries(new FormData(form.target).entries());
    for (let propiedad in formValues) {
      if (formValues[propiedad] === "") {
        delete formValues[propiedad];
      }
    }
    formValues.campusId = props?.data?.id;
    setModalAccept({
      ok: () => {
        setModalAccept(false);
        props.onSubmit(formValues);
      },
      cancel: () => setModalAccept(false),
    });
  };

  const imgChange = (e) => {
    const file = e.target.files[0];
    const reader = new FileReader();

    reader.onload = () => setImgCover(reader.result);

    if (file) reader.readAsDataURL(file);
  };

  return (
    <>
      {modalAccept && <ModalAccept {...modalAccept} />}
      <div className="align-center absolute pl-2 pr-2 w-full h-screen text-center bg-gray-100 pb-20">
        <div className="flex justify-end items-end mt-4 mr-4">
          <svg
            xmlns="http://www.w3.org/2000/svg"
            fill="none"
            viewBox="0 0 24 24"
            strokeWidth={1.5}
            stroke="currentColor"
            className="w-6 h-6 cursor-pointer"
            onClick={props.onCancel}
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              d="M6 18L18 6M6 6l12 12"
            />
          </svg>
        </div>
        <TitlePage text={props.title} />
        <form onSubmit={submit} className="flex justify-center">
          <div>
            <div className="p-4 grid grid-cols-2 max-[600px]:grid-cols-1 gap-6 w-screen max-w-xl">
              <div>
                <TextFieldLabel
                  title="Nombre"
                  type="text"
                  name="name"
                  placeholder="Nombre de la sede"
                  text={props.data?.name}
                  required={!props.data}
                  noVoid
                />
              </div>
              <div>
                <TextFieldLabel
                  title="Dirección"
                  type="text"
                  name="address"
                  placeholder="Dirección de la sede"
                  text={props.data?.address}
                  required={!props.data}
                  noVoid
                />
              </div>
              <div>
                <TextFieldLabel
                  title="Teléfono"
                  type="number"
                  name="phone"
                  placeholder="Teléfono de la sede"
                  text={props.data?.phone}
                  required={!props.data}
                  noVoid
                />
              </div>
              <div>
                <TextFieldLabel
                  title="Pistas interiores"
                  type="number"
                  name="countTrackIndoor"
                  placeholder="Cantidad de pistas interiores"
                  text={props.data?.count_tracks_indoor}
                  required={!props.data}
                  noVoid
                />
              </div>
              <div>
                <TextFieldLabel
                  title="Pistas exteriores"
                  type="number"
                  name="countTrackOutdoor"
                  placeholder="Cantidad de pistas exteriores"
                  text={props.data?.count_tracks_outdoor}
                  required={!props.data}
                  noVoid
                />
              </div>
              <div>
                <TextFieldLabel
                  title="Sitio web"
                  type="text"
                  name="web"
                  placeholder="Url de la sitio web de la sede"
                  text={props.data?.web}
                  required={!props.data}
                  noVoid
                />
              </div>
              <div>
                <TextFieldLabel
                  title="Mapa de la sede"
                  type="text"
                  name="maps"
                  placeholder="Mapa de la sede"
                  text={props.data?.maps}
                  required={!props.data}
                  noVoid
                />
              </div>
              <div>
                <TextFieldLabel
                  title="Latitud"
                  type="text"
                  name="latitude"
                  placeholder="Latitud"
                  text={props.data?.latitude}
                  required={!props.data}
                  noVoid
                />
              </div>
              <div>
                <TextFieldLabel
                  title="Longitud"
                  type="text"
                  name="longitude"
                  placeholder="Longitud"
                  text={props.data?.longitude}
                  required={!props.data}
                  noVoid
                />
              </div>
              <div>
                <TextArea
                  name="observations"
                  rows={4}
                  placeholder="Observaciones..."
                  text={props.data?.observations}
                  required={!props.data}
                  noVoid
                />
              </div>
              <div>
                <div className="mt-3">
                  <div className="flex mt-6 gap-6 justify-center items-center">
                    <div className="w-20 h-20 rounded-md Xm-2 bg-gray-200">
                      <img
                        src={imgCover || api.resources + props.data?.cover}
                        className="w-20 h-20 rounded-md"
                        alt=" "
                      />
                    </div>
                    <div className=" bg-blue-500 rounded-md p-2 w-max h-max pr-3 pl-3">
                      <label
                        htmlFor="file"
                        className="text-white font-semibold cursor-pointer flex"
                      >
                        <svg
                          xmlns="http://www.w3.org/2000/svg"
                          viewBox="0 0 24 24"
                          fill="currentColor"
                          className="w-6 h-6 pr-2 text-white font-medium"
                        >
                          <path
                            fillRule="evenodd"
                            d="M11.47 2.47a.75.75 0 011.06 0l4.5 4.5a.75.75 0 01-1.06 1.06l-3.22-3.22V16.5a.75.75 0 01-1.5 0V4.81L8.03 8.03a.75.75 0 01-1.06-1.06l4.5-4.5zM3 15.75a.75.75 0 01.75.75v2.25a1.5 1.5 0 001.5 1.5h13.5a1.5 1.5 0 001.5-1.5V16.5a.75.75 0 011.5 0v2.25a3 3 0 01-3 3H5.25a3 3 0 01-3-3V16.5a.75.75 0 01.75-.75z"
                            clipRule="evenodd"
                          />
                        </svg>
                        Subir archivo
                      </label>
                      <input
                        id="file"
                        name="image"
                        type="file"
                        className="hidden"
                        onChange={imgChange}
                        accept="image/*"
                      />
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div className="flex justify-center w-full mt-8">
              <PrimaryButton
                type="submit"
                className="rounded-lg"
                text={props.buttonText}
              />
            </div>
          </div>
        </form>
      </div>
    </>
  );
}

export default CampusesUpdate;
