import { Dialog as AlertDialog, Transition } from "@headlessui/react";
import React, { Fragment, useRef } from "react";

const Dialog = ({ action, setAction, title, children }) => {

    const cancelButtonRef = useRef(null);

    return (


        <Transition.Root show={action} as={Fragment}>
            <AlertDialog
                as="div"
                className="relative z-10 mt-8"
                initialFocus={cancelButtonRef}
                onClose={setAction}
            >
                <Transition.Child
                    as={Fragment}
                    enter="ease-out duration-300"
                    enterFrom="opacity-0"
                    enterTo="opacity-100"
                    leave="ease-in duration-200"
                    leaveFrom="opacity-100"
                    leaveTo="opacity-0"
                >
                    <div className="fixed inset-0 bg-gray-500 bg-opacity-75 transition-opacity" />
                </Transition.Child>

                <div className="fixed inset-0 z-10 overflow-y-auto">
                    <div className="flex h-screen  justify-center p-4 text-center items-center sm:p-0">
                        <Transition.Child
                            as={Fragment}
                            enter="ease-out duration-300"
                            enterFrom="opacity-0 translate-y-4 sm:translate-y-0 sm:scale-95"
                            enterTo="opacity-100 translate-y-0 sm:scale-100"
                            leave="ease-in duration-200"
                            leaveFrom="opacity-100 translate-y-0 sm:scale-100"
                            leaveTo="opacity-0 translate-y-4 sm:translate-y-0 sm:scale-95"
                        >
                            <AlertDialog.Panel className="relative bg-white transform overflow-hidden rounded-lg px-4 pt-5 pb-4 text-left shadow-xl transition-all my-8 sm:w-full sm:max-w-lg sm:p-6 h-fit">
                                {children}
                            </AlertDialog.Panel>
                        </Transition.Child>
                    </div>
                </div>
            </AlertDialog>
        </Transition.Root>
    );
}



export default Dialog;