import "./App.css";
import { BrowserRouter, Routes, Route } from "react-router-dom";
import FrontPage from "./pages/FrontPage/FrontPage.js";
import NotFound from "./pages/NotFound/NotFound.js";
import LiguillaCalendar from "./pages/LiguillaCalendar/LiguillaCalendar.js";
import CrucesCalendar from "./pages/CrucesCalendar/CrucesCalendar.js";
import Clasificaciones from "./pages/Clasificaciones/Clasificaciones.js";
import Participantes from "./pages/Participantes/Participantes.js";
import Login from "./pages/Login/Login.js";
import Registro from "./pages/Registro/Registro.js";
import RecuperarCuenta from "./pages/RecuperarCuenta/RecuperarCuenta.js";
import Provinces from "./pages/Provinces/Provinces";
import Municipalities from "./pages/Municipalities/Municipalities";
import AwardTypes from "./pages/AwardTypes/AwardTypes";
import Levels from "./pages/Levels/Levels";
//import Genders from "./pages/Genders/Genders";
import Positions from "./pages/Positions/Positions";
import MatchesTypes from "./pages/MatchesTypes/MatchesTypes";
import "react-toastify/dist/ReactToastify.css";
import Categories from "./pages/Categories/Categories";
import Documents from "./pages/Documents/Documents";
import CrossoverType from "./pages/CrossoverTypes/CrossoverType";
import Galleries from "./pages/Galleries/Galleries";
import Gifts from "./pages/Gifts/Gifts";
import Ligas from "./pages/Ligas/Ligas";
import MyInfo from "./pages/MyInfo/MyInfo";
import Awards from "./pages/Awards/Awards";
import Sponsors from "./pages/Sponsors/Sponsors";
import useUser from "./hooks/auth/useUser";
import LigasCategorias from "./pages/Ligas/LigasCategorias";
import LigasRegalos from "./pages/Ligas/LigasRegalos";
import LigasSedes from "./pages/Ligas/LigasSedes";
import Campuses from "./pages/Campuses/Campuses";
import CampusContact from "./pages/Campuses/CampusContact";
import AccidentInsurances from "./pages/AccidentInsurances/AccidentInsurances";
import Groups from "./pages/Groups/Groups";
import Users from "./pages/Users/Users";
import ParejasResumen from "./pages/Parejas/ParejasResumen";
import Parejas from "./pages/Parejas/Parejas";
import Noticias from "./pages/Noticias/Noticias";
import PublicGallery from "./pages/Galleries/PublicGallery";
import RegistroReferencia from "./pages/Registro/RegistroReferencia";
import Enrollment from "./pages/Enrollment";
import Finish from "./pages/Enrollment/Finish";
import PayEnrollment from "./pages/Enrollment/PayEnrollment";
import FLoatingActionButton from "./components/Buttons/FloatingActionButton";
import NotificationIcon from "./components/Icon/NotificationIcon";
import Ping from "./components/Icon/Ping";

const App = () => {
  const [userData, setUserData] = useUser();
  return (
    <BrowserRouter>
      <div className="relative">
        <Routes>
          {/*Rutas para todos los users*/}
          <Route path="/" element={<FrontPage />} />
          <Route path="/calendario/liguilla" element={<LiguillaCalendar />} />
          <Route path="/calendario/cruces" element={<CrucesCalendar />} />
          <Route path="/clasificaciones" element={<Clasificaciones />} />
          <Route path="/participantes" element={<Participantes />} />
          <Route path="/provinces" element={<Provinces />} />
          <Route path="/municipalities" element={<Municipalities />} />
          <Route path="/awardTypes" element={<AwardTypes />} />
          <Route path="/levels" element={<Levels />} />
          {/*<Route path="/genders" element={<Genders />} />*/}
          <Route path="/positions" element={<Positions />} />
          <Route path="/matchesTypes" element={<MatchesTypes />} />
          <Route path="/categories" element={<Categories />} />
          <Route path="/documents" element={<Documents />} />
          <Route path="/crossOverTypes" element={<CrossoverType />} />
          <Route path="/galleries" element={<Galleries />} />
          <Route path="/gifts" element={<Gifts />} />
          <Route path="/gallery" element={<PublicGallery />} />
          <Route path="/enrollment" element={<Enrollment />} />
          <Route path="/enrollment/finished/:enrollmentId" element={<Finish />} />
          <Route path="/enrollment/pay/:enrollmentId" element={<PayEnrollment />} />
          <Route path="/enrollment/gift" element={<Enrollment />} />
          {/*Rutas admin*/}
          {
            //Condiciones para que se muestren las paginas de admin
            userData.permissions == "admin" && (
              <>
                <Route path="/admin/ligas" element={<Ligas />} />
                <Route path="/admin/ligas/sedes" element={<LigasSedes />} />
                <Route
                  path="/admin/ligas/categorias"
                  element={<LigasCategorias />}
                />
                <Route path="/admin/ligas/regalos" element={<LigasRegalos />} />
                <Route path="/admin/ligas/premios" element={<Awards />} />

                <Route path="/admin/grupos" element={<Groups />} />
                <Route path="/admin/jugadores" element={<Users />} />

                <Route path="/admin/parejas" element={<Parejas />} />
                <Route
                  path="/admin/parejas/parejas-resumen"
                  element={<ParejasResumen />}
                />

                <Route path="/admin/noticias" element={<Noticias />} />
                <Route path="/admin/galeria" element={<Galleries />} />

                <Route path="/admin/categorias" element={<Categories />} />
                <Route path="/admin/provincias" element={<Provinces />} />
                <Route path="/admin/municipios" element={<Municipalities />} />
                <Route path="/admin/documentos" element={<Documents />} />
                <Route path="/admin/niveles" element={<Levels />} />
                <Route path="/admin/posiciones" element={<Positions />} />
                <Route path="/admin/sedes" element={<Campuses />} />
                <Route
                  path="/admin/sedes-contactos"
                  element={<CampusContact />}
                />
                <Route path="/admin/regalos" element={<Gifts />} />
                <Route
                  path="/admin/tipos-de-partido"
                  element={<MatchesTypes />}
                />
                <Route path="/admin/tipos-de-premio" element={<AwardTypes />} />
                <Route
                  path="/admin/tipos-de-cruce"
                  element={<CrossoverType />}
                />
                <Route path="/admin/premios" element={<Awards />} />
                <Route path="/admin/patrocinadores" element={<Sponsors />} />
                <Route path="/admin/seguro" element={<AccidentInsurances />} />
              </>
            )
          }
          {/*Rutas de usuario*/}
          {userData.permissions != "" && (
            <>
              <Route path="/me" element={<MyInfo />} />
              <Route path="/mes" element={<>Hola /admin/ligas/sede</>} />
              <Route path="/mess" element={<>Hola /admin/ligas/categoria</>} />
              <Route path="/messs" element={<>Hola /admin/ligas/regalos</>} />
            </>
          )}
          {/*Rutas de authentication*/}
          <Route path="/login" element={<Login />} />
          <Route path="/registro" element={<Registro />} />
          <Route path="/registro/ref/" element={<RegistroReferencia />} />
          <Route path="/recuperar" element={<RecuperarCuenta />} />
          {/*Error 404 personalizado.*/}
          <Route path="*" element={<NotFound />} />
        </Routes>
       
      </div>
    </BrowserRouter>
  );
};

export default App;
