import React, { useEffect, useRef, useState } from "react";
import NavBar from "../../components/NavBar/NavBar";
import Footer from "../../components/Footer/Footer";
import WizardMenu from "../../components/WisardMenu/WisardMenu";
import './css/index.css';
import useUser from "../../hooks/auth/useUser";
import {
    getAvailableCategoriesByEdition,
} from "../../api/categories";
import {
    getCampuses,
} from "../../api/campuses";
import Loading from "../../components/Loading/Loading";
import Dialog from "../../components/Dialog/Dialog";
import { register } from "../../api/auth";
import { Navigate, useNavigate } from "react-router-dom";
import { checkIfPhoneExist, getAmountWeeks, newEnrollment } from "../../api/enrollments";
import RegisterForm from "./Components/RegisterForm";
import UserDetailsForm from "./Components/UserDetailsForm";
import LoadingIcon from "../../components/Loading/Icon";
import BasicInformationForm from "./Components/BasicInformationForm";
import Campus from "./Components/Campus";
import Gift from "./Components/Gift";
import Checkout from "./Components/Checkout";
import { getGifts } from "../../api/gifts";
import Time from "./Components/Times";
import { ToastContainer, toast } from "react-toastify";
import TimeLine from "./Components/TimeLine";

export default () => {
    const [categories, setCategories] = useState([]);
    const [userData] = useUser();
    const [stateLoading, setStateLoading] = useState(false);
    const [action, setAction] = useState(false);
    const [phone, setPhone] = useState('');
    const [playerTwo, setPlayerTwo] = useState(null);
    const [loadingInfo, setLoadingInfo] = useState(true)
    const [registered, setRegistered] = useState(false);
    const [campuses, setCampuses] = useState([]);
    const [favoritesCampuses, setFavoriteCampuses] = useState([]);
    const [gifts, setGifts] = useState([]);
    const [selectedGifts1, setSelectedGifts1] = useState([]);
    const [selectedGifts2, setSelectedGifts2] = useState([]);
    const titles = ['Información personal', 'Horarios disponibles para jugar', 'Elegir pistas favoritas', 'Elegir regalos', 'Finalizar inscripción'];
    const [position, setPosition] = useState(0);
    const [optionPay, setOptionPay] = useState();
    const navigate = useNavigate();
    const timesRef = useRef(null)
    const [weeks, setWeeks] = useState(0);


    const toastConfig = {
        position: "top-right",
        autoClose: 2500,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
    };

    const checkUser = () => {
        if (isNaN(phone)) {
            toast.error("Compruebe que el número de teléfono sea válido", toastConfig);
        } else {
            setLoadingInfo(true)
            setAction(true)

            checkIfPhoneExist(phone, userData.token, (json, error) => {
                if (json) {
                    if (json.code == "ok") {
                        setLoadingInfo(false)
                        setPlayerTwo(json.data)
                    } else {
                        toast.error(json.message, toastConfig);
                        setAction(false)
                    }
                } else {
                    toast.error("Ha ocurrido un error en la verificación", toastConfig);

                }

            })
        }
    }

    const handleEnrollmentSubmit = e => {
        e.preventDefault();

        const formValues = Object.fromEntries(new FormData(e.target).entries());
        const days = Array.from(timesRef.current.querySelectorAll(`.week`));
        let image = e.target.querySelector('#file').files[0];
        let allDates = [];

        for (const iterator of days) {
            const ds = iterator.querySelectorAll('input[type="checkbox"]');
            let inputs = Array.from(ds, checkbox => ({ [checkbox.name]: checkbox.checked }))
            inputs.push({ startTime: iterator.querySelector('input[name="startTime"]').value }, { endTime: iterator.querySelector('input[name="endTime"]').value });
            allDates.push(Object.assign({}, ...inputs));
        }


        const data = {
            categoryId: formValues.category,
            player2Id: playerTwo.id,
            name: formValues.name,
            favoritesCampuses: [...favoritesCampuses],
            reserveTrack: formValues.reserveTrack,
            playOpeningDay: formValues.playOpeningDay,
            observations: formValues.observations,
            acceptDataProtectionPolicy: true,
            player1Gift: selectedGifts1[0],
            player2Gift: selectedGifts2[0],
            timesAvailabilities: [
                ...allDates
            ]
        }

        const imageToBase64 = (image) => {
            return new Promise((resolve, reject) => {
                const reader = new FileReader();
                reader.readAsDataURL(image);
                reader.onload = () => resolve(reader.result);
                reader.onerror = error => reject(error);
            });
        };
        imageToBase64(image).then(img => data.photo = img);
        setStateLoading(true)
        newEnrollment(data, userData.token, (json, error) => {
            if (json) {
                 if (json.code == "ok") {
                    toast.success(json.message, toastConfig);
                    if (optionPay == 1) {
                        navigate('/enrollment/pay/' + json.data.id)
                    } else {
                        navigate('/enrollment/finished/' + json.data.id)
                    }
                } else {
                    toast.error(json.message, toastConfig);
                } 
            }
            setStateLoading(false);


        });
    }

    const infoAsync = async () => {
        try {
            setStateLoading(true);

            const [c, cat, g, aW] = await Promise.all([
                new Promise((resolve, reject) => {
                    getCampuses((json, error) => {
                        if (json) {
                            if (json.code === 'ok') {
                                resolve(json.data);
                            } else {
                                reject(new Error(`Error getting campuses: ${json.message}`));
                            }
                        } else {
                            reject(new Error(`Error getting campuses: ${error}`));
                        }
                    });
                }),
                new Promise((resolve, reject) => {
                    getAvailableCategoriesByEdition((json, error) => {
                        if (json) {
                            if (json.code === 'ok') {
                                resolve(json.data);
                            } else {
                                reject(new Error(`Error getting categories: ${json.message}`));
                            }
                        } else {
                            reject(new Error(`Error getting categories: ${error}`));
                        }
                    });
                }),
                new Promise((resolve, reject) => {
                    getGifts((json, error) => {
                        if (json) {
                            if (json.code === 'ok') {
                                resolve(json.data);
                            } else {
                                reject(new Error(`Error getting gifts: ${json.message}`));
                            }
                        } else {
                            reject(new Error(`Error getting gifts: ${error}`));
                        }
                    });
                }),
                new Promise((resolve, reject) => {
                    getAmountWeeks(userData.token, (json, error) => {
                        if (json) {
                            if (json.code === 'ok') {
                                resolve(json.amount);
                            } else {
                                reject(new Error(`Error getting weeks: ${json.message}`));
                            }
                        } else {
                            reject(new Error(`Error getting weeks: ${error}`));
                        }
                    });
                })
            ]);

            setCampuses([...c]);
            setCategories([...cat]);
            setGifts([...g]);
            setWeeks(aW);

            setStateLoading(false);

        } catch (error) {
            console.error(error);
            setStateLoading(false);
        }
    };

    const handleSubmit = (e) => {
        e.preventDefault();
        const formValues = Object.fromEntries(new FormData(e.target).entries());
        const data = {
            name: formValues.name,
            lastName: formValues.lastName,
            password: formValues.password,
            email: formValues.email,
            phone: formValues.phone,
            referenceCode: userData.id,
            acceptPP: true
        };
        setStateLoading(true);
        register(data, (json, error) => {
            //Loading stop
            setStateLoading(false);
            if (json) {
                if (json.code == "ok") {
                    //Register successfull
                    console.log(json.data.user.id);
                    setAction(false);
                    setRegistered(true)
                    setPlayerTwo(json.data.user.id)
                    toast.success("Usuario registrado correctamente!", toastConfig);
                } else {
                    toast.error('Compruebe los datos antes de registrar al jugador', toastConfig);
                }
            } else {
                //Error register
                toast.error("Ocurrio un error!, " + error, toastConfig);
            }

        });
    }

    useEffect(() => {
        infoAsync();
    }, []);

    useEffect(() => {
        setRegistered(false);
        setAction(false);
    }, [phone]);

    const handleRegister = () => {
        setRegistered(true);
    }

    return userData.permissions == "" ? <Navigate to={'/login'} replace /> :
        <>
            <NavBar />
            {stateLoading && <Loading />}
            <ToastContainer />
            <div className="responsive-form">
                <form className="form" onSubmit={handleEnrollmentSubmit}>
                    <h1 className="text-4xl text-center text-blue-500 font-bold">{titles[position]}</h1>
                    <TimeLine position={position} />
                    <WizardMenu
                        checkUser={checkUser}
                        registered={registered}
                        setPosition={setPosition}
                        strollToTop={true}
                        sections={[
                            <BasicInformationForm phone={phone} setPhone={setPhone} categories={categories} />
                            ,
                            <Time timesRef={timesRef} weeks={weeks} />,
                            <Campus favoritesCampuses={favoritesCampuses} setFavoriteCampuses={setFavoriteCampuses} campuses={campuses} />
                            ,
                            <Gift selectedGifts1={selectedGifts1} setSelectedGifts1={setSelectedGifts1} selectedGifts2={selectedGifts2} setSelectedGifts2={setSelectedGifts2} gifts={gifts} />
                            ,
                            <Checkout setOptionPay={setOptionPay} />
                            ,
                        ]}
                    />
                </form>
            </div>
            <Footer />
            <Dialog action={action && !registered} setAction={setAction} >
                {
                    loadingInfo ?
                        <div className="flex items-center justify-start"><LoadingIcon width={25} height={25} /><span className="ml-2">Verificando datos...</span></div> :
                        !playerTwo ?
                            <RegisterForm userData={userData} handleSubmit={handleSubmit} phone={phone} /> : <UserDetailsForm playerTwo={playerTwo}
                                handleClick={handleRegister} />
                }
            </Dialog>
        </>


}