import NavBar from "../../components/NavBar/NavBar";
import Footer from "../../components/Footer/Footer";
import TitlePage from "../../components/TitlePage/TitlePage";
import TableColumn from "../../components/Table/TableColumn.js";
import TableRow from "../../components/Table/TableRow.js";
import Table from "../../components/Table/Table.js";
import ItemEdit from "../../components/Table/ItemEdit";
import ItemDelete from "../../components/Table/ItemDelete";
import AddButton from "../../components/Buttons/AddButton";
import Loading from "../../components/Loading/Loading";
import Alert from "../../components/PushMessage/Alert";
import AwardsUpdate from "./AwardsUpdate";
import { useState, useEffect } from "react";
import useUser from "../../hooks/auth/useUser";
import ModalAccept from "../../components/Modal/ModalAccept";
import {
  deleteAward,
  getAwards,
  newAward,
  updateAward,
} from "../../api/awards";
import { getAwardsTypes } from "../../api/awardsTypes";
import { getCategories } from "../../api/categories";
import { getEditions } from "../../api/editions";

function Awards() {
  const [awardsTypes, setAwardsTypes] = useState(false);
  const [categories, setCategories] = useState(false);
  const [editions, setEditions] = useState(false);
  const [awards, setAwards] = useState(false);
  const [stateAlert, setStateAlert] = useState(false);
  const [stateLoading, setStateLoading] = useState(false);
  const [edit, setEdit] = useState(false);
  const [modalAccept, setModalAccept] = useState(false);
  const [userData, setUserData] = useUser();

  const AwardsTypes = () => {
    setStateAlert(false);
    setStateLoading(true);

    getAwardsTypes((json, error) => {
      setStateLoading(false);

      if (json) {
        if (json.code == "ok") {
          setAwardsTypes(json.data);
          //console.log(json.data);
        } else {
          setStateAlert({
            type: "war",
            message: json.message,
          });
        }
      } else {
        setStateAlert({
          type: "err",
          message: "Ocurrio un error!, " + error,
        });
      }
    });
  };

  const Categories = () => {
    setStateAlert(false);
    setStateLoading(true);

    getCategories((json, error) => {
      setStateLoading(false);

      if (json) {
        if (json.code == "ok") {
          setCategories(json.data);
        } else {
          setStateAlert({
            type: "war",
            message: json.message,
          });
        }
      } else {
        setStateAlert({
          type: "err",
          message: "Ocurrio un error!, " + error,
        });
      }
    });
  };

  const Editions = () => {
    setStateAlert(false);
    setStateLoading(true);

    getEditions((json, error) => {
      setStateLoading(false);

      if (json) {
        if (json.code == "ok") {
          setEditions(json.data);
        } else {
          setStateAlert({
            type: "war",
            message: json.message,
          });
        }
      } else {
        setStateAlert({
          type: "err",
          message: "Ocurrio un error!, " + error,
        });
      }
    });
  };

  const Award = () => {
    setStateAlert(false);
    setStateLoading(true);

    getAwards((json, error) => {
      setStateLoading(false);

      if (json) {
        if (json.code == "ok") {
          setAwards(json.data);
        } else {
          setStateAlert({
            type: "war",
            message: json.message,
          });
        }
      } else {
        setStateAlert({
          type: "err",
          message: "Ocurrio un error!, " + error,
        });
      }
    });
  };

  useEffect(() => {
    AwardsTypes();
    Categories();
    Editions();
    Award();
  }, []);

  const addAward = () => {
    setEdit({
      title: "Premio nuevo",
      data: {
        awardsTypes: awardsTypes,
        categories: categories,
        editions: editions,
      },
      buttonText: "Crear nuevo",
      onSubmit: (data) => {
        setStateLoading(true);
        newAward(data, userData.token, (json, error) => {
          setStateLoading(false);
          if (json.code == "ok") {
            setStateAlert({
              type: "ok",
              message: "Premio creado con exito!",
            });
            setTimeout(() => Award(), 1000);
            setEdit(false);
          } else {
            setStateAlert({
              type: "err",
              message: json.message || error,
            });
          }
        });
      },
      onCancel: () => setEdit(false),
    });
  };

  const editAward = (id) => {
    let data = awards.find((e) => e.id == id);
    data.awardsTypes = awardsTypes;
    data.categories = categories;
    data.editions = editions;
    setEdit({
      title: "Editar premio",
      data: data,
      buttonText: "Guardar cambios",
      onSubmit: (dataUpdate) => {
        setStateLoading(true);
        updateAward(dataUpdate, userData.token, (json, error) => {
          setStateLoading(false);
          if (json.code == "ok") {
            setStateAlert({
              type: "ok",
              message: "Premio actualizado con exito!",
            });
            setTimeout(() => Award(), 1000);
            setEdit(false);
          } else {
            setStateAlert({
              type: "err",
              message: json.message || error,
            });
          }
        });
      },
      onCancel: () => setEdit(false),
    });
  };

  const removeAward = (id) => {
    setModalAccept({
      ok: () => {
        setStateLoading(true);
        setModalAccept(false);
        deleteAward(id, userData.token, (json, error) => {
          setStateLoading(false);
          if (json.code == "ok") {
            setStateAlert({
              type: "ok",
              message: "Eliminado con exito!",
            });
            setTimeout(() => Award(), 1000);
            setEdit(false);
          } else {
            setStateAlert({
              type: "err",
              message: json.message || error,
            });
          }
        });
      },
      cancel: () => setModalAccept(false),
    });
  };

  return (
    <>
      {stateLoading && <Loading />}
      {stateAlert && <Alert {...stateAlert} />}
      {edit && <AwardsUpdate {...edit} />}
      {modalAccept && <ModalAccept {...modalAccept} />}
      <NavBar />
      <div className="pl-2 pr-2 block h-full text-center bg-gray-100 pt-10 pb-20">
        <TitlePage text="Admin - Premios" />
        <div className="flex justify-end items-end lg:pr-32 mb-6">
          <div className="w-max h-max" onClick={addAward}>
            <AddButton />
          </div>
        </div>
        <Table
          title="Premios"
          columns={
            <TableColumn
              columns={[
                "Tipo de premio",
                "Categoria",
                "Edicion",
                "Detalles",
                "Fecha de creación",
                "Fecha de actualización",
                "Acciones",
              ]}
            />
          }
          imageHeader={
            <svg
              xmlns="http://www.w3.org/2000/svg"
              fill="none"
              viewBox="0 0 24 24"
              strokeWidth={1.5}
              stroke="currentColor"
              className="w-6 h-6"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                d="M16.5 18.75h-9m9 0a3 3 0 013 3h-15a3 3 0 013-3m9 0v-3.375c0-.621-.503-1.125-1.125-1.125h-.871M7.5 18.75v-3.375c0-.621.504-1.125 1.125-1.125h.872m5.007 0H9.497m5.007 0a7.454 7.454 0 01-.982-3.172M9.497 14.25a7.454 7.454 0 00.981-3.172M5.25 4.236c-.982.143-1.954.317-2.916.52A6.003 6.003 0 007.73 9.728M5.25 4.236V4.5c0 2.108.966 3.99 2.48 5.228M5.25 4.236V2.721C7.456 2.41 9.71 2.25 12 2.25c2.291 0 4.545.16 6.75.47v1.516M7.73 9.728a6.726 6.726 0 002.748 1.35m8.272-6.842V4.5c0 2.108-.966 3.99-2.48 5.228m2.48-5.492a46.32 46.32 0 012.916.52 6.003 6.003 0 01-5.395 4.972m0 0a6.726 6.726 0 01-2.749 1.35m0 0a6.772 6.772 0 01-3.044 0"
              />
            </svg>
          }
        >
          {awards &&
            awards.map((item, index) => (
              <tr key={index}>
                <TableRow>
                  <div className="p-3 font-semibold text-gray-900 text-sm">
                    {awardsTypes &&
                      awardsTypes.find((e) => e.id == item.award_type_id).type}
                  </div>
                </TableRow>
                <TableRow>
                  <div className="p-3 font-semibold text-gray-600 text-sm">
                    {categories &&
                      categories.find((e) => e.id == item.category_id).name}
                  </div>
                </TableRow>
                <TableRow>
                  <div className="p-3 font-semibold text-blue-900 text-sm">
                    {editions &&
                      editions.find((e) => e.id == item.edition_id).name}
                  </div>
                </TableRow>
                <TableRow>
                  <div className="p-3 font-semibold text-gray-600 text-sm">
                    {item.details.length > 6
                      ? item.details.slice(0, 6) + "..."
                      : item.details}
                  </div>
                </TableRow>
                <TableRow>
                  <div className="font-semibold text-blue-500 text-sm">
                    {new Date(item.created_at).toLocaleString()}
                  </div>
                </TableRow>
                <TableRow>
                  <div className="font-semibold text-red-500 text-sm">
                    {new Date(item.updated_at).toLocaleString()}
                  </div>
                </TableRow>
                <TableRow>
                  <div className="flex">
                    <ItemEdit id={item.id} onClick={editAward} />
                    <ItemDelete id={item.id} onClick={removeAward} />
                  </div>
                </TableRow>
              </tr>
            ))}
        </Table>
      </div>
      <Footer />
    </>
  );
}

export default Awards;
