import { NavLink } from "react-router-dom";

function FormAccount(props) {
  return (
    <>
      <div className="flex flex-col items-center justify-center h-full">
        <div className="flex items-center mt-6 text-2xl font-semibold">
          <NavLink
            to="/"
          >
            <img
              className="h-16 XXmr-2 m-10"
              src="https://padelamigos.es/wp-content/uploads/2019/12/logo-p%C3%A1del-amigos-10.png"
              alt="PadelAmigos"
            />
          </NavLink>
        </div>
        <div className="w-full bg-white rounded-lg shadow md:mt-0 sm:max-w-md xl:p-0 mb-6">
          <div className="p-6 space-y-4 md:space-y-6 sm:p-8">
            <h1 className="text-xl font-bold leading-tight tracking-tight text-gray-900 md:text-2xl">
              {props.title}
            </h1>
            <form
              id={props.id}
              className="space-y-6"
              onSubmit={props.submit}
            >
              {props.children}
            </form>
          </div>
        </div>
      </div>
    </>
  );
}

export default FormAccount;
