import TableColumn from "./TableColumn.js";
import TableRow from "./TableRow.js";
import Table from "./Table.js";
import ItemCouple from "./ItemCouple.js";

function TableClasificacion(props) {
  return (
    <Table
      title="Clasificación"
      columns={
        <TableColumn
          columns={[
            "Orden",
            "Pareja",
            "pt",
            "pj",
            "pg",
            "pp",
            "sg",
            "sp",
            "jg",
            "jp"
          ]}
        />
      }
      imageHeader={
        <svg
          xmlns="http://www.w3.org/2000/svg"
          fill="none"
          viewBox="0 0 24 24"
          strokeWidth={1.5}
          stroke="currentColor"
          className="w-6 h-6"
        >
          <path
            strokeLinecap="round"
            strokeLinejoin="round"
            d="M3.75 3v11.25A2.25 2.25 0 006 16.5h2.25M3.75 3h-1.5m1.5 0h16.5m0 0h1.5m-1.5 0v11.25A2.25 2.25 0 0118 16.5h-2.25m-7.5 0h7.5m-7.5 0l-1 3m8.5-3l1 3m0 0l.5 1.5m-.5-1.5h-9.5m0 0l-.5 1.5m.75-9l3-3 2.148 2.148A12.061 12.061 0 0116.5 7.605"
          />
        </svg>
      }
    >
      {props.rows &&
        props.rows.map((item) => (
          <tr>
            <TableRow>{item.id}</TableRow>
            <ItemCouple image={item.couple.image} name={item.couple.name} />
            <TableRow>{item.results.pt}</TableRow>
            <TableRow>{item.results.pj}</TableRow>
            <TableRow>{item.results.pg}</TableRow>
            <TableRow>{item.results.pp}</TableRow>
            <TableRow>{item.results.sg}</TableRow>
            <TableRow>{item.results.sp}</TableRow>
            <TableRow>{item.results.jg}</TableRow>
            <TableRow>{item.results.jp}</TableRow>
          </tr>
        ))}
    </Table>
  );
}

export default TableClasificacion;
