import DropDownItem from "./DropDownItem.js";
import { Link } from 'react-router-dom';

function NavItem(props) {
  const items = () => {
    return <DropDownItem
      key={props.key}
      text={props.text}
      expand={props.expand}
      menu={props.menu}
      className="mt-2"
    />
  }
  return props.expand ? items() : (
    <Link 
      key={props.key}
      className="mt-2 inline-flex w-full justify-center gap-x-1.3 rounded-md bg-white px-3 py-2 text-sm text-gray-400 hover:text-black"
      to={props.link}
    >
      {props.text}
    </Link>
  );
}

export default NavItem;