function TableColumn(props) {
  return (
    <>
      {props.columns &&
        props.columns.map((item, index) => (
          <th key={index} className="p-2 whitespace-nowrap">
            <div className="font-semibold text-left">{item}</div>
          </th>
        ))}
    </>
  );
}

export default TableColumn;
