import NavBar from "../../components/NavBar/NavBar";
import Footer from "../../components/Footer/Footer";
import TitlePage from "../../components/TitlePage/TitlePage";
import TableColumn from "../../components/Table/TableColumn.js";
import TableRow from "../../components/Table/TableRow.js";
import Table from "../../components/Table/Table.js";
import ItemEdit from "../../components/Table/ItemEdit";
import ItemDelete from "../../components/Table/ItemDelete";
import AddButton from "../../components/Buttons/AddButton";
import Loading from "../../components/Loading/Loading";
import Alert from "../../components/PushMessage/Alert";
import CampusContactUpdate from "./CampusContactUpdate";
import { useState, useEffect } from "react";
import useUser from "../../hooks/auth/useUser";
import ModalAccept from "../../components/Modal/ModalAccept";
import { deleteCampusContact, getCampusesContacts, newCampusContact, updateCampusContact } from "../../api/campusesContacts";
import { getCampuses } from "../../api/campuses";

function CampusContact() {
  const [campuses, setCampuses] = useState(false);
  const [campusesContacts, setCampusesContacts] = useState(false);
  const [stateAlert, setStateAlert] = useState(false);
  const [stateLoading, setStateLoading] = useState(false);
  const [edit, setEdit] = useState(false);
  const [modalAccept, setModalAccept] = useState(false);
  const [userData, setUserData] = useUser();

  const Campuses = () => {
    setStateAlert(false);
    setStateLoading(true);

    getCampuses((json, error) => {
      setStateLoading(false);

      if (json) {
        if (json.code != "error") {
          setCampuses(json.data);
          //console.log(json.data);
        } else {
          setStateAlert({
            type: "war",
            message: json.message,
          });
        }
      } else {
        setStateAlert({
          type: "err",
          message: "Ocurrio un error!, " + error,
        });
      }
    });
  };

  const CampusContacts = () => {
    setStateAlert(false);
    setStateLoading(true);

    getCampusesContacts((json, error) => {
      setStateLoading(false);

      if (json) {
        if (json.code != "error") {
          setCampusesContacts(json.data);
          //console.log(json.data);
        } else {
          setStateAlert({
            type: "war",
            message: json.message,
          });
        }
      } else {
        setStateAlert({
          type: "err",
          message: "Ocurrio un error!, " + error,
        });
      }
    });
  };

  useEffect(() => {
    CampusContacts();
    Campuses();
  }, []);

  const addCampusContact = () => {
    setEdit({
      title: "Nuevo contacto de sede",
      data: { campuses },
      buttonText: "Crear contacto",
      onSubmit: (data) => {
        setStateLoading(true);
        newCampusContact(data, userData.token, (json, error) => {
          setStateLoading(false);
          if (json.code == "ok") {
            setStateAlert({
              type: "ok",
              message: "Contacto de sede creado con exito!",
            });
            setTimeout(() => CampusContacts(), 1000);
            setEdit(false);
          } else {
            setStateAlert({
              type: "err",
              message: json.message || error,
            });
          }
        });
      },
      onCancel: () => setEdit(false),
    });
  };

  const editCampusContact = (id) => {
    let data = campusesContacts.find((e) => e.id == id);
    data.campuses = campuses;
    setEdit({
      title: "Editar contacto de la sede",
      data: data,
      buttonText: "Guardar cambios",
      onSubmit: (dataUpdate) => {
        setStateLoading(true);
        updateCampusContact(dataUpdate, userData.token, (json, error) => {
          setStateLoading(false);
          if (json.code == "ok") {
            setStateAlert({
              type: "ok",
              message: "Contacto de sede actualizada con exito!",
            });
            setTimeout(() => CampusContacts(), 1000);
            setEdit(false);
          } else {
            setStateAlert({
              type: "err",
              message: json.message || error,
            });
          }
        });
      },
      onCancel: () => setEdit(false),
    });
  };

  const removeCampusContact = (id) => {
    setModalAccept({
      ok: () => {
        setStateLoading(true);
        setModalAccept(false);
        deleteCampusContact(id, userData.token, (json, error) => {
          setStateLoading(false);
          if (json.code == "ok") {
            setStateAlert({
              type: "ok",
              message: "Elemento eliminado con exito!",
            });
            setTimeout(() => CampusContacts(), 1000);
            setEdit(false);
          } else {
            setStateAlert({
              type: "err",
              message: json.message || error,
            });
          }
        });
      },
      cancel: () => setModalAccept(false),
    });
  };

  return (
    <>
      {stateLoading && <Loading />}
      {stateAlert && <Alert {...stateAlert} />}
      {edit && <CampusContactUpdate {...edit} />}
      {modalAccept && <ModalAccept {...modalAccept} />}
      <NavBar />
      <div className="pl-2 pr-2 block h-full text-center bg-gray-100 pt-10 pb-20">
        <TitlePage text="Admin - Contactos de las sedes" />
        <div className="flex justify-end items-end lg:pr-32 mb-6">
          <div className="w-max h-max" onClick={addCampusContact}>
            <AddButton />
          </div>
        </div>
        <Table
          title="Contactos de las sedes"
          columns={
            <TableColumn
              columns={[
                "Nombre",
                "Fecha de creación",
                "Fecha de actualización",
                "Acciones",
              ]}
            />
          }
          imageHeader={
            <svg
              xmlns="http://www.w3.org/2000/svg"
              fill="none"
              viewBox="0 0 24 24"
              strokeWidth={1.5}
              stroke="currentColor"
              className="w-6 h-6"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                d="M2.25 21h19.5m-18-18v18m10.5-18v18m6-13.5V21M6.75 6.75h.75m-.75 3h.75m-.75 3h.75m3-6h.75m-.75 3h.75m-.75 3h.75M6.75 21v-3.375c0-.621.504-1.125 1.125-1.125h2.25c.621 0 1.125.504 1.125 1.125V21M3 3h12m-.75 4.5H21m-3.75 3.75h.008v.008h-.008v-.008zm0 3h.008v.008h-.008v-.008zm0 3h.008v.008h-.008v-.008z"
              />
            </svg>
          }
        >
          {campusesContacts &&
            campusesContacts.map((item, index) => (
              <tr key={index}>
                <TableRow>
                  <div className="p-2 font-semibold text-gray-800 text-sm">
                    {item.name}
                  </div>
                </TableRow>
                <TableRow>
                  <div className="font-semibold text-blue-500 text-sm">
                    {new Date(item.created_at).toLocaleString()}
                  </div>
                </TableRow>
                <TableRow>
                  <div className="font-semibold text-red-500 text-sm">
                    {new Date(item.updated_at).toLocaleString()}
                  </div>
                </TableRow>
                <TableRow>
                  <div className="flex">
                    <ItemEdit id={item.id} onClick={editCampusContact} />
                    <ItemDelete id={item.id} onClick={removeCampusContact} />
                  </div>
                </TableRow>
              </tr>
            ))}
        </Table>
      </div>
      <Footer />
    </>
  );
}

export default CampusContact;