import Api from "./api.js";
import { get, post, put, del } from "./request.js";

//callback (json, error)

const callbacks = (callback) => {
  return {
    ready: (json) => {
      if (json) callback(json, null);
      else
        callback(
          null,
          " Posiblemente acurrio error del servidor!, intentelo mas tarde."
        );
    },
    error: (error) => callback(null, error),
  };
};

export function getCampuses(callback) {
  get(Api.getCampuses, {
    ...callbacks(callback),
  });
}

export function getCampusById(id, token, callback) {
  get(Api.getCampusById + id, {
    headers: {
      Authorization: `Bearer ${token}`,
      Accept: "application/json",
    },
    ...callbacks(callback),
  });
}

export function newCampus(data, token, callback) {
  post(Api.newCampus, {
    headers: {
      "Content-Type": "application/json",
      Authorization: `Bearer ${token}`,
      Accept: "application/json",
    },
    body: data,
    ...callbacks(callback),
  });
}

export function updateCampus(data, token, callback) {
  put(Api.updateCampus, {
    headers: {
      "Content-Type": "application/json",
      Authorization: `Bearer ${token}`,
      Accept: "application/json",
    },
    body: data,
    ...callbacks(callback),
  });
}

export function deleteCampus(id, token, callback) {
  del(Api.deleteCampus, {
    headers: {
      "Content-Type": "application/json",
      Authorization: `Bearer ${token}`,
      Accept: "application/json",
    },
    body: { campusId: id },
    ...callbacks(callback),
  });
}

export function uploadCampusCover(data, token, callback) {
  post(Api.uploadCampusCover, {
    format: "form-data",
    headers: {
      Authorization: `Bearer ${token}`,
      Accept: "application/json",
    },
    body: data,
   ...callbacks(callback),
  });
}