import TitlePage from "../../components/TitlePage/TitlePage";
import TextFieldLabel from "../../components/TextField/TextFieldLabel.js";
import SelectField from "../../components/SelectField/SelectField.js";
import TextArea from "../../components/TextField/TextArea";
import PrimaryButton from "../../components/Buttons/PrimaryButton.js";
import ModalAccept from "../../components/Modal/ModalAccept";
import Alert from "../../components/PushMessage/Alert";
import { useEffect, useState } from "react";
import { getCategories } from "../../api/categories";
import { getEditionById, updateMatchesDates } from "../../api/editions";
import Loading from "../../components/Loading/Loading";
import useUser from "../../hooks/auth/useUser";

function LigasFechas(props) {
  const [editionSelected, setEditionSelected] = useState(false);
  const [categories, setCategories] = useState(false);
  const [modalAccept, setModalAccept] = useState(false);
  const [stateAlert, setStateAlert] = useState(false);
  const [stateLoading, setStateLoading] = useState(false);
  const [userData, setUserData] = useUser();

  const Categories = () => {
    setStateLoading(true);
    getCategories((json, error) => {
      setStateLoading(false);
      if (json.code == "ok") {
        setCategories(json.data);
      } else {
        setStateAlert({
          type: "err",
          message: json.message || error,
        });
      }
    });
  };

  const EditionSelected = () => {
    setStateLoading(true);
    getEditionById(props.editionId, userData.token, (json, error) => {
      setStateLoading(false);
      if (json.code == "ok") {
        setEditionSelected(json.data?.name);
      } else {
        setStateAlert({
          type: "err",
          message: json.message || error,
        });
      }
    });
  };

  useEffect(() => {
    Categories();
    EditionSelected();
    //console.log(props.data);
  }, []);

  const submit = (form, id) => {
    form.preventDefault();
    const formValues = convertToNumbers(
      Object.fromEntries(new FormData(form.target).entries())
    );

    const data = props.data.find((e) => e.id === id);
    formValues.id = id;
    formValues.edition_id = data.edition_id;
    formValues.category_id = data.category_id;
    //formValues.start_date = "null";
    //formValues.end_date = "null";
    //formValues.league_phase_date = "null";
    //console.log(formValues);
    
    setModalAccept({
      ok: () => {
        setModalAccept(false);
        setStateLoading(true);
        updateMatchesDates(formValues, userData.token, (json, error) => {
          setStateLoading(false);
          if (json.code == "ok") {
            setStateAlert({
              type: "ok",
              message: "Fechas de la liga actualizadas con exito!",
            });
          } else {
            setStateAlert({
              type: "err",
              message: json.message || error,
            });
          }
        });
      },
      cancel: () => setModalAccept(false),
    });
  };

  const convertToNumbers = (formValues) => {
    const result = {};
    for (const [key, value] of Object.entries(formValues)) {
      result[key] = isNaN(value) ? value : Number(value);
    }
    return result;
  };

  return (
    <>
      {modalAccept && <ModalAccept {...modalAccept} />}
      {stateAlert && <Alert {...stateAlert} />}
      {stateLoading && <Loading {...stateLoading} />}
      <div className="align-center absolute pl-2 pr-2 w-full h-full text-center bg-gray-100 pb-20">
        <div className="flex justify-end items-end mt-4 mr-4">
          <svg
            xmlns="http://www.w3.org/2000/svg"
            fill="none"
            viewBox="0 0 24 24"
            strokeWidth={1.5}
            stroke="currentColor"
            className="w-6 h-6 cursor-pointer"
            onClick={props.onCancel}
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              d="M6 18L18 6M6 6l12 12"
            />
          </svg>
        </div>
        <TitlePage text={"Fechas de la liga: " + editionSelected} />
        <div className="w-full mt-6">
          {props.data &&
            props.data.map((item) => (
              <CategoryElement
                id={item.id}
                data={item}
                category={
                  categories &&
                  categories.find((e) => e.id == item.category_id).name
                }
                submit={submit}
              />
            ))}
        </div>
      </div>
    </>
  );
}

function CategoryElement(props) {
  return (
    <div className="flex justify-center">
      <div className="w-screen max-w-4xl mt-4">
        <div className="flex justify-center gap-2 bg-gray-700 text-white rounded-lg p-2 mr-2 ml-2">
          <svg
            xmlns="http://www.w3.org/2000/svg"
            fill="none"
            viewBox="0 0 24 24"
            strokeWidth={1.5}
            stroke="currentColor"
            className="w-6 h-6"
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              d="M9.568 3H5.25A2.25 2.25 0 003 5.25v4.318c0 .597.237 1.17.659 1.591l9.581 9.581c.699.699 1.78.872 2.607.33a18.095 18.095 0 005.223-5.223c.542-.827.369-1.908-.33-2.607L11.16 3.66A2.25 2.25 0 009.568 3z"
            />
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              d="M6 6h.008v.008H6V6z"
            />
          </svg>
          {props.category}
        </div>
        <div className="bg-white shadow-md rounded-md p-4 mt-6 ml-2 mr-2">
          <form onSubmit={(form) => props.submit(form, props.id)}>
            <div className="flex justify-center w-full p-2 pt-6">
              <div className="grid grid-cols-2 max-[600px]:grid-cols-1 gap-6 w-screen max-w-xl">
              <div className="shadow-lg rounded-md pt-4 p-6 border">
                <div className="w-full mt-2">
                  <TextFieldLabel
                    title="Fecha de inicio"
                    type="date"
                    name="start_date"
                    placeholder=""
                    text={props.data.start_date}
                    required
                    //noVoid
                  />
                </div>
                <div className="w-full mt-2">
                  <TextFieldLabel
                    title="Fecha de final"
                    type="date"
                    name="end_date"
                    placeholder=""
                    text={props.data.end_date}
                    required
                    //noVoid
                  />
                </div>
                <div className="w-full mt-2">
                  <TextFieldLabel
                    title="Fechas de la fase liguilla"
                    type="date"
                    name="league_phase_date"
                    placeholder=""
                    text={props.data.league_phase_date}
                    required
                    //noVoid
                  />
                </div>
                </div>
                <div className="shadow-lg rounded-md pt-4 p-6 border">
                  <span className="font-semibold">Treintaydosavos</span>
                  <div className="w-full mt-1">
                    <TextFieldLabel
                      title="Inicio"
                      type="date"
                      name="thirty_seconds_start_date"
                      placeholder=""
                      text={props.data.thirty_seconds_start_date}
                      required
                      //noVoid
                    />
                  </div>
                  <div className="w-full mt-2">
                    <TextFieldLabel
                      title="Final"
                      type="date"
                      name="thirty_seconds_end_date"
                      placeholder=""
                      text={props.data.thirty_seconds_end_date}
                      required
                      //noVoid
                    />
                  </div>
                </div>
                <div className="shadow-lg rounded-md pt-4 p-6 border">
                  <span className="font-semibold">Dieciseisavos</span>
                  <div className="w-full mt-1">
                    <TextFieldLabel
                      title="Inicio"
                      type="date"
                      name="sixteenth_start_date"
                      placeholder=""
                      text={props.data.sixteenth_start_date}
                      required
                      //noVoid
                    />
                  </div>
                  <div className="w-full mt-2">
                    <TextFieldLabel
                      title="Final"
                      type="date"
                      name="sixteenth_end_date"
                      placeholder=""
                      text={props.data.sixteenth_end_date}
                      required
                      //noVoid
                    />
                  </div>
                </div>
                <div className="shadow-lg rounded-md pt-4 p-6 border">
                  <span className="font-semibold">Octavos</span>
                  <div className="w-full mt-1">
                    <TextFieldLabel
                      title="Inicio"
                      type="date"
                      name="eighth_start_date"
                      placeholder=""
                      text={props.data.eighth_start_date}
                      required
                      //noVoid
                    />
                  </div>
                  <div className="w-full mt-2">
                    <TextFieldLabel
                      title="Final"
                      type="date"
                      name="eighth_end_date"
                      placeholder=""
                      text={props.data.eighth_end_date}
                      required
                      //noVoid
                    />
                  </div>
                </div>
                <div className="shadow-lg rounded-md pt-4 p-6 border">
                  <span className="font-semibold">Cuartos</span>
                  <div className="w-full mt-1">
                    <TextFieldLabel
                      title="Inicio"
                      type="date"
                      name="quarterfinals_start_date"
                      placeholder=""
                      text={props.data.quarterfinals_start_date}
                      required
                      //noVoid
                    />
                  </div>
                  <div className="w-full mt-2">
                    <TextFieldLabel
                      title="Final"
                      type="date"
                      name="quarterfinals_end_date"
                      placeholder=""
                      text={props.data.quarterfinals_end_date}
                      required
                      //noVoid
                    />
                  </div>
                </div>
                <div className="w-full mt-2">
                  <TextFieldLabel
                    title="Semifinales"
                    type="date"
                    name="semifinals_date"
                    placeholder=""
                    text={props.data.semifinals_date}
                    required
                    //noVoid
                  />
                </div>
                <div className="w-full mt-2">
                  <TextFieldLabel
                    title="Finales"
                    type="date"
                    name="finals_date"
                    placeholder=""
                    text={props.data.finals_date}
                    required
                    //noVoid
                  />
                </div>
              </div>
            </div>
            <div className="w-full mt-6 mb-4">
              <PrimaryButton
                type="submit"
                className="rounded-lg"
                text="Guardar cambios"
              />
            </div>
          </form>
        </div>
      </div>
    </div>
  );
}

export default LigasFechas;
