//Convierte endpoints en objeto con cada uno de ellos
//ejemplo {login: url + version + login} ...

const getApi = () => {
  return Object.fromEntries(
    Object.entries(api.endpoints).map(([key, value]) => [
      key,
      !value[0] ?
        api.url.replace("/api", "") + "/" + value[1] :
        api.url + api.version + value, //Urls normales
    ])
  );
};

export default getApi();
