import NavBar from "../../components/NavBar/NavBar";
import Footer from "../../components/Footer/Footer";
import TitlePage from "../../components/TitlePage/TitlePage";
import TableColumn from "../../components/Table/TableColumn.js";
import TableRow from "../../components/Table/TableRow.js";
import Table from "../../components/Table/Table.js";
import ItemEdit from "../../components/Table/ItemEdit";
import ItemDelete from "../../components/Table/ItemDelete";
import AddButton from "../../components/Buttons/AddButton";
import Loading from "../../components/Loading/Loading";
import Alert from "../../components/PushMessage/Alert";
import MunicipalitiesUpdate from "./MunicipalitiesUpdate";
import { useState, useEffect } from "react";
import { getProvinces } from "../../api/provinces";
import {
  getMunicipalities,
  updateMunicipality,
  newMunicipality,
  deleteMunicipality,
} from "../../api/municipalities";
import useUser from "../../hooks/auth/useUser";
import ModalAccept from "../../components/Modal/ModalAccept";

function Municipalities() {
  const [provinces, setProvinces] = useState(false);
  const [municipalities, setMunicipalities] = useState(false);
  const [stateAlert, setStateAlert] = useState(false);
  const [stateLoading, setStateLoading] = useState(false);
  const [edit, setEdit] = useState(false);
  const [modalAccept, setModalAccept] = useState(false);
  const [userData, setUserData] = useUser();

  const Provinces = () => {
    setStateAlert(false);
    setStateLoading(true);

    getProvinces((json, error) => {
      setStateLoading(false);

      if (json) {
        if (json.code != "error") {
          setProvinces(json.data);
          console.log(json);
        } else {
          setStateAlert({
            type: "war",
            message: json.message,
          });
        }
      } else {
        //Error login
        setStateAlert({
          type: "err",
          message: "Ocurrio un error!, " + error,
        });
      }
    });
  };

  const Municipalities = () => {
    setStateAlert(false);
    setStateLoading(true);

    getMunicipalities((json, error) => {
      setStateLoading(false);

      if (json) {
        if (json.code != "error") {
          setMunicipalities(json.data);
          //console.log(json);
        } else {
          setStateAlert({
            type: "war",
            message: json.message,
          });
        }
      } else {
        //Error login
        setStateAlert({
          type: "err",
          message: "Ocurrio un error!, " + error,
        });
      }
    });
  };

  useEffect(() => {
    Provinces();
    Municipalities();
  }, []);

  const addProvince = () => {
    setEdit({
      title: "Nuevo municipio",
      data: {
        provinces: provinces,
        codeKey: municipalities
          ? municipalities[municipalities.length - 1].code
          : 0,
      },
      buttonText: "Crear municipio",
      onSubmit: (data) => {
        setStateLoading(true);
        newMunicipality(data, userData.token, (json, error) => {
          setStateLoading(false);
          if (json.code == "ok") {
            setStateAlert({
              type: "ok",
              message: "Municipio creada con exito!",
            });
            setTimeout(() => Municipalities(), 1000);
            setEdit(false);
          } else {
            setStateAlert({
              type: "err",
              message: json.message || error,
            });
          }
        });
      },
      onCancel: () => setEdit(false),
    });
  };

  const editMunicipality = (id) => {
    let data = municipalities.find((e) => e.id == id);
    data.provinces = provinces;
    setEdit({
      title: "Editar municipio",
      data: data,
      buttonText: "Guardar cambios",
      onSubmit: (dataUpdate) => {
        setStateLoading(true);
        updateMunicipality(dataUpdate, userData.token, (json, error) => {
          setStateLoading(false);
          if (json.code == "ok") {
            setStateAlert({
              type: "ok",
              message: "Municipios actualizada con exito!",
            });
            setTimeout(() => Municipalities(), 1000);
            setEdit(false);
          } else {
            setStateAlert({
              type: "err",
              message: json.message || error,
            });
          }
        });
      },
      onCancel: () => setEdit(false),
    });
  };

  const removeMunicipality = (id) => {
    setModalAccept({
      ok: () => {
        setStateLoading(true);
        setModalAccept(false);
        deleteMunicipality(id, userData.token, (json, error) => {
          setStateLoading(false);
          if (json.code == "ok") {
            setStateAlert({
              type: "ok",
              message: "Elemento eliminado con exito!",
            });
            setTimeout(() => Municipalities(), 1000);
            setEdit(false);
          } else {
            setStateAlert({
              type: "err",
              message: json.message || error,
            });
          }
        });
      },
      cancel: () => setModalAccept(false),
    });
  };

  const getProvinceName = (code) => {
    if (provinces) return provinces.find((e) => e.id == code).name;
    return "";
  };

  return (
    <>
      {stateLoading && <Loading />}
      {stateAlert && <Alert {...stateAlert} />}
      {edit && <MunicipalitiesUpdate {...edit} />}
      {modalAccept && <ModalAccept {...modalAccept} />}
      <NavBar />
      <div className="pl-2 pr-2 block h-full text-center bg-gray-100 pt-10 pb-20">
        <TitlePage text="Admin - Municipios" />
        <div className="flex justify-end items-end lg:pr-32 mb-6">
          <div className="w-max h-max" onClick={addProvince}>
            <AddButton />
          </div>
        </div>
        <Table
          title="Municipios"
          columns={
            <TableColumn
              columns={[
                "Codigo",
                "Provincia",
                "Nombre",
                "Fecha de creación",
                "Fecha de actualización",
                "Acciones",
              ]}
            />
          }
          imageHeader={
            <svg
              xmlns="http://www.w3.org/2000/svg"
              fill="none"
              viewBox="0 0 24 24"
              strokeWidth={1.5}
              stroke="currentColor"
              className="w-6 h-6"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                d="M2.25 21h19.5m-18-18v18m10.5-18v18m6-13.5V21M6.75 6.75h.75m-.75 3h.75m-.75 3h.75m3-6h.75m-.75 3h.75m-.75 3h.75M6.75 21v-3.375c0-.621.504-1.125 1.125-1.125h2.25c.621 0 1.125.504 1.125 1.125V21M3 3h12m-.75 4.5H21m-3.75 3.75h.008v.008h-.008v-.008zm0 3h.008v.008h-.008v-.008zm0 3h.008v.008h-.008v-.008z"
              />
            </svg>
          }
        >
          {municipalities &&
            municipalities.map((item, index) => (
              <tr key={index}>
                <TableRow>
                  <div className="p-3 font-semibold text-gray-800 text-sm">
                    {item.code}
                  </div>
                </TableRow>
                <TableRow>
                  <div className="font-semibold text-gray-800 text-sm">
                    {getProvinceName(item.province_id)}
                  </div>
                </TableRow>
                <TableRow>
                  <div className="text-gray-800 text-sm">{item.name}</div>
                </TableRow>
                <TableRow>
                  <div className="font-semibold text-blue-500 text-sm">
                    {new Date(item.created_at).toLocaleString()}
                  </div>
                </TableRow>
                <TableRow>
                  <div className="font-semibold text-red-500 text-sm">
                    {new Date(item.updated_at).toLocaleString()}
                  </div>
                </TableRow>
                <TableRow>
                  <div className="flex">
                    <ItemEdit id={item.id} onClick={editMunicipality} />
                    <ItemDelete id={item.id} onClick={removeMunicipality} />
                  </div>
                </TableRow>
              </tr>
            ))}
        </Table>
      </div>
      <Footer />
    </>
  );
}

export default Municipalities;
