import NavBar from "../../components/NavBar/NavBar.js";
import Footer from "../../components/Footer/Footer.js";
import { Dialog, Transition } from "@headlessui/react";
import React, { Fragment, useEffect, useRef, useState } from "react";
import { useForm } from "react-hook-form";
import api from "../../api/api.js";
import useUser from "../../hooks/auth/useUser.js";
import Loading from "../../components/Loading/Loading.js";
import EmptyInput from "./EmptyInput.js";
import LoadedImage from "./LoadedImage.js";
import TitlePage from "../../components/TitlePage/TitlePage.js";
import AddButton from "../../components/Buttons/AddButton.js";
import TableColumn from "../../components/Table/TableColumn.js";
import TableRow from "../../components/Table/TableRow.js";
import Table from "../../components/Table/Table.js";
import ItemEdit from "../../components/Table/ItemEdit";
import ItemDelete from "../../components/Table/ItemDelete";
import Alert from "../../components/PushMessage/Alert.js";
import ModalAccept from "../../components/Modal/ModalAccept.js";
import {
  getGalleries,
  newGallery,
  updateGallery,
  deleteGallery,
} from "../../api/gallery.js";

function Galleries() {
  useEffect(() => window.scrollTo(0, 0), []);
  const [galleries, setGalleries] = useState([]);
  const [idGallery, setIdGallery] = useState();
  const cancelButtonRef = useRef(null);
  const [userData, setUserData] = useUser();
  const [stateLoading, setStateLoading] = useState(false);
  const inputRef = useRef(null);
  const { createAction, updateAction } = { createAction: 1, updateAction: 2 };
  const [action, setAction] = useState(createAction);
  const [actionImage, setActionImage] = useState(false);
  const [urlImage, setUrlImage] = useState(undefined);
  const [modalAccept, setModalAccept] = useState(false);
  const [stateAlert, setStateAlert] = useState(false);

  useEffect(() => {
    Gallery();
  }, []);

  const Gallery = () => {
    //setStateLoading(true);
    //let json = await apiGalleries.getAllGalleries();
    //if (json.code === "ok") {
    //  setGalleries(json.data);
    //}
    //setStateLoading(false);
    setStateAlert(false);
    setStateLoading(true);

    getGalleries((json, error) => {
      setStateLoading(false);

      if (json) {
        if (json.code == "ok") {
          setGalleries(json.data);
        } else {
          setStateAlert({
            type: "war",
            message: json.message,
          });
        }
      } else {
        setStateAlert({
          type: "err",
          message: "Ocurrio un error!, " + error,
        });
      }
    });
  };

  const addGallery = () => {
    let data = new FormData();
    data.append("img", inputRef.current.files[0]);

    setStateAlert(false);
    setStateLoading(true);
    newGallery(data, userData.token, (json, error) => {
      setStateLoading(false);
      if (json?.code == "ok") {
        setStateAlert({
          type: "ok",
          message: "Imagen creada con exito!",
        });
        setTimeout(() => Gallery(), 1000);
      } else {
        setStateAlert({
          type: "err",
          message: json.message || error,
        });
      }
    });
  };

  const editGallery = () => {
    let data = new FormData();
    data.append("galleryId", idGallery);
    data.append("img", inputRef.current.files[0]);

    setStateAlert(false);
    setStateLoading(true);
    updateGallery(data, userData.token, (json, error) => {
      setStateLoading(false);
      if (json?.code == "ok") {
        setStateAlert({
          type: "ok",
          message: "Imagen creada con exito!",
        });
        setTimeout(() => Gallery(), 1000);
      } else {
        setStateAlert({
          type: "err",
          message: json.message || error,
        });
      }
    });
  };

  const delGallery = (idGallery) => {
    setStateAlert(false);
    setModalAccept({
      ok: () => {
        setModalAccept(false);
        setStateAlert(false);
        setStateLoading(true);
        deleteGallery(idGallery, userData.token, (json, error) => {
          setStateLoading(false);
          if (json?.code == "ok") {
            setStateAlert({
              type: "ok",
              message: "Eliminado con exito!",
            });
            setTimeout(() => Gallery(), 1000);
          } else {
            setStateAlert({
              type: "err",
              message: json.message || error,
            });
          }
        });
      },
      cancel: () => setModalAccept(false),
    });
  };

  const {
    register,
    handleSubmit,
    watch,
    setValue,
    resetField,
    reset,
    formState: { errors },
  } = useForm({});

  const handleClickImage = () => {
    inputRef.current.click();
  };

  const handleChangeImage = (e) => {
    let url = URL.createObjectURL(e.target.files[0]);
    setUrlImage(url);
  };

  return (
    <>
      <NavBar />
      {modalAccept && <ModalAccept {...modalAccept} />}
      {stateAlert && <Alert {...stateAlert} />}
      {stateLoading && <Loading />}
      <div className="pl-2 pr-2 block h-full text-center bg-gray-100 pt-10 pb-20">
        <TitlePage text="Admin - Galería" />
        <div className="flex justify-end items-end lg:pr-32 mb-6">
          <div
            className="w-max h-max"
            onClick={() => {
              setActionImage(true);
              setUrlImage(undefined);
              setAction(createAction);
            }}
          >
            <AddButton />
          </div>
        </div>
        <Table
          title="Galería"
          columns={
            <TableColumn
              columns={[
                "Imagen",
                "Fecha de creación",
                "Fecha de actualización",
                "Acciones",
              ]}
            />
          }
          imageHeader={
            <svg
              xmlns="http://www.w3.org/2000/svg"
              fill="none"
              viewBox="0 0 24 24"
              strokeWidth={1.5}
              stroke="currentColor"
              className="w-6 h-6"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                d="M2.25 15.75l5.159-5.159a2.25 2.25 0 013.182 0l5.159 5.159m-1.5-1.5l1.409-1.409a2.25 2.25 0 013.182 0l2.909 2.909m-18 3.75h16.5a1.5 1.5 0 001.5-1.5V6a1.5 1.5 0 00-1.5-1.5H3.75A1.5 1.5 0 002.25 6v12a1.5 1.5 0 001.5 1.5zm10.5-11.25h.008v.008h-.008V8.25zm.375 0a.375.375 0 11-.75 0 .375.375 0 01.75 0z"
              />
            </svg>
          }
        >
          {galleries &&
            galleries.map((item, index) => (
              <tr key={index}>
                <TableRow>
                  <div className="rounded-lg bg-gray-200 w-14 h-14">
                    <img
                      className="w-14 h-14 rounded-lg"
                      src={api.resources + item.img}
                      alt=" "
                      style={{ objectFit: "cover" }}
                    />
                  </div>
                </TableRow>
                <TableRow>
                  <div className="font-semibold text-blue-500 text-sm">
                    {new Date(item.created_at).toLocaleString()}
                  </div>
                </TableRow>
                <TableRow>
                  <div className="font-semibold text-red-500 text-sm">
                    {new Date(item.updated_at).toLocaleString()}
                  </div>
                </TableRow>
                <TableRow>
                  <div className="flex">
                    <ItemEdit
                      id={item.id}
                      onClick={(id) => {
                        setActionImage(true);
                        setUrlImage(api.resources + item.img);
                        setIdGallery(id);
                        setAction(updateAction);
                      }}
                    />
                    <ItemDelete
                      id={item.id}
                      onClick={(id) => {
                        setIdGallery(id);
                        delGallery(id);
                      }}
                    />
                  </div>
                </TableRow>
              </tr>
            ))}
        </Table>
      </div>
      <Footer />

      <Transition.Root show={actionImage} as={Fragment}>
        <Dialog
          as="div"
          className="relative z-10"
          initialFocus={cancelButtonRef}
          onClose={setActionImage}
        >
          <Transition.Child
            as={Fragment}
            enter="ease-out duration-300"
            enterFrom="opacity-0"
            enterTo="opacity-100"
            leave="ease-in duration-200"
            leaveFrom="opacity-100"
            leaveTo="opacity-0"
          >
            <div className="fixed inset-0 bg-gray-500 bg-opacity-75 transition-opacity" />
          </Transition.Child>

          <div className="fixed inset-0 z-10 overflow-y-auto">
            <div className="flex min-h-full items-end justify-center p-4 text-center sm:items-center sm:p-0">
              <Transition.Child
                as={Fragment}
                enter="ease-out duration-300"
                enterFrom="opacity-0 translate-y-4 sm:translate-y-0 sm:scale-95"
                enterTo="opacity-100 translate-y-0 sm:scale-100"
                leave="ease-in duration-200"
                leaveFrom="opacity-100 translate-y-0 sm:scale-100"
                leaveTo="opacity-0 translate-y-4 sm:translate-y-0 sm:scale-95"
              >
                <Dialog.Panel className="relative bg-white transform overflow-hidden rounded-lg px-4 pt-5 pb-4 text-left shadow-xl transition-all sm:my-8 sm:w-full sm:max-w-lg sm:p-6">
                  <div className="p-6">
                    <h3 className="mb-5 text-center">Agregar galería</h3>
                    <form
                      onSubmit={handleSubmit(
                        action == createAction ? addGallery : editGallery
                      )}
                    >
                      <div className="flex flex-col w-full mt-4">
                        <label className="base-body-Work-Sans">Imagen</label>
                        <div
                          className="mt-1 w-full cursor-pointer flex justify-center rounded-md border-2 border-dashed border-gray-300 px-6 pt-5 pb-6"
                          onClick={handleClickImage}
                        >
                          {!urlImage ? (
                            <EmptyInput />
                          ) : (
                            <LoadedImage
                              urlImage={urlImage}
                              setUrl={setUrlImage}
                            />
                          )}
                        </div>
                      </div>
                      <div className="flex justify-center items-center mt-4">
                        <input
                          type="file"
                          className="hidden"
                          ref={inputRef}
                          onChange={handleChangeImage}
                        />
                        <button
                          onClick={() => {
                            setActionImage(false);
                          }}
                          className="bg-blue-500 hover:bg-blue-600 text-white font-bold py-2 px-4 rounded-full"
                        >
                          Guardar
                        </button>
                      </div>
                    </form>
                  </div>
                </Dialog.Panel>
              </Transition.Child>
            </div>
          </div>
        </Dialog>
      </Transition.Root>
    </>
  );
}

export default Galleries;
