import Api from "./api.js";
import { get, post, put, del } from "./request.js";

//callback (json, error)

const callbacks = (callback) => {
  return {
    ready: (json) => {
      if (json) callback(json, null);
      else
        callback(
          null,
          " Posiblemente acurrio error del servidor!, intentelo mas tarde."
        );
    },
    error: (error) => callback(null, error),
  };
};

export function getUsers(token, callback) {
  get(Api.getUsers, {
    headers: {
      'Content-Type': "application/json",
      Authorization: `Bearer ${token}`,
      Accept: "application/json",
    },
    ...callbacks(callback),
  });
}

export function unsuscribe(id, token, callback) {
  post(Api.unsuscribe, {
    headers: {
      'Content-Type': "application/json",
      Authorization: `Bearer ${token}`,
      Accept: "application/json",
    },
    body: { id },
    ...callbacks(callback),
  });
}

export function updateUser(id, data, token, callback) {
  put(Api.updateUser + id, {
    headers: {
      'Content-Type': "application/json",
      Authorization: `Bearer ${token}`,
      Accept: "application/json",
    },
    body: data,
    ...callbacks(callback),
  });
}

export function search(data, token, callback) {
  post(Api.search, {
    headers: {
      'Content-Type': "application/json",
      Authorization: `Bearer ${token}`,
      Accept: "application/json",
    },
    body: data,
    ...callbacks(callback),
  });
}