import React from "react";
import CheckboxCard from "./CheckboxCard";
import '../../../App.css'
import EmptySection from "../../../components/EmptySection";


const Campus = ({ favoritesCampuses, setFavoriteCampuses, campuses }) => {

    return <div className="grid-gallery">
        {
            campuses.length ? campuses.map((el) =>
                <CheckboxCard src={el.cover} key={el.id} id={el.id} title={el.name} subtitle={el.observations} data={favoritesCampuses} setData={setFavoriteCampuses} limit={3} />)
                : <EmptySection text={'No hay sedes disponibles actualmente'} />
        }

    </div>
}

export default Campus;