import TitlePage from "../../components/TitlePage/TitlePage";
import TextFieldLabel from "../../components/TextField/TextFieldLabel.js";
import TextArea from "../../components/TextField/TextArea";
import PrimaryButton from "../../components/Buttons/PrimaryButton.js";
import ModalAccept from "../../components/Modal/ModalAccept";
import SelectField from "../../components/SelectField/SelectField";
import { useState } from "react";

function CampusContactUpdate(props) {
  const [modalAccept, setModalAccept] = useState(false);

  const submit = (form) => {
    form.preventDefault();
    const formValues = Object.fromEntries(new FormData(form.target).entries());
    for (let propiedad in formValues) {
      if (formValues[propiedad] === "") {
        delete formValues[propiedad];
      }
    }
    formValues.campusContactId = props?.data?.id;
    //delete formValues.charge;
    setModalAccept({
      ok: () => {
        setModalAccept(false);
        props.onSubmit(formValues);
      },
      cancel: () => setModalAccept(false),
    });
  };

  return (
    <>
      {modalAccept && <ModalAccept {...modalAccept} />}
      <div className="align-center absolute pl-2 pr-2 w-full h-screen text-center bg-gray-100 pb-20">
        <div className="flex justify-end items-end mt-4 mr-4">
          <svg
            xmlns="http://www.w3.org/2000/svg"
            fill="none"
            viewBox="0 0 24 24"
            strokeWidth={1.5}
            stroke="currentColor"
            className="w-6 h-6 cursor-pointer"
            onClick={props.onCancel}
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              d="M6 18L18 6M6 6l12 12"
            />
          </svg>
        </div>
        <TitlePage text={props.title} />
        <form onSubmit={submit} className="flex justify-center">
          <div>
            <div className="mt-4 p-4 grid grid-cols-2 max-[600px]:grid-cols-1 gap-6 w-screen max-w-xl">
              <div>
                <TextFieldLabel
                  title="Nombre"
                  type="text"
                  name="name"
                  placeholder="Nombre del contacto de la sede"
                  text={props.data?.name}
                  required={!props.data}
                  noVoid
                />
              </div>
              <div>
                <TextFieldLabel
                  title="Cargar"
                  type="text"
                  name="charge"
                  placeholder="Cargar del contacto de la sede"
                  text={props.data?.charge}
                  required={!props.data}
                  noVoid
                />
              </div>
              <div>
                <TextFieldLabel
                  title="Teléfono"
                  type="number"
                  name="phone"
                  placeholder="Teléfono del contacto de la sede"
                  text={props.data?.phone}
                  required={!props.data}
                  noVoid
                />
              </div>
              <div>
                <TextFieldLabel
                  title="Correo electrónico"
                  type="email"
                  name="email"
                  placeholder="Correo electrónico"
                  text={props.data?.email}
                  required={!props.data}
                  noVoid
                />
              </div>
              <div>
                <TextFieldLabel
                  title="Fecha baja"
                  type="date"
                  name="lowDate"
                  placeholder="Fecha baja"
                  text={props.data?.low_date}
                  required={!props.data}
                  noVoid
                />
              </div>
              <div>
                <SelectField
                  title="Sedes"
                  name="campusId"
                  default="Seleccione la sede"
                  body={props?.data?.campuses.map((item) => ({
                    value: item.id,
                    label: item.name,
                    selected: item.id === props?.data?.campus_id,
                  }))}
                  required
                  noVoid
                />
              </div>
              <div>
                <TextArea
                  name="observations"
                  rows={4}
                  placeholder="Observaciones..."
                  text={props.data?.observations}
                  required={!props.data}
                  noVoid
                />
              </div>
            </div>
            <div className="flex justify-center w-full mt-8">
              <PrimaryButton
                type="submit"
                className="rounded-lg"
                text={props.buttonText}
              />
            </div>
          </div>
        </form>
      </div>
    </>
  );
}

export default CampusContactUpdate;
