import TitlePage from "../../components/TitlePage/TitlePage";
import ModalAccept from "../../components/Modal/ModalAccept";
import { useEffect, useState } from "react";
import { assignCoupleToGroup, deleteGroup, getGroups, newGroup } from "../../api/groups";
import useUser from "../../hooks/auth/useUser";
import Alert from "../../components/PushMessage/Alert";
import Loading from "../../components/Loading/Loading";
import AddButton from "../../components/Buttons/AddButton";
import api from "../../api/api";
import ItemEdit from "../../components/Table/ItemEdit";
import { getCouplesByEditionId } from "../../api/enrollments";
import NewGroup from "./NewGroup";
import EditGroup from "./EditGroup";

function GroupsUpdate(props) {
  const [groups, setGroups] = useState(false);
  const [couples, setCouples] = useState(false);
  const [stateNewGroup, setStateNewGroup] = useState(false);
  const [stateEditGroup, setStateEditGroup] = useState(false);
  const [stateAlert, setStateAlert] = useState(false);
  const [stateLoading, setStateLoading] = useState(false);
  const [modalAccept, setModalAccept] = useState(false);
  const [userData, setUserData] = useUser();

  const Group = () => {
    setStateAlert(false);
    setStateLoading(true);
    getGroups(
      props.data.edition_id,
      props.data.category_id,
      userData.token,
      (json, error) => {
        setStateLoading(false);
        if (json?.code == "ok") {
          setGroups(json.data);
          //console.log("Groups: ", json.data);
        } else {
          setStateAlert({
            type: "err",
            message: json.message || error,
          });
        }
      }
    );
  };

  const Couples = () => {
    setStateAlert(false);
    setStateLoading(true);
    getCouplesByEditionId(
      props.data.edition_id,
      userData.token,
      (json, error) => {
        setStateLoading(false);
        if (json?.code == "ok") {
          setCouples(json.data);
          //console.log("Couples: ", json.data);
        } else {
          setStateAlert({
            type: "err",
            message: json.message || error,
          });
        }
      }
    );
  };

  useEffect(() => {
    Group();
    Couples();
  }, []);

  const addGroup = () => {
    setStateAlert(false);
    const data = {
      editionId: props?.data?.edition_id,
      categoryId: props?.data?.category_id,
      name: "Grupo " + (groups.length + 1),
    };
    setStateNewGroup({
      data: data,
      onSubmit: (dataNew) => {
        setModalAccept({
          ok: () => {
            setStateLoading(true);
            setModalAccept(false);
            newGroup(dataNew, userData.token, (json, error) => {
              setStateLoading(false);
              if (json?.code == "ok") {
                setStateAlert({
                  type: "ok",
                  message: "Grupo creado con exito!",
                });
                setTimeout(() => Group(), 1000);
                setStateNewGroup(false);
              } else {
                setStateAlert({
                  type: "err",
                  message: json.message || error,
                });
              }
            });
          },
          cancel: () => setModalAccept(false),
        });
      },
      onCancel: () => setStateNewGroup(false),
    });
  };

  const editGroup = (item) => {
    setStateAlert(false);
    setStateEditGroup({
      data: {
        groupId: item.group_id,
        couple: item.couple,
        couples_all: couples,
      },
      onSubmit: (data) => {
        setModalAccept({
          ok: () => {
            setStateLoading(true);
            setModalAccept(false);
            assignCoupleToGroup(data, userData.token, (json, error) => {
              setStateLoading(false);
              if (json?.code == "ok") {
                setStateAlert({
                  type: "ok",
                  message: "Pareja asignada con exito!",
                });
                setTimeout(() => Group(), 1000);
                setTimeout(() => Couples(), 1000);
                setStateEditGroup(false);
              } else {
                setStateAlert({
                  type: "err",
                  message: json.message || error,
                });
              }
            });
          },
          cancel: () => setModalAccept(false),
        });
      },
      onCancel: () => setStateEditGroup(false),
    });
  };

  const delGroup = (id) => {
    setStateAlert(false);
    setModalAccept({
      ok: () => {
        setStateLoading(true);
        setModalAccept(false);
        deleteGroup(id, userData.token, (json, error) => {
          setStateLoading(false);
          if (json?.code == "ok") {
            setStateAlert({
              type: "ok",
              message: "Grupo eliminado con exito!",
            });
            setTimeout(() => Group(), 1000);
          } else {
            setStateAlert({
              type: "err",
              message: json.message || error,
            });
          }
        });
      },
      cancel: () => setModalAccept(false),
    });
  };

  return (
    <>
      {stateNewGroup && <NewGroup {...stateNewGroup} />}
      {stateEditGroup && <EditGroup {...stateEditGroup} />}
      {stateLoading && <Loading {...stateLoading} />}
      {stateAlert && <Alert {...stateAlert} />}
      {modalAccept && <ModalAccept {...modalAccept} />}
      <div className="align-center absolute pl-2 pr-2 w-full text-center bg-gray-100 pb-20">
        <div className="flex justify-end items-end mt-4 mr-4">
          <svg
            xmlns="http://www.w3.org/2000/svg"
            fill="none"
            viewBox="0 0 24 24"
            strokeWidth={1.5}
            stroke="currentColor"
            className="w-6 h-6 cursor-pointer"
            onClick={props.onCancel}
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              d="M6 18L18 6M6 6l12 12"
            />
          </svg>
        </div>
        <TitlePage text={"Editar grupos de la liga: " + props.title} />
        <div>
          <div className="mt-10 flex justify-center">
            <div className="w-max h-max" onClick={addGroup}>
              <AddButton />
            </div>
          </div>
          <div className="h-full m-2 mb-10 mt-10 flex flex-wrap justify-center items-center gap-6">
            {groups &&
              groups.map((group, index) => (
                <GroupElement
                  key={index}
                  delGroup={() => delGroup(group.id)}
                  editGroup={editGroup}
                  {...group}
                />
              ))}
          </div>
        </div>
      </div>
    </>
  );
}

function GroupElement(props) {
  return (
    <div className="max-w-[16em] Xmax-h-xs w-full min-h-[18em] md:w-1/2 lg:w-1/3 rounded-md shadow">
      <div className="flex justify-end items-end">
        <div
          onClick={props.delGroup}
          className="cursor-pointer -mb-4 -mr-3 text-white p-1.5 absolute bg-slate-500 rounded-full"
        >
          <svg
            xmlns="http://www.w3.org/2000/svg"
            fill="none"
            viewBox="0 0 24 24"
            strokeWidth={1.5}
            stroke="currentColor"
            className="w-5 h-5"
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              d="M6 18L18 6M6 6l12 12"
            />
          </svg>
        </div>
      </div>
      <div className="font-semibold rounded-t-md w-full text-white bg-gray-600 flex justify-center gap-2">
        <div className="flex p-2">
          <svg
            xmlns="http://www.w3.org/2000/svg"
            fill="none"
            viewBox="0 0 24 24"
            strokeWidth={1.5}
            stroke="currentColor"
            className="w-6 h-6"
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              d="M18 18.72a9.094 9.094 0 003.741-.479 3 3 0 00-4.682-2.72m.94 3.198l.001.031c0 .225-.012.447-.037.666A11.944 11.944 0 0112 21c-2.17 0-4.207-.576-5.963-1.584A6.062 6.062 0 016 18.719m12 0a5.971 5.971 0 00-.941-3.197m0 0A5.995 5.995 0 0012 12.75a5.995 5.995 0 00-5.058 2.772m0 0a3 3 0 00-4.681 2.72 8.986 8.986 0 003.74.477m.94-3.197a5.971 5.971 0 00-.94 3.197M15 6.75a3 3 0 11-6 0 3 3 0 016 0zm6 3a2.25 2.25 0 11-4.5 0 2.25 2.25 0 014.5 0zm-13.5 0a2.25 2.25 0 11-4.5 0 2.25 2.25 0 014.5 0z"
            />
          </svg>
          <label className="pl-2">{props.name}</label>
        </div>
      </div>
      <div className="p-2">
        <div className="inline">
          {props?.group_has_couples?.map((item) => (
            <div className="flex p-1.5 items-center">
              <div className="w-10 h-10 flex-shrink-0 mr-2 sm:mr-3">
                {item.couple.avatar ? (
                  <img
                    className="w-full h-full rounded-full"
                    src={api.resources + item.couple.avatar}
                    width="40"
                    height="40"
                    alt=" "
                    style={{ objectFit: "cover" }}
                  />
                ) : (
                  <div className="rounded-full bg-gray-200 w-10 h-10"></div>
                )}
              </div>
              <div className="text-gray-800">{item.couple.name}</div>
              <div className="text-gray-600 pl-3">
                <ItemEdit
                  id={item.couple.id}
                  onClick={() => props.editGroup(item)}
                />
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
}

export default GroupsUpdate;
