import React from "react";

const LoadedImage = ({ urlImage, setUrl }) => {
  const handleClick = (e) => {
    e.stopPropagation();
    setUrl(undefined);
  };

  return (
    <div className="relative">
      <img
        src={urlImage}
        className="w-full hover:opacity-80 rounded-xl shadow-lg h-48 object-cover"
      />
      <svg
        onClick={handleClick}
        xmlns="http://www.w3.org/2000/svg"
        fill="none"
        viewBox="0 0 24 24"
        strokeWidth={1.5}
        stroke="currentColor"
        className="w-10 h-10 absolute top-3 bg-white rounded-full p-2 shadow-2xl hover:bg-red-200 text-gray-800 right-3"
      >
        <path
          strokeLinecap="round"
          strokeLinejoin="round"
          d="M20.25 7.5l-.625 10.632a2.25 2.25 0 01-2.247 2.118H6.622a2.25 2.25 0 01-2.247-2.118L3.75 7.5m8.25 3v6.75m0 0l-3-3m3 3l3-3M3.375 7.5h17.25c.621 0 1.125-.504 1.125-1.125v-1.5c0-.621-.504-1.125-1.125-1.125H3.375c-.621 0-1.125.504-1.125 1.125v1.5c0 .621.504 1.125 1.125 1.125z"
        />
      </svg>
    </div>
  );
};

export default LoadedImage;
