import TableRow from "./TableRow.js";

function ItemResults(props) {
  const isMax = (set) => {
    const s = [parseInt(props.set1), parseInt(props.set2)];
    if (!set && s[0] > s[1]) return "bg-blue-400";
    if (set && s[0] < s[1]) return "bg-blue-400";
    return "bg-gray-600";
  };
  return (
    <TableRow>
      <div className="block">
        <div className="flex text-white">
          <div
            className={
              "mr-0.5 pl-3 pr-3 rounded-l-full text-lg text-bold " +
              isMax(false)
            }
          >
            {props.set1}
          </div>
          <div
            className={
              "pl-3 pr-3 rounded-r-full text-lg text-bold " + isMax(true)
            }
          >
            {props.set2}
          </div>
        </div>
        <div className="text-semibold">{props.other}</div>
      </div>
    </TableRow>
  );
}

export default ItemResults;
