import Api from "./api.js";
import { get, post, put, del } from "./request.js";

//callback (json, error)

const callbacks = (callback) => {
  return {
    ready: (json) => {
      if (json) callback(json, null);
      else
        callback(
          null,
          " Posiblemente acurrio error del servidor!, intentelo mas tarde."
        );
    },
    error: (error) => callback(null, error),
  };
};

export function getGenders(callback) {
  get(Api.getGenders, {
    ...callbacks(callback),
  });
}

export function getGenderById(id, token, callback) {
  get(Api.getGenderById + id, {
    headers: {
      Authorization: `Bearer ${token}`,
      Accept: "application/json",
    },
    ...callbacks(callback),
  });
}

export function newGender(data, token, callback) {
  post(Api.newGender, {
    headers: {
      Authorization: `Bearer ${token}`,
      Accept: "application/json",
    },
    body: data,
    ...callbacks(callback),
  });
}

export function updateGender(data, token, callback) {
  put(Api.updateGender, {
    headers: {
      Authorization: `Bearer ${token}`,
      Accept: "application/json",
    },
    body: data,
    ...callbacks(callback),
  });
}

export function deleteGender(id, token, callback) {
  del(Api.deleteGender, {
    headers: {
      Authorization: `Bearer ${token}`,
      Accept: "application/json",
    },
    body: { id },
    ...callbacks(callback),
  });
}
