import TitlePage from "../../components/TitlePage/TitlePage";
import TextFieldLabel from "../../components/TextField/TextFieldLabel.js";
import PrimaryButton from "../../components/Buttons/PrimaryButton.js";
import ModalAccept from "../../components/Modal/ModalAccept";
import { useState } from "react";
import TextArea from "../../components/TextField/TextArea";
import CheckField from "../../components/CheckField/CheckField";
import api from "../../api/api";

function LevelsUpdate(props) {
  const [modalAccept, setModalAccept] = useState(false);
  const [imgCover, setImgCover] = useState(false);

  const imgChange = (e) => {
    const file = e.target.files[0];
    const reader = new FileReader();

    reader.onload = () => setImgCover(reader.result);

    if (file) reader.readAsDataURL(file);
  };

  const submit = (form) => {
    form.preventDefault();
    const formValues = convertToNumbers(
      Object.fromEntries(new FormData(form.target).entries())
    );
    formValues.newsId = props?.data?.notice?.id;
    formValues.status = formValues.status == "on" ? 1 : 0;
    setModalAccept({
      ok: () => {
        setModalAccept(false);
        props.onSubmit(formValues);
      },
      cancel: () => setModalAccept(false),
    });
  };

  const convertToNumbers = (formValues) => {
    const result = {};
    for (const [key, value] of Object.entries(formValues)) {
      result[key] = isNaN(value) ? value : Number(value);
    }
    return result;
  };

  return (
    <>
      {modalAccept && <ModalAccept {...modalAccept} />}
      <div className="fixed inset-0 flex items-center justify-center bg-black bg-opacity-50">
        <div className="rounded-md p-2 m-2 bg-white w-full h-max max-w-md">
          <div className="block">
            <div className="flex justify-end items-end mt-3 mr-3">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                fill="none"
                viewBox="0 0 24 24"
                strokeWidth={1.5}
                stroke="currentColor"
                className="w-6 h-6 cursor-pointer"
                onClick={props.onCancel}
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  d="M6 18L18 6M6 6l12 12"
                />
              </svg>
            </div>
            <TitlePage text={props?.title} />
            <form onSubmit={submit}>
              <div className="p-4">
                <div className="mt-0">
                  <div className="p-2 rounded-md shadow">
                    <div className="text-start pl-2">Foto de la noticia</div>
                    <div className="flex mt-2">
                      <div className="bg-gray-200 w-16 h-16 rounded-md m-2">
                        <img
                          src={imgCover || api.resources + props?.data?.notice?.photo}
                          className="w-16 h-16 rounded-md"
                          alt=" "
                        />
                      </div>
                      <div className="m-auto bg-blue-500 rounded-md p-2 w-max h-max pr-3 pl-3">
                        <label
                          htmlFor="image"
                          className="text-white font-semibold cursor-pointer flex"
                        >
                          <svg
                            xmlns="http://www.w3.org/2000/svg"
                            viewBox="0 0 24 24"
                            fill="currentColor"
                            className="w-6 h-6 pr-2 text-white font-medium"
                          >
                            <path
                              fillRule="evenodd"
                              d="M11.47 2.47a.75.75 0 011.06 0l4.5 4.5a.75.75 0 01-1.06 1.06l-3.22-3.22V16.5a.75.75 0 01-1.5 0V4.81L8.03 8.03a.75.75 0 01-1.06-1.06l4.5-4.5zM3 15.75a.75.75 0 01.75.75v2.25a1.5 1.5 0 001.5 1.5h13.5a1.5 1.5 0 001.5-1.5V16.5a.75.75 0 011.5 0v2.25a3 3 0 01-3 3H5.25a3 3 0 01-3-3V16.5a.75.75 0 01.75-.75z"
                              clipRule="evenodd"
                            />
                          </svg>
                          Subir archivo
                        </label>
                        <input
                          id="image"
                          name="image"
                          type="file"
                          className="hidden"
                          onChange={imgChange}
                          accept="image/*"
                        />
                      </div>
                    </div>
                  </div>
                </div>
                <div className="mt-3">
                  <TextFieldLabel
                    title="Titulo"
                    type="text"
                    name="title"
                    placeholder="Titulo de la noticia"
                    text={props?.data?.notice?.title}
                    required
                    noVoid
                  />
                </div>
                <div className="mt-3">
                  <TextArea
                    type="text"
                    name="details"
                    placeholder="Detalles de la noticia..."
                    text={props?.data?.notice?.details}
                    required
                    noVoid
                  />
                </div>
                <div className="ml-2 mt-4">
                  <CheckField
                    text="Estado de la noticia"
                    name="status"
                    selected={props?.data?.notice?.status}
                  />
                </div>
                <div className="flex justify-center w-full mt-8">
                  <PrimaryButton
                    type="submit"
                    className="rounded-lg"
                    text={props.buttonText}
                  />
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>
    </>
  );
}

export default LevelsUpdate;
