import TitlePage from "../../components/TitlePage/TitlePage";
import TextFieldLabel from "../../components/TextField/TextFieldLabel.js";
import SelectField from "../../components/SelectField/SelectField.js";
import TextArea from "../../components/TextField/TextArea";
import PrimaryButton from "../../components/Buttons/PrimaryButton.js";
import ModalAccept from "../../components/Modal/ModalAccept";
import Alert from "../../components/PushMessage/Alert";
import { useEffect, useState } from "react";
import { getCategories } from "../../api/categories";
import {
  getEditionById,
  updateCategoriesByEditionId,
  updateMatchesDates,
} from "../../api/editions";
import Loading from "../../components/Loading/Loading";
import useUser from "../../hooks/auth/useUser";
import CheckField from "../../components/CheckField/CheckField";
import Table from "../../components/Table/Table";
import TableColumn from "../../components/Table/TableColumn";
import TableRow from "../../components/Table/TableRow";

function LigasCategoriesUpdate(props) {
  const [categories, setCategories] = useState(false);
  const [categoriesSelected, setCategoriesSelected] = useState(false);
  const [modalAccept, setModalAccept] = useState(false);
  const [stateAlert, setStateAlert] = useState(false);
  const [stateLoading, setStateLoading] = useState(false);
  const [userData, setUserData] = useUser();

  const Categories = () => {
    setStateLoading(true);
    getCategories((json, error) => {
      setStateLoading(false);
      if (json.code == "ok") {
        setCategories(json.data);
        const data = json.data.map((item) => ({
          id: item.id,
          state: props.data.categories.find((e) => e.category_id == item.id)
            ? true
            : false,
        }));
        setCategoriesSelected(data);
      } else {
        setStateAlert({
          type: "err",
          message: json.message || error,
        });
      }
    });
  };

  useEffect(() => {
    Categories();
    //console.log(props.data.categories);
  }, []);

  const submit = () => {
    const data = categoriesSelected
      .filter((obj) => obj.state === true)
      .map((obj) => obj.id);
    const requestData = { ids: data, edition_id: props.data.edition_id };
    //console.log(requestData);
    setModalAccept({
      ok: () => {
        setModalAccept(false);
        setStateLoading(true);
        updateCategoriesByEditionId(
          requestData,
          userData.token,
          (json, error) => {
            setStateLoading(false);
            //console.log(json);
            if (json.code == "ok") {
              setStateAlert({
                type: "ok",
                message: "Categorias de la liga actualizadas con exito!",
              });
              setTimeout(() => setStateAlert(false), 1000);
              props.onUpdate();
              //setTimeout(() => props.onCancel(), 1000);
            } else {
              setStateAlert({
                type: "err",
                message: json.message || error,
              });
            }
          }
        );
      },
      cancel: () => setModalAccept(false),
    });
  };

  const onCheckBoxChange = (id) => {
    if (categoriesSelected) {
      const data = categoriesSelected;
      data.map((item) => {
        if (item.id == id) item.state = !item.state;
      });
      setCategoriesSelected(data);
      //console.log(data);
    }
  };

  return (
    <>
      {modalAccept && <ModalAccept {...modalAccept} />}
      {stateAlert && <Alert {...stateAlert} />}
      {stateLoading && <Loading {...stateLoading} />}
      <div className="align-center absolute pl-2 pr-2 w-full h-full text-center bg-gray-100 pb-20">
        <div className="flex justify-end items-end mt-4 mr-4">
          <svg
            xmlns="http://www.w3.org/2000/svg"
            fill="none"
            viewBox="0 0 24 24"
            strokeWidth={1.5}
            stroke="currentColor"
            className="w-6 h-6 cursor-pointer"
            onClick={props.onCancel}
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              d="M6 18L18 6M6 6l12 12"
            />
          </svg>
        </div>
        <TitlePage text={"Categorias de la liga: " + props.title} />
        <div className="w-screen mt-8 Xflex flex-wrap justify-center Xgap-4">
          <div>
            <Table
              title="Todas las categorias"
              columns={<TableColumn columns={["Nombre", "Nivel", "Activa"]} />}
              imageHeader={
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  fill="none"
                  viewBox="0 0 24 24"
                  strokeWidth={1.5}
                  stroke="currentColor"
                  className="w-6 h-6"
                >
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    d="M9.568 3H5.25A2.25 2.25 0 003 5.25v4.318c0 .597.237 1.17.659 1.591l9.581 9.581c.699.699 1.78.872 2.607.33a18.095 18.095 0 005.223-5.223c.542-.827.369-1.908-.33-2.607L11.16 3.66A2.25 2.25 0 009.568 3z"
                  />
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    d="M6 6h.008v.008H6V6z"
                  />
                </svg>
              }
            >
              {categories &&
                categories.map((item, index) => (
                  <tr key={index}>
                    <TableRow>
                      <div className="p-3 font-semibold text-gray-800 text-sm">
                        {item.name}
                      </div>
                    </TableRow>
                    <TableRow>
                      <div className="font-semibold text-gray-800 text-sm">
                        {item.level}
                      </div>
                    </TableRow>
                    <TableRow>
                      <div className="flex">
                        <CheckField
                          selected={props.data.categories.find(
                            (e) => e.category_id == item.id
                          )}
                          onChange={() => onCheckBoxChange(item.id)}
                        />
                      </div>
                    </TableRow>
                  </tr>
                ))}
            </Table>
          </div>
          <div className="w-full mt-6">
            <div onClick={submit}>
              <PrimaryButton className="rounded-lg" text={props.buttonText} />
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default LigasCategoriesUpdate;
