import ItemCategory from "./ItemCategory.js";
import { useState, useEffect } from "react";
import Alert from "../PushMessage/Alert.js";
import Loading from "../Loading/Loading.js";
import { getCategories } from "../../api/categories.js";

function Categories(props) {
  //const [categories, setCategories] = useState([
  //  { name: "MASC. ORO (3ªA)", id: "1", state: true },
  //  { name: "MASC. PLATA (2ªA)", id: "2", state: false },
  //  { name: "FEM. ORO (2ªA)", id: "3", state: false },
  //]);
  const [categories, setCategories] = useState(false);
  const [stateAlert, setStateAlert] = useState(false);
  const [stateLoading, setStateLoading] = useState(false);

  const Categories = () => {
    setStateAlert(false);
    setStateLoading(true);

    getCategories((json, error) => {
      setStateLoading(false);

      if (json) {
        if (json.code == "ok") {
          let categs = json.data.map((item, index) => ({
            state: index == 0,
            name: item.name,
            id: item.id,
          }));
          setCategories(categs);
          props.onClick(categs[0]?.id);
        } else {
          setStateAlert({
            type: "war",
            message: json.message,
          });
        }
      } else {
        setStateAlert({
          type: "err",
          message: "Ocurrio un error!, " + error,
        });
      }
    });
  };

  useEffect(() => {
    Categories();
  }, []);

  const handleClick = (id) => {
    const updatedCategories = categories.map((category) =>
      category.id === id
        ? { ...category, state: true }
        : { ...category, state: false }
    );
    if (JSON.stringify(updatedCategories) === JSON.stringify(categories))
      return; //Este activo no actualizar
    setCategories(updatedCategories);
    props.onClick(id);
  };

  return (
    <>
      {stateLoading && <Loading />}
      {stateAlert && <Alert {...stateAlert} />}
      <div className="flex flex-wrap justify-center mb-6">
        {categories &&
          categories.map((category, index) => (
            <ItemCategory
              key={index}
              id={category.id}
              state={category.state}
              text={category.name}
              onClick={() => handleClick(category.id)}
            />
          ))}
      </div>
      <div className="pb-6 pt-2">
        <h1 className="text-2xl text-semibold">
          <span className="text-blue-500 text-bold">Categoría</span> -{" "}
          {categories && categories.map((item) => {
            if (item.state) return item.name;
          })}
        </h1>
      </div>
    </>
  );
}

export default Categories;
