import Api from "./api.js";
import { get, post, put, del } from "./request.js";

//callback (json, error)

const callbacks = (callback) => {
  return {
    ready: (json) => {
      if (json) callback(json, null);
      else
        callback(
          null,
          " Posiblemente acurrio error del servidor!, intentelo mas tarde."
        );
    },
    error: (error) => callback(null, error),
  };
};

export function getActivesNews(callback) {
  get(Api.getActivesNews, {
    ...callbacks(callback),
  });
}

export function getNews(token, callback) {
  get(Api.getNews, {
    headers: {
      "Content-Type": "application/json",
      Authorization: `Bearer ${token}`,
      Accept: "application/json",
    },
    ...callbacks(callback),
  });
}

export function getNewsById(id, token, callback) {
  get(Api.getNewsById + id, {
    headers: {
      "Content-Type": "application/json",
      Authorization: `Bearer ${token}`,
      Accept: "application/json",
    },
    ...callbacks(callback),
  });
}

export function newNews(data, token, callback) {
  post(Api.newNews, {
    headers: {
      'Content-Type': 'application/json',
      Authorization: `Bearer ${token}`,
      Accept: "application/json",
    },
    body: data,
    ...callbacks(callback),
  });
}

export function updateNews(data, token, callback) {
  put(Api.updateNews, {
    headers: {
      "Content-Type": "application/json",
      Authorization: `Bearer ${token}`,
      Accept: "application/json",
    },
    body: data,
    ...callbacks(callback),
  });
}

export function deleteNews(id, token, callback) {
  del(Api.deleteNews, {
    headers: {
      'Content-Type': 'application/json',
      Authorization: `Bearer ${token}`,
      Accept: "application/json",
    },
    body: { newsId: id },
   ...callbacks(callback),
  });
}

export function uploadNewImage(data, token, callback) {
  post(Api.uploadNewImage, {
    format: "form-data",
    headers: {
      Authorization: `Bearer ${token}`,
      Accept: "application/json",
    },
    body: data,
   ...callbacks(callback),
  });
}