import { useEffect, useState } from "react";

function WizardMenu(props) {
  const [position, setPosition] = useState(0);
  const next = () => {
    const requiredElems = document.querySelectorAll(
      `#section${position} [required]`
    );
    const invalidElems = [...requiredElems].filter((elem) => {
      elem.reportValidity();
      return !elem.checkValidity();
    });
    if (invalidElems.length > 0) return;
    if (props.checkUser && !props.registered) {
      props.checkUser();
      return;
    }

    if (position < props.sections.length - 1) setPosition(position + 1);

  };

  const back = () => {
    if (position != 0) setPosition(position - 1);

  };

  useEffect(() => {
    props.setPosition && props.setPosition(position)
    props.strollToTop && window.scrollTo(0, 0);
  }, [position]);

  return (
    <div className="h-fit flex items-center w-full">
      <div className="grow">
        <div className="mb-6">
          <h2 className="text-2xl font-semibold text-gray-700">
            {props.title}
          </h2>
          <p className="text-gray-500">{props.description}</p>
        </div>
        <div>
          {props.sections &&
            props.sections.map((section, index) => (
              <div
                key={index}
                className={`transition-opacity duration-500 ${position === index ? "block opacity-100" : "hidden opacity-0"
                  }`}
                id={`section${index}`}
              >
                <div>{section}</div>
              </div>
            ))}
        </div>
        <div className="mt-6 flex justify-between">
          <div
            className={`bg-blue-500X hover:bg-blue-700X text-whiteX cursor-pointer font-bold py-2 px-4 rounded ${position === 0 ? "opacity-0 cursor-not-allowed" : ""
              }`}
            onClick={back}
            disabled={position === 0}
          >
            <svg
              xmlns="http://www.w3.org/2000/svg"
              fill="none"
              viewBox="0 0 24 24"
              strokeWidth={1.5}
              stroke="currentColor"
              className="w-6 h-6"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                d="M19.5 12h-15m0 0l6.75 6.75M4.5 12l6.75-6.75"
              />
            </svg>
          </div>
          <div className="flex mt-2">
            {props.sections &&
              props.sections.map((item, index) => (
                <div
                  key={index}
                  className={`w-2 h-2 m-1 rounded-full transition-opacity duration-500 ${position === index ? "bg-gray-400" : "bg-gray-300"
                    }`}
                ></div>
              ))}
          </div>
          <div
            className={`bg-blue-500X hover:bg-blue-700X text-whiteX cursor-pointer font-bold py-2 px-4 rounded ${position === props.sections.length - 1
              ? "opacity-0 cursor-not-allowed"
              : ""
              }`}
            onClick={next}
            disabled={position === props.sections.length - 1}
          >
            <svg
              xmlns="http://www.w3.org/2000/svg"
              fill="none"
              viewBox="0 0 24 24"
              strokeWidth={1.5}
              stroke="currentColor"
              className="w-6 h-6"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                d="M4.5 12h15m0 0l-6.75-6.75M19.5 12l-6.75 6.75"
              />
            </svg>
          </div>
        </div>
      </div>
    </div>
  );
}

export default WizardMenu;
