import NavBar from "../../components/NavBar/NavBar";
import Footer from "../../components/Footer/Footer";
import TitlePage from "../../components/TitlePage/TitlePage";
import TableColumn from "../../components/Table/TableColumn.js";
import TableRow from "../../components/Table/TableRow.js";
import Table from "../../components/Table/Table.js";
import ItemEdit from "../../components/Table/ItemEdit";
import ItemDelete from "../../components/Table/ItemDelete";
import AddButton from "../../components/Buttons/AddButton";
import Loading from "../../components/Loading/Loading";
import Alert from "../../components/PushMessage/Alert";
import CrossoverTypeUpdate from "./CrossoverTypeUpdate";
import { useState, useEffect } from "react";
import useUser from "../../hooks/auth/useUser";
import ModalAccept from "../../components/Modal/ModalAccept";
import { deleteCrossoverType, getCrossoversTypes, newCrossoverType, updateCrossoverType } from "../../api/crossoversTypes"

function CrossoverTypes() {
  const [crossoversTypes, setCrossoverTypes] = useState(false);
  const [stateAlert, setStateAlert] = useState(false);
  const [stateLoading, setStateLoading] = useState(false);
  const [edit, setEdit] = useState(false);
  const [modalAccept, setModalAccept] = useState(false);
  const [userData, setUserData] = useUser();

  const CrossoverType = () => {
    setStateAlert(false);
    setStateLoading(true);

    getCrossoversTypes((json, error) => {
      setStateLoading(false);

      if (json) {
        if (json.code == "ok") {
          setCrossoverTypes(json.data);
        } else {
          setStateAlert({
            type: "war",
            message: json.message,
          });
        }
      } else {
        setStateAlert({
          type: "err",
          message: "Ocurrio un error!, " + error,
        });
      }
    });
  };

  useEffect(() => {
    CrossoverType();
  }, []);

  const addCrossoverType = () => {
    setEdit({
      title: "Tipo de cruce nuevo",
      buttonText: "Crear nuevo",
      onSubmit: (data) => {
        setStateLoading(true);
        newCrossoverType(data, userData.token, (json, error) => {
          setStateLoading(false);
          if (json.code == "ok") {
            setStateAlert({
              type: "ok",
              message: "Tipo de cruce creado con exito!",
            });
            setTimeout(() => CrossoverType(), 1000);
            setEdit(false);
          } else {
            setStateAlert({
              type: "err",
              message: json.message || error,
            });
          }
        });
      },
      onCancel: () => setEdit(false),
    });
  };

  const editCrossoverType = (id) => {
    let data = crossoversTypes.find((e) => e.id == id);
    setEdit({
      title: "Editar tipo de cruce",
      data: data,
      buttonText: "Guardar cambios",
      onSubmit: (dataUpdate) => {
        setStateLoading(true);
        updateCrossoverType(dataUpdate, userData.token, (json, error) => {
          setStateLoading(false);
          if (json.code == "ok") {
            setStateAlert({
              type: "ok",
              message: "Tipo de cruce actualizado con exito!",
            });
            setTimeout(() => CrossoverType(), 1000);
            setEdit(false);
          } else {
            setStateAlert({
              type: "err",
              message: json.message || error,
            });
          }
        });
      },
      onCancel: () => setEdit(false),
    });
  };

  const removeCrossoverType = (id) => {
    setModalAccept({
      ok: () => {
        setStateLoading(true);
        setModalAccept(false);
        deleteCrossoverType(id, userData.token, (json, error) => {
          setStateLoading(false);
          if (json.code == "ok") {
            setStateAlert({
              type: "ok",
              message: "Eliminado con exito!",
            });
            setTimeout(() => CrossoverType(), 1000);
            setEdit(false);
          } else {
            setStateAlert({
              type: "err",
              message: json.message || error,
            });
          }
        });
      },
      cancel: () => setModalAccept(false),
    });
  };

  return (
    <>
      {stateLoading && <Loading />}
      {stateAlert && <Alert {...stateAlert} />}
      {edit && <CrossoverTypeUpdate {...edit} />}
      {modalAccept && <ModalAccept {...modalAccept} />}
      <NavBar />
      <div className="pl-2 pr-2 block h-full text-center bg-gray-100 pt-10 pb-20">
        <TitlePage text="Admin - Tipos de cruces" />
        <div className="flex justify-end items-end lg:pr-32 mb-6">
          <div className="w-max h-max" onClick={addCrossoverType}>
            <AddButton />
          </div>
        </div>
        <Table
          title="Tipos de cruces"
          columns={
            <TableColumn
              columns={[
                "Codigo",
                "Nombre",
                "Es eliminatoria",
                "Fecha de creación",
                "Fecha de actualización",
                "Acciones",
              ]}
            />
          }
          imageHeader={
            <svg
              xmlns="http://www.w3.org/2000/svg"
              fill="none"
              viewBox="0 0 24 24"
              strokeWidth={1.5}
              stroke="currentColor"
              className="w-6 h-6"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                d="M10.5 6h9.75M10.5 6a1.5 1.5 0 11-3 0m3 0a1.5 1.5 0 10-3 0M3.75 6H7.5m3 12h9.75m-9.75 0a1.5 1.5 0 01-3 0m3 0a1.5 1.5 0 00-3 0m-3.75 0H7.5m9-6h3.75m-3.75 0a1.5 1.5 0 01-3 0m3 0a1.5 1.5 0 00-3 0m-9.75 0h9.75"
              />
            </svg>
          }
        >
          {crossoversTypes &&
            crossoversTypes.map((item, index) => (
              <tr key={index}>
                <TableRow>
                  <div className="p-3 font-semibold text-gray-800 text-sm">
                    {item.code}
                  </div>
                </TableRow>
                <TableRow>
                  <div className="p-3 font-semibold text-gray-700 text-sm">
                    {item.name}
                  </div>
                </TableRow>
                <TableRow>
                  <div className="p-3 font-semibold text-gray-800 text-sm">
                    {item.is_eliminator ? "Si" : "No"}
                  </div>
                </TableRow>
                <TableRow>
                  <div className="font-semibold text-blue-500 text-sm">
                    {new Date(item.created_at).toLocaleString()}
                  </div>
                </TableRow>
                <TableRow>
                  <div className="font-semibold text-red-500 text-sm">
                    {new Date(item.updated_at).toLocaleString()}
                  </div>
                </TableRow>
                <TableRow>
                  <div className="flex">
                    <ItemEdit id={item.id} onClick={editCrossoverType} />
                    <ItemDelete id={item.id} onClick={removeCrossoverType} />
                  </div>
                </TableRow>
              </tr>
            ))}
        </Table>
      </div>
      <Footer />
    </>
  );
}

export default CrossoverTypes;
