function PrimaryButton({ type, className, handleCLick, text, disabled, children, id }) {
  return (
    <>
      <button
        type={type}
        className={
          "bg-blue-500 hover:bg-blue-600 text-white font-semibold py-2 px-4 pb-2.5 " +
          className
        }
        onClick={handleCLick}
        disabled={disabled}
        id={id}
      >
        <div className="flex justify-center">
          {children}
          <div className="truncate">
            {text}
          </div>
        </div>
      </button>
    </>
  );
}

export default PrimaryButton;
