import Api from "./api.js";
import { get, post, put, del } from "./request.js";

//callback (json, error)

const callbacks = (callback) => {
  return {
    ready: (json) => {
      if (json) callback(json, null);
      else
        callback(
          null,
          " Posiblemente acurrio error del servidor!, intentelo mas tarde."
        );
    },
    error: (error) => callback(null, error),
  };
};

export function getSponsors(callback) {
  get(Api.getSponsors, {
    ...callbacks(callback),
  });
}

export function getSponsorById(id, token, callback) {
  get(Api.getSponsorById + id, {
    headers: {
      Authorization: `Bearer ${token}`,
      Accept: "application/json",
    },
    ...callbacks(callback),
  });
}

export function newSponsor(data, token, callback) {
  post(Api.newSponsor, {
    headers: {
      'Content-Type': "application/json",
      Authorization: `Bearer ${token}`,
      Accept: "application/json",
    },
    body: data,
    ...callbacks(callback),
  });
}

export function updateSponsor(data, token, callback) {
  put(Api.updateSponsor, {
    headers: {
      'Content-Type': "application/json",
      Authorization: `Bearer ${token}`,
      Accept: "application/json",
    },
    body: data,
    ...callbacks(callback),
  });
}

export function deleteSponsor(id, token, callback) {
  del(Api.deleteSponsor, {
    headers: {
      'Content-Type': "application/json",
      Authorization: `Bearer ${token}`,
      Accept: "application/json",
    },
    body: { sponsorId: id },
    ...callbacks(callback),
  });
}

export function uploadSponsorImage(data, token, callback) {
  post(Api.uploadSponsorImage, {
    format: "form-data",
    headers: {
      Authorization: `Bearer ${token}`,
      Accept: "application/json",
    },
    body: data,
   ...callbacks(callback),
  });
}