import NavBar from "../../components/NavBar/NavBar.js";
import Group from "../../components/Table/Group.js";
import TableParticipantes from "../../components/Table/TableParticipantes.js";
import Categories from "../../components/Categories/Categories.js";
import Footer from "../../components/Footer/Footer.js";
import { useState } from "react";
import { getCouplesByCategory } from "../../api/couples.js";
import Alert from "../../components/PushMessage/Alert.js";
import Loading from "../../components/Loading/Loading.js";
import api from "../../api/api.js";

function Participantes() {
  const [stateAlert, setStateAlert] = useState(false);
  const [stateLoading, setStateLoading] = useState(false);
  const [couples, setCouples] = useState(false);

  const CouplesByCategory = (id) => {
    setStateAlert(false);
    setStateLoading(true);

    getCouplesByCategory(id, (json, error) => {
      setStateLoading(false);

      if (json) {
        if (json.code == "ok") {
          setCouples(json.data);
        } else {
          setStateAlert({
            type: "war",
            message: json.message,
          });
        }
      } else {
        setStateAlert({
          type: "err",
          message: "Ocurrio un error!, " + error,
        });
      }
    });
  };

  return (
    <>
      {stateLoading && <Loading />}
      {stateAlert && <Alert {...stateAlert} />}
      <NavBar />
      <div className="pl-2 pr-2 block h-full text-center bg-gray-100 pt-10 pb-20">
        <Categories onClick={CouplesByCategory} />
        {couples &&
          couples.map((item, index) => (
            <div key={index}>
              <Group title={item.name} />
              <TableParticipantes
                rows={
                  item.group_has_couples &&
                  item.group_has_couples.map((couple) => ({
                    couple: {
                      name: couple?.couple?.name || (
                        <span className="text-blue-600"># Reserva #</span>
                      ),
                      image: api.resources + couple?.couple?.avatar,
                    },
                    jugador1: couple?.couple?.user_one?.name || (
                      <span className="text-blue-600"># Reserva #</span>
                    ),
                    jugador2: couple?.couple?.user_two?.name || (
                      <span className="text-blue-600"># Reserva #</span>
                    ),
                  }))
                }
              />
            </div>
          ))}
      </div>
      <Footer />
    </>
  );
}

export default Participantes;
