const links = [
  {
    title: "PÁDEL AMIGOS",
    options: [
      {
        name: "Inscripciones",
        url: "#!",
      },
      {
        name: "Competición",
        url: "#!",
      },
      {
        name: "Patrocinio y Colaboración",
        url: "#!",
      },
    ],
  },
  {
    title: "INFORMACIÓN",
    options: [
      {
        name: "Política de Privacidad",
        url: "#!",
      },
      {
        name: "Política de Cookies",
        url: "#!",
      },
      {
        name: "Aviso Legal",
        url: "#!",
      },
    ],
  },
  {
    title: "CONTACTO",
    options: [
      {
        name: "646 61 77 79",
        url: "",
      },
      {
        name: "info@padelamigos.es",
        url: "",
      },
      {
        name: "Alicante",
        url: "",
      },
    ],
  },
];

function Footer() {
  return (
    <footer className="z-10 w-full h-max mt-auto">
      <div className="Xmt-10 mb-6 flex flex-wrap justify-center padding-3">
        {links &&
          links.map((item, index) => (
            <div key={index} className="text-center ml-10 mr-10 mb-6 m-4">
              <h5 className="mb-2.5 font-bold uppercase text-blue-900">
                {item.title}
              </h5>
              <ul className="mb-0 list-none">
                {item.options &&
                  item.options.map((opt, index) => (
                    <li key={index}>
                      <a href={opt.url} className="text-gray-800">
                        {opt.name}
                      </a>
                    </li>
                  ))}
              </ul>
            </div>
          ))}
      </div>
      <div className="shadow bg-white p-4 pb-6 text-center">
        © 2023 Copyright:
        <a className="text-blue-600" href="https://padelamigos.es/">
          {" "}
          Padel Amigos
        </a>
      </div>
    </footer>
  );
}

export default Footer;
