function ModalAccept(props) {
  return (
    <div className="fixed inset-0 z-50 flex items-center justify-center bg-black bg-opacity-50">
      <div className="flex flex-col rounded-xl p-2 pl-6 pr-6 pt-8 bg-white">
        <div className="flex justify-center">
          <div className="block">
            <div className="flex items-center justify-center gap-4">
              <svg
                className="text-orange-500 mt-0.5"
                xmlns="http://www.w3.org/2000/svg"
                width="60"
                height="60"
                fill="currentColor"
                viewBox="0 0 16 16"
              >
                <path d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0zM8 4a.905.905 0 0 0-.9.995l.35 3.507a.552.552 0 0 0 1.1 0l.35-3.507A.905.905 0 0 0 8 4zm.002 6a1 1 0 1 0 0 2 1 1 0 0 0 0-2z" />
              </svg>
              {props.message || "Estas seguro que desea realizar la accion?"}
            </div>
            <div className="flex justify-end p-1">
              <div className="m-2 p-2 pl-4 pr-4 shadow-lg rounded-md">
                <button onClick={props.ok || props.ok()}>OK</button>
              </div>
              <div className="m-2 p-2 pl-4 pr-4 shadow">
                <button onClick={props.cancel || props.cancel()}>
                  CANCELAR
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default ModalAccept;
