
function Loading(props) {
  return (
    <div className="fixed inset-0 z-50 flex items-center justify-center bg-black bg-opacity-50">
      <div className="flex flex-col rounded-xl p-5 bg-white">
        <div className="flex justify-center">
          <div
            className="animate-spin inline-block w-14 h-14 border-[4px] border-current border-t-transparent text-blue-600 rounded-full"
            role="status"
            aria-label="loading"
          >
            <span className="sr-only">Loading...</span>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Loading;