import NavBar from "../../components/NavBar/NavBar";
import Footer from "../../components/Footer/Footer";
import TitlePage from "../../components/TitlePage/TitlePage";
import TableColumn from "../../components/Table/TableColumn.js";
import TableRow from "../../components/Table/TableRow.js";
import Table from "../../components/Table/Table.js";
import ItemEdit from "../../components/Table/ItemEdit";
import Loading from "../../components/Loading/Loading";
import Alert from "../../components/PushMessage/Alert";
import SelectField from "../../components/SelectField/SelectField";
import GroupsUpdate from "./GroupsUpdate";
import { useState, useEffect } from "react";
import { getEditions, getEditionCategories } from "../../api/editions";
import useUser from "../../hooks/auth/useUser";
import api from "../../api/api";

function Groups() {
  const [editions, setEditions] = useState(false);
  const [editionsCategories, setEditionsCategories] = useState(false);
  const [stateAlert, setStateAlert] = useState(false);
  const [stateLoading, setStateLoading] = useState(false);
  const [editEdition, setEditEdition] = useState(false);
  const [userData, setUserData] = useUser();

  const Editions = () => {
    setStateAlert(false);
    setStateLoading(true);

    getEditions((json, error) => {
      setStateLoading(false);

      if (json) {
        if (json.code != "error") {
          setEditions(json.data);
          //console.log(json);
        } else {
          setStateAlert({
            type: "war",
            message: json.message,
          });
        }
      } else {
        setStateAlert({
          type: "err",
          message: "Ocurrio un error!, " + error,
        });
      }
    });
  };

  const EditionsCategories = () => {
    setStateAlert(false);
    setStateLoading(true);

    getEditionCategories(userData.token, (json, error) => {
      setStateLoading(false);

      if (json) {
        if (json.code != "error") {
          setEditionsCategories(json.data);
          //console.log(json.data);
        } else {
          setStateAlert({
            type: "war",
            message: json.message,
          });
        }
      } else {
        setStateAlert({
          type: "err",
          message: "Ocurrio un error!, " + error,
        });
      }
    });
  };

  useEffect(() => {
    Editions();
    EditionsCategories();
  }, []);

  const editGroup = (form) => {
    form.preventDefault();
    const formValues = convertToNumbers(
      Object.fromEntries(new FormData(form.target).entries())
    );
    //console.log(formValues);
    setEditEdition({
      data: formValues,
      title: editions.find((e) => e.id == formValues.edition_id).name,
      buttonText: "Guardar cambios",
      onUpdate: () => {
        Editions();
        EditionsCategories();
      },
      onCancel: () => setEditEdition(false),
    });
  };

  const convertToNumbers = (formValues) => {
    const result = {};
    for (const [key, value] of Object.entries(formValues)) {
      result[key] = isNaN(value) ? value : Number(value);
    }
    return result;
  };

  return (
    <>
      {stateLoading && <Loading />}
      {stateAlert && <Alert {...stateAlert} />}
      {editEdition && <GroupsUpdate {...editEdition} />}
      <NavBar />
      <div className="pl-2 pr-2 block h-full text-center bg-gray-100 pt-10 pb-20">
        <TitlePage text="Admin - Grupos" />
        <br></br>
        <Table
          title="Todas las Ligas"
          columns={
            <TableColumn columns={["Portada", "Nombre", "Categorias"]} />
          }
          imageHeader={
            <svg
              xmlns="http://www.w3.org/2000/svg"
              fill="none"
              viewBox="0 0 24 24"
              strokeWidth={1.5}
              stroke="currentColor"
              className="w-6 h-6"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                d="M6 6.878V6a2.25 2.25 0 012.25-2.25h7.5A2.25 2.25 0 0118 6v.878m-12 0c.235-.083.487-.128.75-.128h10.5c.263 0 .515.045.75.128m-12 0A2.25 2.25 0 004.5 9v.878m13.5-3A2.25 2.25 0 0119.5 9v.878m0 0a2.246 2.246 0 00-.75-.128H5.25c-.263 0-.515.045-.75.128m15 0A2.25 2.25 0 0121 12v6a2.25 2.25 0 01-2.25 2.25H5.25A2.25 2.25 0 013 18v-6c0-.98.626-1.813 1.5-2.122"
              />
            </svg>
          }
        >
          {editions &&
            editions.map((item, index) => (
              <tr key={index}>
                <TableRow>
                  <div className="m-2">
                    <div className="rounded-lg bg-gray-200 w-16 h-16">
                      <img
                        className="w-16 h-16 rounded-lg"
                        src={api.resources + item.cover}
                        alt=" "
                        style={{ objectFit: "cover" }}
                      />
                    </div>
                  </div>
                </TableRow>
                <TableRow>
                  <div className="m-4 p-2 font-semibold text-gray-800 text-sm">
                    {item.name}
                  </div>
                </TableRow>
                <TableRow>
                  <form onSubmit={editGroup}>
                    <div className="flex mb-4 mt-2 mr-6 w-max">
                      <SelectField
                        disabledTitle={true}
                        name="category_id"
                        default="Seleccione una categoria"
                        body={
                          editionsCategories &&
                          editionsCategories
                            .find((e) => e.id == item.id)
                            .editions_categories.map((item) => ({
                              value: item.category.id,
                              label: item.category.name,
                            }))
                        }
                        required
                        noVoid
                      />
                      <input hidden={true} name="edition_id" value={item.id} />
                      <div className="ml-10 flex">
                        <button type="submit">
                          <ItemEdit />
                        </button>
                      </div>
                    </div>
                  </form>
                </TableRow>
              </tr>
            ))}
        </Table>
      </div>
      <Footer />
    </>
  );
}

export default Groups;
