import React, { useEffect, useState } from "react";
import Info from "./Icons/Info";
import Calendar from "./Icons/Calendar";
import Campus from "./Icons/Campus";
import Gift from "./Icons/Gift";
import CreditCard from "./Icons/CreditCard";

export default ({ position }) => {

    const [percent, setPercent] = useState(0);
    const icons = [<Info />, <Calendar />, <Campus />, <Gift />, <CreditCard />];
    useEffect(() => {
        setPercent(position * 25)
    }, [position])


    return (<div className="mt-8 relative flex justify-between">
        <hr className="absolute top-1/2 -translate-y-1/2 translate-y-1/2 z-10 w-full"
           style={{
            borderImageSource: `linear-gradient(to right, #3B82F6 0, #3B82F6 ${percent}%, #18181b ${percent}%, #18181b 100%)`,
            borderImageWidth: 1,
            borderImageSlice: '1 fill'
          }} />
        {
            icons.map((e, i) => <div key={i} className={`z-20 ${percent < (20 * i) ? 'bg-zinc-500' : 'bg-blue-500'} h-14 w-14 rounded-full shadow-md flex items-center justify-center cursor-pointer hover:scale-105 transition-transform duration-300 text-white`}>
                {e}
            </div>)
        }
    </div>);
}