import React from "react";
import TextFieldLabel from "../../../components/TextField/TextFieldLabel";
import CheckField from "../../../components/CheckField/CheckField";

const Time = ({ timesRef, weeks }) => {
    const weeksArray = [];
    for (let index = 0; index < weeks; index++) {
        weeksArray.push(<Week key={index} id={`week-${index}`} index={index} />)
    }

    return (
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-4" ref={timesRef}>
            {
                weeksArray.length ? weeksArray : <h1 className="text-center text-blue-500 text-2xl col-span-2 py-5">El rango de fecha establecido para esta edici&oacute;n es incorrecto</h1>
            }
        </div>);
}


const Week = ({ id, index }) => {
    const days = [
        { key: 'dl', value: 'Lunes' },
        { key: 'dm', value: 'Martes' },
        { key: 'dx', value: 'Miercoles' },
        { key: 'dj', value: 'Jueves' },
        { key: 'dv', value: 'Viernes' },
        { key: 'ds', value: 'Sabado' },
        { key: 'dd', value: 'Domingo' }];
    return (
        <div id={id} className="pb-4 bg-white rounded-md shadow-md week">
            <div className="w-full bg-slate-600 p-3 rounded-md col-span-2">
                <p className="text-white text-2xl text-center truncate">
                    D&iacute;as de la semana {index + 1}
                </p>
            </div>
            <div className="grid grid-cols-1 md:grid-cols-2 gap-3 my-3 px-4">
                <TextFieldLabel
                    title="Desde"
                    type="date"
                    name="startTime"
                    id={`startTime-${id}`}
                    placeholder="Definir horario"
                    required
                />
                <TextFieldLabel
                    title="Hasta"
                    type="date"
                    name="endTime"
                    id={`endTime-${id}`}
                    placeholder="Definir horario"
                    required
                />
            </div>
            <div className="col-span-2 px-4">
                <div className="grid grid-cols-2 sm:grid-cols-3 md:grid-cols-5">
                    {
                        days.map((e, index) =>

                            <CheckField
                                key={e.key}
                                className="text-lg cursor-pointer truncate"
                                id={`${e.key}-${id}`}
                                name={`${e.key}`}
                                text={
                                    <div className="cursor-pointer">
                                        {e.value}
                                    </div>
                                }
                            />

                        )

                    }
                </div>
            </div>
        </div>)
}
export default Time;