import { useState, useRef, useEffect } from "react";

function TextArea(props) {
  const [visible, setVisible] = useState(false);
  const inputRef = useRef(null);

  useEffect(() => {
    if (props.text || props.text == 0) inputRef.current.value = props.text;
  }, []);

  const changeText = (e) => {
    if (!props.noVoid) return;
    e.target.value === "" ? setVisible(true) : setVisible(false);
    props.onChange && props.onChange(e);
  };
  return (
    <>
      <textarea
        ref={inputRef}
        className={
          "resize-none block p-2.5 w-full text-sm text-gray-900 bg-gray-50 rounded-lg border border-gray-300" +
          props.className
        }
        {...props}
        onChange={changeText}
      />
      <p className="mt-2 text-red-500 text-xs italic" hidden={!visible}>
        Este campo es obligatorio!
      </p>
    </>
  );
}

export default TextArea;
