import NavBar from "../../components/NavBar/NavBar";
import Footer from "../../components/Footer/Footer";
import TitlePage from "../../components/TitlePage/TitlePage";
import TableColumn from "../../components/Table/TableColumn.js";
import TableRow from "../../components/Table/TableRow.js";
import Table from "../../components/Table/Table.js";
import Loading from "../../components/Loading/Loading";
import Alert from "../../components/PushMessage/Alert";
import SelectField from "../../components/SelectField/SelectField";
import { useState, useEffect } from "react";
import { getEditionsResume } from "../../api/editions";
import useUser from "../../hooks/auth/useUser";
import api from "../../api/api";

function ParejasResumen() {
  const [editionsResume, setEditionsResume] = useState(false);
  const [countCouples, setCountCouples] = useState(false);
  const [stateAlert, setStateAlert] = useState(false);
  const [stateLoading, setStateLoading] = useState(false);
  const [userData, setUserData] = useUser();

  const EditionsResume = () => {
    setStateAlert(false);
    setStateLoading(true);

    getEditionsResume(userData.token, (json, error) => {
      setStateLoading(false);

      if (json) {
        if (json.code == "ok") {
          setEditionsResume(json.data);
        } else {
          setStateAlert({
            type: "war",
            message: json.message,
          });
        }
      } else {
        setStateAlert({
          type: "err",
          message: "Ocurrio un error!, " + error,
        });
      }
    });
  };

  useEffect(() => {
    EditionsResume();
  }, []);

  const categorySelected = (element) => {
    let count = element?.target?.value;
    setCountCouples(count ? `${count} Pareja${count > 1 ? "s" : ""}` : false);
  };

  return (
    <>
      {stateLoading && <Loading />}
      {stateAlert && <Alert {...stateAlert} />}
      <NavBar />
      <div className="pl-2 pr-2 block h-full text-center bg-gray-100 pt-10 pb-20">
        <TitlePage text="Admin - Resumen de las parejas" />
        <br></br>
        <Table
          title="Todas las Ligas"
          columns={
            <TableColumn
              columns={[
                "Portada",
                "Nombre",
                "Categorias",
                "Cantidad de parejas",
              ]}
            />
          }
          imageHeader={
            <svg
              xmlns="http://www.w3.org/2000/svg"
              fill="none"
              viewBox="0 0 24 24"
              strokeWidth={1.5}
              stroke="currentColor"
              className="w-6 h-6"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                d="M6 6.878V6a2.25 2.25 0 012.25-2.25h7.5A2.25 2.25 0 0118 6v.878m-12 0c.235-.083.487-.128.75-.128h10.5c.263 0 .515.045.75.128m-12 0A2.25 2.25 0 004.5 9v.878m13.5-3A2.25 2.25 0 0119.5 9v.878m0 0a2.246 2.246 0 00-.75-.128H5.25c-.263 0-.515.045-.75.128m15 0A2.25 2.25 0 0121 12v6a2.25 2.25 0 01-2.25 2.25H5.25A2.25 2.25 0 013 18v-6c0-.98.626-1.813 1.5-2.122"
              />
            </svg>
          }
        >
          {editionsResume &&
            editionsResume.map((item, index) => (
              <tr key={index}>
                <TableRow>
                  <div className="m-2">
                    <div className="rounded-lg bg-gray-200 w-16 h-16">
                      <img
                        className="w-16 h-16 rounded-lg"
                        src={api.resources + item.cover}
                        alt=" "
                        style={{ objectFit: "cover" }}
                      />
                    </div>
                  </div>
                </TableRow>
                <TableRow>
                  <div className="m-4 p-2 font-semibold text-gray-800 text-sm">
                    {item.name}
                  </div>
                </TableRow>
                <TableRow>
                  <div className="flex mb-4 mt-2 mr-6 w-max">
                    <SelectField
                      disabledTitle={true}
                      onChange={categorySelected}
                      default="Seleccione una categoria"
                      body={item.editions_categories.map((e) => ({
                        value: e.category.enrollments_count,
                        label: e.category.name,
                      }))}
                      required
                      noVoid
                    />
                  </div>
                </TableRow>
                <TableRow>
                  {countCouples || "Seleccione una categoria"}
                </TableRow>
              </tr>
            ))}
        </Table>
      </div>
      <Footer />
    </>
  );
}

export default ParejasResumen;
