import { useNavigate } from "react-router-dom";

function DropDownItem(props) {
  return (
    <>
      <div className="group inline-block py-2">
        <button className="inline-flex w-auto justify-center rounded-md bg-white px-3 py-2 text-sm text-gray-400 hover:text-black">
          <span className="pr-1 font-semibold flex-1">{props.text}</span>
          <span className="mr-auto">
            <svg
              className="mt-1 fill-current h-4 w-4 transition duration-150 ease-in-out"
              xmlns="http://www.w3.org/2000/svg"
              viewBox="0 0 20 20"
            >
              <path d="M9.293 12.95l.707.707L15.657 8l-1.414-1.414L10 10.828 5.757 6.586 4.343 8z" />
            </svg>
          </span>
        </button>
        <ul className="w-auto bg-white border rounded-sm transform scale-0 group-hover:scale-100 absolute transition duration-150 ease-in-out origin-top p-2">
          {props.menu &&
            props.menu.map((item, index) =>
              item.menu ? (
                <SubMenu key={index} {...item} />
              ) : (
                <Item key={index} {...item} />
              )
            )}
        </ul>
      </div>
      <style
        dangerouslySetInnerHTML={{
          __html:
            "\n  /* since nested groupes are not supported we have to use \n     regular css for the nested dropdowns \n  */\n  li>ul                 { transform: translatex(100%) scale(0) }\n  li:hover>ul           { transform: translatex(101%) scale(1) }\n  li > button svg       { transform: rotate(-90deg) }\n  li:hover > button svg { transform: rotate(-270deg) }\n\n  /* Below styles fake what can be achieved with the tailwind config\n     you need to add the group-hover variant to scale and define your custom\n     min width style.\n  \t See https://codesandbox.io/s/tailwindcss-multilevel-dropdown-y91j7?file=/index.html\n  \t for implementation with config file\n  */\n  .group:hover .group-hover\\:scale-100 { transform: scale(1) }\n  .group:hover .group-hover\\:-rotate-180 { transform: rotate(180deg) }\n  .scale-0 { transform: scale(0) }\n  .min-w-32 { min-width: 8rem }\n",
        }}
      />
    </>
  );
}

function SubMenu(props) {
  return (
    <li className="p-1 py-1 hover:bg-gray-100">
      <div className="cursor-pointer w-full text-left flex items-center outline-none">
        <span className="opacity-70 hover:opacity-50 text-sm font-semibold p-1 pl-2 pr-2 flex-1">
          {props.name}
        </span>
        <span className="mr-auto">
          <svg
            className="opacity-60 hover:opacity-75 fill-current h-4 w-4 transition duration-150 ease-in-out"
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 20 20"
          >
            <path d="M9.293 12.95l.707.707L15.657 8l-1.414-1.414L10 10.828 5.757 6.586 4.343 8z" />
          </svg>
        </span>
      </div>
      <ul
        style={{ marginTop: "-35px" }}
        className="border bg-white rounded-sm absolute right-0 pt-2 pb-2 transition duration-150 ease-in-out origin-top-left"
      >
        {props.menu &&
          props.menu.map((item, index) => <Item key={index} {...item} />)}
      </ul>
    </li>
  );
}

function Item(props) {
  const navigation = useNavigate();

  return (
    <li className="min-w-max opacity-70 hover:opacity-50 text-sm font-semibold p-3 pt-1 pb-1 py-2 hover:bg-gray-100">
      <div
        className="cursor-pointer w-full h-full"
        onClick={() => navigation(props.link)}
      >
        {props.name}
      </div>
    </li>
  );
}

export default DropDownItem;
