import TextField from "./TextField.js";

function TextFieldLabel(props) {
  return (
    <div>
      <label
        htmlFor={props.name}
        className="block text-start mb-2 text-sm font-medium text-gray-900 dark:text-gray-900 truncate"
      >
        {props.required || props.noVoid ? (
          <span className="text-red-600">* </span>
        ) : (
          ""
        )}
        {props.title}
      </label>
      <TextField
        type={props.type}
        name={props.name}
        id={props.id}
        text={props.text}
        placeholder={props.placeholder}
        required={props.required}
        noVoid={props.noVoid}
        onChange={props.onChange}
      />

    </div>
  );
}

export default TextFieldLabel;
