import NavBar from "../../components/NavBar/NavBar";
import Footer from "../../components/Footer/Footer";
import TitlePage from "../../components/TitlePage/TitlePage";
import TableColumn from "../../components/Table/TableColumn.js";
import TableRow from "../../components/Table/TableRow.js";
import Table from "../../components/Table/Table.js";
import ItemEdit from "../../components/Table/ItemEdit";
import ItemDelete from "../../components/Table/ItemDelete";
import AddButton from "../../components/Buttons/AddButton";
import Loading from "../../components/Loading/Loading";
import Alert from "../../components/PushMessage/Alert";
import SponsorsUpdate from "./SponsorsUpdate";
import { useState, useEffect } from "react";
import useUser from "../../hooks/auth/useUser";
import ModalAccept from "../../components/Modal/ModalAccept";
import {
  deleteSponsor,
  getSponsors,
  newSponsor,
  updateSponsor,
  uploadSponsorImage
} from "../../api/sponsors";
import api from "../../api/api";

function Sponsors() {
  const [sponsors, setSponsors] = useState(false);
  const [stateAlert, setStateAlert] = useState(false);
  const [stateLoading, setStateLoading] = useState(false);
  const [edit, setEdit] = useState(false);
  const [modalAccept, setModalAccept] = useState(false);
  const [userData, setUserData] = useUser();

  const Sponsor = () => {
    setStateAlert(false);
    setStateLoading(true);

    getSponsors((json, error) => {
      setStateLoading(false);

      if (json) {
        if (json.code == "ok") {
          setSponsors(json.data);
        } else {
          setStateAlert({
            type: "war",
            message: json.message,
          });
        }
      } else {
        setStateAlert({
          type: "err",
          message: "Ocurrio un error!, " + error,
        });
      }
    });
  };

  useEffect(() => {
    Sponsor();
  }, []);

  const addSponsor = () => {
    setEdit({
      title: "Patrocinador nuevo",
      buttonText: "Crear nuevo",
      onSubmit: (data) => {
        setStateLoading(true);
        
        const image = data.image;
        delete data.image;

        newSponsor(data, userData.token, (json, error) => {
          setStateLoading(false);
          if (json.code == "ok") {
            setStateAlert({
              type: "ok",
              message: "Patrocinador creado con exito!",
            });
            if (image?.name == "") {
              setTimeout(() => Sponsor(), 1000);
              setEdit(false);
            } else uploadImage(json.id, image);
          } else {
            setStateAlert({
              type: "err",
              message: json.message || error,
            });
          }
        });
      },
      onCancel: () => setEdit(false),
    });
  };

  const editSponsor = (id) => {
    let data = sponsors.find((e) => e.id == id);
    setEdit({
      title: "Editar patrocinador",
      data: data,
      buttonText: "Guardar cambios",
      onSubmit: (dataUpdate) => {
        setStateLoading(true);
                
        const image = dataUpdate.image;
        delete data.image;

        updateSponsor(dataUpdate, userData.token, (json, error) => {
          setStateLoading(false);
          if (json.code == "ok") {
            setStateAlert({
              type: "ok",
              message: "Patrocinador actualizado con exito!",
            });
            if (image?.name == "") {
              setTimeout(() => Sponsor(), 1000);
              setEdit(false);
            } else uploadImage(id, image);
          } else {
            setStateAlert({
              type: "err",
              message: json.message || error,
            });
          }
        });
      },
      onCancel: () => setEdit(false),
    });
  };

  const removeSponsor = (id) => {
    setModalAccept({
      ok: () => {
        setStateLoading(true);
        setModalAccept(false);
        deleteSponsor(id, userData.token, (json, error) => {
          setStateLoading(false);
          if (json.code == "ok") {
            setStateAlert({
              type: "ok",
              message: "Eliminado con exito!",
            });
            setTimeout(() => Sponsor(), 1000);
            setEdit(false);
          } else {
            setStateAlert({
              type: "err",
              message: json.message || error,
            });
          }
        });
      },
      cancel: () => setModalAccept(false),
    });
  };

  const uploadImage = (id, image) => {
    const formData = new FormData();
    formData.append("id", id);
    formData.append("photo", image);

    setStateLoading(true);

    uploadSponsorImage(formData, userData.token, (json, error) => {
      setStateLoading(false);
      if (json.code == "ok") {
        setStateAlert({
          type: "ok",
          message: "Foto del patrocinador subida con exito!",
        });
        setTimeout(() => Sponsor(), 1000);
        setEdit(false);
      } else {
        setStateAlert({
          type: "err",
          message: json.message || error,
        });
      }
      //console.log(json);
    });
  };

  return (
    <>
      {stateLoading && <Loading />}
      {stateAlert && <Alert {...stateAlert} />}
      {edit && <SponsorsUpdate {...edit} />}
      {modalAccept && <ModalAccept {...modalAccept} />}
      <NavBar />
      <div className="pl-2 pr-2 block h-full text-center bg-gray-100 pt-10 pb-20">
        <TitlePage text="Admin - Patrocinadores" />
        <div className="flex justify-end items-end lg:pr-32 mb-6">
          <div className="w-max h-max" onClick={addSponsor}>
            <AddButton />
          </div>
        </div>
        <Table
          title="Patrocinadores"
          columns={
            <TableColumn
              columns={[
                "Foto",
                "Nombre",
                "Fecha de creación",
                "Fecha de actualización",
                "Acciones",
              ]}
            />
          }
          imageHeader={
            <svg
              xmlns="http://www.w3.org/2000/svg"
              fill="none"
              viewBox="0 0 24 24"
              strokeWidth={1.5}
              stroke="currentColor"
              className="w-6 h-6"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                d="M16.712 4.33a9.027 9.027 0 011.652 1.306c.51.51.944 1.064 1.306 1.652M16.712 4.33l-3.448 4.138m3.448-4.138a9.014 9.014 0 00-9.424 0M19.67 7.288l-4.138 3.448m4.138-3.448a9.014 9.014 0 010 9.424m-4.138-5.976a3.736 3.736 0 00-.88-1.388 3.737 3.737 0 00-1.388-.88m2.268 2.268a3.765 3.765 0 010 2.528m-2.268-4.796a3.765 3.765 0 00-2.528 0m4.796 4.796c-.181.506-.475.982-.88 1.388a3.736 3.736 0 01-1.388.88m2.268-2.268l4.138 3.448m0 0a9.027 9.027 0 01-1.306 1.652c-.51.51-1.064.944-1.652 1.306m0 0l-3.448-4.138m3.448 4.138a9.014 9.014 0 01-9.424 0m5.976-4.138a3.765 3.765 0 01-2.528 0m0 0a3.736 3.736 0 01-1.388-.88 3.737 3.737 0 01-.88-1.388m2.268 2.268L7.288 19.67m0 0a9.024 9.024 0 01-1.652-1.306 9.027 9.027 0 01-1.306-1.652m0 0l4.138-3.448M4.33 16.712a9.014 9.014 0 010-9.424m4.138 5.976a3.765 3.765 0 010-2.528m0 0c.181-.506.475-.982.88-1.388a3.736 3.736 0 011.388-.88m-2.268 2.268L4.33 7.288m6.406 1.18L7.288 4.33m0 0a9.024 9.024 0 00-1.652 1.306A9.025 9.025 0 004.33 7.288"
              />
            </svg>
          }
        >
          {sponsors &&
            sponsors.map((item, index) => (
              <tr key={index}>
                <TableRow>
                  <div className="rounded-lg bg-gray-200 w-16 h-16 mt-1 mb-1">
                    <img
                      className="w-16 h-16 rounded-lg"
                      src={item.photo ? api.resources + item.photo : null}
                      alt=" "
                      style={{ objectFit: "cover" }}
                    />
                  </div>
                </TableRow>
                <TableRow>
                  <div className="p-3 font-semibold text-gray-800 text-sm">
                    {item.name}
                  </div>
                </TableRow>
                <TableRow>
                  <div className="font-semibold text-blue-500 text-sm">
                    {new Date(item.created_at).toLocaleString()}
                  </div>
                </TableRow>
                <TableRow>
                  <div className="font-semibold text-red-500 text-sm">
                    {new Date(item.updated_at).toLocaleString()}
                  </div>
                </TableRow>
                <TableRow>
                  <div className="flex">
                    <ItemEdit id={item.id} onClick={editSponsor} />
                    <ItemDelete id={item.id} onClick={removeSponsor} />
                  </div>
                </TableRow>
              </tr>
            ))}
        </Table>
      </div>
      <Footer />
    </>
  );
}

export default Sponsors;
