import React, { useEffect, useRef, useState } from "react";
import NavBar from "../../components/NavBar/NavBar";
import Footer from "../../components/Footer/Footer";
import api from "../../api/api";
import "./css/publicGallery.css";
import Image from "../../components/Image";
import { getGalleries } from "../../api/gallery";
import Alert from "../../components/PushMessage/Alert";
import Loading from "../../components/Loading/Loading";

function PublicGallery() {
  const [images, setImages] = useState([]);
  const [stateLoading, setStateLoading] = useState(false);
  const [stateAlert, setStateAlert] = useState(false);

  const randomSord = () => Math.random() - 0.5;

  useEffect(() => {
    Galleries();
  }, []);

  const Galleries = () => {
    setStateAlert(false);
    setStateLoading(true);

    getGalleries((json, error) => {
      setStateLoading(false);

      if (json) {
        if (json.code == "ok") {
          json.data.sort(randomSord);
          setImages(json.data);
        } else {
          setStateAlert({
            type: "war",
            message: json.message,
          });
        }
      } else {
        setStateAlert({
          type: "err",
          message: "Ocurrio un error!, " + error,
        });
      }
    });
  };

  return (
    <>
      {stateAlert && <Alert {...stateAlert} />}
      {stateLoading && <Loading />}
      <div className="flex flex-col">
        <NavBar />
        <div className="bg-gray-100 p-10">
          <div className="grid-gallery self-center py-3">
            {images.map(({ img, id }) => (
              <figure className="card" key={id}>
                <Image
                  src={api.resources + img}
                  styles={{ width: "100%", objectFit: "cover" }}
                />
              </figure>
            ))}
          </div>
        </div>
        <Footer />
      </div>
    </>
  );
}

export default PublicGallery;
