import TitlePage from "../../components/TitlePage/TitlePage";
import ModalAccept from "../../components/Modal/ModalAccept";
import TextFieldLabel from "../../components/TextField/TextFieldLabel";
import SelectField from "../../components/SelectField/SelectField";
import PrimaryButton from "../../components/Buttons/PrimaryButton";
import TextArea from "../../components/TextField/TextArea";
import api from "../../api/api";
import { useState } from "react";

function ParejasUpdate(props) {

  const [modalAccept, setModalAccept] = useState(false);

  const submit = (form) => {
    form.preventDefault();
    const formValues = convertToNumbers(
      Object.fromEntries(new FormData(form.target).entries())
    );
    formValues.coupleId = props?.data?.couple?.id;
    setModalAccept({
      ok: () => {
        setModalAccept(false);
        props.onSubmit(formValues);
      },
      cancel: () => setModalAccept(false),
    });
  };

  const convertToNumbers = (formValues) => {
    const result = {};
    for (const [key, value] of Object.entries(formValues)) {
      result[key] = isNaN(value) ? value : Number(value);
    }
    return result;
  };

  return (
    <>
      {modalAccept && <ModalAccept {...modalAccept} />}
      <div className="align-center absolute pl-2 pr-2 w-full h-screen text-center bg-gray-100 pb-20">
        <div className="flex justify-end items-end mt-4 mr-4">
          <svg
            xmlns="http://www.w3.org/2000/svg"
            fill="none"
            viewBox="0 0 24 24"
            strokeWidth={1.5}
            stroke="currentColor"
            className="w-6 h-6 cursor-pointer"
            onClick={props.onCancel}
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              d="M6 18L18 6M6 6l12 12"
            />
          </svg>
        </div>
        <TitlePage text="Editar pareja" />
        <form onSubmit={submit}>
          <div className="m-auto pt-6 grid grid-cols-2 max-[600px]:grid-cols-1 max-[600px]:p-4 XXmd:grid-cols-3 md:max-w-xl gap-4 w-screen max-w-md">
            <div className="p-2">
              <div className="bg-gray-200 rounded-full w-40 h-40">
                <img
                  className="rounded-full w-40 h-40"
                  style={{ objectFit: "cover" }}
                  src={api.resources + props?.data?.couple?.avatar}
                  alt=" "
                />
              </div>
            </div>
            <br></br>
            <div className="w-full">
              <TextFieldLabel
                title="Nombre"
                type="text"
                name="name"
                placeholder="Nombre de la pareja"
                text={props?.data?.couple?.name}
                required
                noVoid
              />
            </div>
            <div className="w-full">
              <SelectField
                title="Jugador 1"
                name="player_1_id"
                default="Seleccione el jugador 1"
                body={
                  props?.data?.users &&
                  props?.data?.users.map((item) => ({
                    value: item.id,
                    label: item.name,
                    selected: item.id == props?.data?.couple?.player_1_id
                  }))
                }
                required
              />
            </div>
            <div className="w-full">
              <SelectField
                title="Jugador 2"
                name="player_2_id"
                default="Seleccione el jugador 2"
                body={
                  props?.data?.users &&
                  props?.data?.users.map((item) => ({
                    value: item.id,
                    label: item.name,
                    selected: item.id == props?.data?.couple?.player_2_id
                  }))
                }
                required
              />
            </div>
            <div className="w-full">
              <SelectField
                title="Pareja activa"
                name="active"
                default="Seleccione el estado de la pareja"
                body={[
                  {
                    value: 0,
                    label: "No",
                    selected: !props?.data?.couple?.active,
                  },
                  {
                    value: 1,
                    label: "Si",
                    selected: props?.data?.couple?.active,
                  },
                ]}
                required
              />
            </div>
            <div className="w-full mt-4">
              <TextArea
                name="deactivation_reason"
                rows={4}
                placeholder="Motivo de desactivación..."
                text={props?.data?.couple?.deactivation_reason}
              />
            </div>
          </div>
          <div className="w-full mt-10">
            <PrimaryButton
              type="submit"
              className="rounded-lg"
              text="Guardar cambios"
            />
          </div>
        </form>
      </div>
    </>
  );
}

export default ParejasUpdate;
