import { Link } from "react-router-dom";
import { useState, useEffect } from "react";
import PrimaryButton from "../../components/Buttons/PrimaryButton.js";
import TextField from "../../components/TextField/TextField.js";
import Loading from "../../components/Loading/Loading.js";
import Alert from "../../components/PushMessage/Alert.js";
import FormAccount from "../../components/Form/FormAccount.js";
import { useNavigate } from "react-router-dom";
import { login } from "../../api/auth.js";
import useUser from "../../hooks/auth/useUser.js";

function Login() {
  const [stateLoading, setStateLoading] = useState(false);
  const [stateAlert, setStateAlert] = useState(false);
  const [userData, setUserData] = useUser();
  const navigate = useNavigate();

  useEffect(() => {
    if (userData.token != "") navigate("/");
  }, []);

  const submit = (form) => {
    setStateAlert(false);
    form.preventDefault();
    //Recuperar data de el form
    const formValues = Object.fromEntries(new FormData(form.target).entries());

    //Validar la data fromValues
    if (!formValues.user || !formValues.password) {
      setStateAlert({
        type: "err",
        message: "Llene todos los campos!",
      });
    } else {
      //Aqui login al backend

      //Login loading start
      setStateLoading(true);

      login(formValues.user, formValues.password, (json, error) => {
        //Login loading stop
        setStateLoading(false);

        if (json) {
          if (json.code != "error") {
            //Login successfull
            setStateAlert({
              type: "ok",
              message: "Login, iniciado correctamente!",
            });
            //Guardando token
            setUserData({
              token: json.data.access_token,
              permissions: json.data.user.roles.includes("Admin")
                ? "admin"
                : "user",
            });
            //Ir al home
            setTimeout(function(){
              //navigate("/");
              window.location.href = "/";
            }, 2000);
          } else {
            setStateAlert({
              type: "war",
              message: "Revise su email y password!, " + json.message,
            });
          }
        } else {
          //Error login
          setStateAlert({
            type: "err",
            message: "Ocurrio un error!, " + error,
          });
        }
      });
    }
  };

  return (
    <div className="bg-gray-50 h-screen">
      {stateAlert && <Alert {...stateAlert} />}
      {stateLoading && <Loading />}
      <FormAccount
        id="login"
        title="Inicia sesión en tu cuenta"
        submit={submit}
      >
        <div>
          <label
            htmlFor="user"
            className="block mb-2 text-sm font-medium text-gray-900 dark:text-gray-900"
          >
            Tu correo electrónico
          </label>
          <TextField
            type="email"
            name="user"
            id="user"
            placeholder="ejemplo@correo.com"
            noVoid
            required
          />
        </div>
        <div>
          <label
            htmlFor="password"
            className="block mb-2 text-sm font-medium text-gray-900 dark:text-gray-900"
          >
            Contraseña
          </label>
          <TextField
            type="password"
            name="password"
            id="password"
            placeholder="••••••••"
            noVoid
            required
          />
        </div>
        <div className="flex items-center justify-between">
          <Link
            to="/recuperar"
            className="cursor-pointer text-sm font-medium text-primary-600 hover:underline dark:text-primary-500"
          >
            ¿Olvidaste tu contraseña?
          </Link>
        </div>
        <PrimaryButton
          type="submit"
          className="w-full focus:ring-4 focus:outline-none focus:ring-primary-300 font-medium rounded-lg text-sm px-5 py-2.5"
          text="Iniciar sesión"
        />
        <p className="text-sm font-light text-gray-500 dark:text-gray-500">
          ¿Aún no tienes una cuenta?{" "}
          <Link
            to={"/registro"}
            className="font-medium text-primary-600 hover:underline dark:text-primary-600"
          >
            Registrarse
          </Link>
        </p>
      </FormAccount>
    </div>
  );
}

export default Login;
