import { Link } from "react-router-dom";
import { useState } from "react";
import PrimaryButton from "../../components/Buttons/PrimaryButton.js";
import TextField from "../../components/TextField/TextField.js";
import Loading from "../../components/Loading/Loading.js";
import Alert from "../../components/PushMessage/Alert.js";
import FormAccount from "../../components/Form/FormAccount.js";
import { forgotPassword } from "../../api/auth.js";

function RecuperarCuenta() {
  const [stateAlert, setStateAlert] = useState(false);
  const [stateLoading, setStateLoading] = useState(false);

  const submit = (form) => {
    setStateAlert(false);
    form.preventDefault();
    //Recuperar data de el form
    const formValues = Object.fromEntries(new FormData(form.target).entries());
    //Validar la data fromValues
    if (!formValues.user) {
      setStateAlert({
        type: "err",
        message: "Llene todos los campos!",
      });
    } else {
      //Aqui recuperar password al backend

      //Login loading start
      setStateLoading(true);

      forgotPassword(formValues.user, (json, error) => {
        //Login loading stop
        setStateLoading(false);

        if (json) {
          if (json.code != "error") {
            //Forgot password success
            setStateAlert({
              type: "ok",
              message: "Se envio el password a su correo!",
            });
          } else {
            setStateAlert({
              type: "war",
              message: "Revise su email!, " + json.message,
            });
          }
        } else {
          //Error recuperar password
          setStateAlert({
            type: "err",
            message: "Ocurrio un error!, " + error,
          });
        }
      });
    }
  };

  return (
    <div className="bg-gray-50">
      {stateAlert && <Alert {...stateAlert} />}
      {stateLoading && <Loading />}
      <FormAccount id="recuperar" title="Recuperar cuenta" submit={submit}>
        <div>
          <label
            htmlFor="user"
            className="block mb-2 text-sm font-medium text-gray-900 dark:text-white"
          >
            Tu correo electrónico
          </label>
          <TextField
            type="email"
            name="user"
            id="user"
            placeholder="ejemplo@correo.com"
            noVoid
            required
          />
        </div>
        <PrimaryButton
          type="submit"
          className="w-full focus:ring-4 focus:outline-none focus:ring-primary-300 font-medium rounded-lg text-sm px-5 py-2.5"
          text="Recuperar tu contraseña"
        />
        <p className="text-sm font-light text-gray-500 dark:text-gray-400">
          Quiero iniciar a mi cuenta!{" "}
          <Link
            to={"/login"}
            className="font-medium text-primary-600 hover:underline dark:text-primary-500"
          >
            Iniciar
          </Link>
        </p>
      </FormAccount>
    </div>
  );
}

export default RecuperarCuenta;
