import Api from "./api.js";
import { get, post, put, del } from "./request.js";

//callback (json, error)

const callbacks = (callback) => {
  return {
    ready: (json) => {
      if (json) callback(json, null);
      else
        callback(
          null,
          " Posiblemente acurrio error del servidor!, intentelo mas tarde."
        );
    },
    error: (error) => callback(null, error),
  };
};

export function getCrossoversTypes(callback) {
  get(Api.getCrossoversTypes, {
    ...callbacks(callback),
  });
}

export function getCrossoverTypeById(id, token, callback) {
  get(Api.getCrossoverTypeById + id, {
    headers: {
      Authorization: `Bearer ${token}`,
      Accept: "application/json",
    },
    ...callbacks(callback),
  });
}

export function newCrossoverType(data, token, callback) {
  post(Api.newCrossoverType, {
    headers: {
      'Content-Type': "application/json",
      Authorization: `Bearer ${token}`,
      Accept: "application/json",
    },
    body: data,
    ...callbacks(callback),
  });
}

export function updateCrossoverType(data, token, callback) {
  put(Api.updateCrossoverType, {
    headers: {
      'Content-Type': "application/json",
      Authorization: `Bearer ${token}`,
      Accept: "application/json",
    },
    body: data,
    ...callbacks(callback),
  });
}

export function deleteCrossoverType(id, token, callback) {
  del(Api.deleteCrossoverType, {
    headers: {
      'Content-Type': "application/json",
      Authorization: `Bearer ${token}`,
      Accept: "application/json",
    },
    body: { crossoverTypeId: id },
    ...callbacks(callback),
  });
}