import NavBar from "../../components/NavBar/NavBar";
import Footer from "../../components/Footer/Footer";
import TitlePage from "../../components/TitlePage/TitlePage";
import TableColumn from "../../components/Table/TableColumn.js";
import TableRow from "../../components/Table/TableRow.js";
import Table from "../../components/Table/Table.js";
import ItemEdit from "../../components/Table/ItemEdit";
import ItemDelete from "../../components/Table/ItemDelete";
import AddButton from "../../components/Buttons/AddButton";
import Loading from "../../components/Loading/Loading";
import Alert from "../../components/PushMessage/Alert";
import CampusesUpdate from "./CampusesUpdate";
import { useState, useEffect } from "react";
import useUser from "../../hooks/auth/useUser";
import ModalAccept from "../../components/Modal/ModalAccept";
import { deleteCampus, getCampuses, newCampus, updateCampus, uploadCampusCover } from "../../api/campuses";
import api from "../../api/api";

function Campuses() {
  const [campuses, setCampuses] = useState(false);
  const [stateAlert, setStateAlert] = useState(false);
  const [stateLoading, setStateLoading] = useState(false);
  const [edit, setEdit] = useState(false);
  const [modalAccept, setModalAccept] = useState(false);
  const [userData, setUserData] = useUser();

  const Campus = () => {
    setStateAlert(false);
    setStateLoading(true);

    getCampuses((json, error) => {
      setStateLoading(false);

      if (json) {
        if (json.code != "error") {
          setCampuses(json.data);
          console.log(json.data);
        } else {
          setStateAlert({
            type: "war",
            message: json.message,
          });
        }
      } else {
        setStateAlert({
          type: "err",
          message: "Ocurrio un error!, " + error,
        });
      }
    });
  };

  useEffect(() => {
    Campus();
  }, []);

  const addCampus = () => {
    setEdit({
      title: "Nueva sede",
      buttonText: "Crear sede",
      onSubmit: (data) => {
        setStateLoading(true);

        const image = data.image;
        delete data.image;

        newCampus(data, userData.token, (json, error) => {
          setStateLoading(false);
          if (json.code == "ok") {
            setStateAlert({
              type: "ok",
              message: "Sede creada con exito!",
            });
            if (image?.name == "") {
              setTimeout(() => Campus(), 1000);
              setEdit(false);
            } else uploadImage(json.id, image);
          } else {
            setStateAlert({
              type: "err",
              message: json.message || error,
            });
          }
        });
      },
      onCancel: () => setEdit(false),
    });
  };

  const editCampus = (id) => {
    let data = campuses.find((e) => e.id == id);
    setEdit({
      title: "Editar sede",
      data: data,
      buttonText: "Guardar cambios",
      onSubmit: (dataUpdate) => {
        setStateLoading(true);

        const image = dataUpdate.image;
        delete data.image;

        updateCampus(dataUpdate, userData.token, (json, error) => {
          setStateLoading(false);
          if (json.code == "ok") {
            setStateAlert({
              type: "ok",
              message: "Sede actualizada con exito!",
            });
            if (image?.name == "") {
              setTimeout(() => Campus(), 1000);
              setEdit(false);
            } else uploadImage(id, image);
          } else {
            setStateAlert({
              type: "err",
              message: json.message || error,
            });
          }
        });
      },
      onCancel: () => setEdit(false),
    });
  };

  const removeCampus = (id) => {
    setModalAccept({
      ok: () => {
        setStateLoading(true);
        setModalAccept(false);
        deleteCampus(id, userData.token, (json, error) => {
          setStateLoading(false);
          if (json.code == "ok") {
            setStateAlert({
              type: "ok",
              message: "Elemento eliminado con exito!",
            });
            setTimeout(() => Campus(), 1000);
            setEdit(false);
          } else {
            setStateAlert({
              type: "err",
              message: json.message || error,
            });
          }
        });
      },
      cancel: () => setModalAccept(false),
    });
  };

  const uploadImage = (id, image) => {
    const formData = new FormData();
    formData.append("id", id);
    formData.append("cover", image);

    setStateLoading(true);

    uploadCampusCover(formData, userData.token, (json, error) => {
      setStateLoading(false);
      if (json.code == "ok") {
        setStateAlert({
          type: "ok",
          message: "Foto de la sede subida con exito!",
        });
        setTimeout(() => Campus(), 1000);
        setEdit(false);
      } else {
        setStateAlert({
          type: "err",
          message: json.message || error,
        });
      }
      //console.log(json);
    });
  };

  return (
    <>
      {stateLoading && <Loading />}
      {stateAlert && <Alert {...stateAlert} />}
      {edit && <CampusesUpdate {...edit} />}
      {modalAccept && <ModalAccept {...modalAccept} />}
      <NavBar />
      <div className="pl-2 pr-2 block h-full text-center bg-gray-100 pt-10 pb-20">
        <TitlePage text="Admin - Sedes de padel" />
        <div className="flex justify-end items-end lg:pr-32 mb-6">
          <div className="w-max h-max" onClick={addCampus}>
            <AddButton />
          </div>
        </div>
        <Table
          title="Campos o sedes"
          columns={
            <TableColumn
              columns={[
                "Portada",
                "Nombre",
                "Fecha de creación",
                "Fecha de actualización",
                "Acciones",
              ]}
            />
          }
          imageHeader={
            <svg
              xmlns="http://www.w3.org/2000/svg"
              fill="none"
              viewBox="0 0 24 24"
              strokeWidth={1.5}
              stroke="currentColor"
              className="w-6 h-6"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                d="M2.25 21h19.5m-18-18v18m10.5-18v18m6-13.5V21M6.75 6.75h.75m-.75 3h.75m-.75 3h.75m3-6h.75m-.75 3h.75m-.75 3h.75M6.75 21v-3.375c0-.621.504-1.125 1.125-1.125h2.25c.621 0 1.125.504 1.125 1.125V21M3 3h12m-.75 4.5H21m-3.75 3.75h.008v.008h-.008v-.008zm0 3h.008v.008h-.008v-.008zm0 3h.008v.008h-.008v-.008z"
              />
            </svg>
          }
        >
          {campuses &&
            campuses.map((item, index) => (
              <tr key={index}>
                <TableRow>
                  <div className="rounded-lg bg-gray-200 w-16 h-16 mt-1 mb-1 mr-2">
                    <img
                      className="w-16 h-16 rounded-lg"
                      src={item.cover ? api.resources + item.cover : null}
                      alt=" "
                      style={{ objectFit: "cover" }}
                    />
                  </div>
                </TableRow>
                <TableRow>
                  <div className="font-semibold text-gray-800 text-sm">
                    {item.name}
                  </div>
                </TableRow>
                <TableRow>
                  <div className="font-semibold text-blue-500 text-sm">
                    {new Date(item.created_at).toLocaleString()}
                  </div>
                </TableRow>
                <TableRow>
                  <div className="font-semibold text-red-500 text-sm">
                    {new Date(item.updated_at).toLocaleString()}
                  </div>
                </TableRow>
                <TableRow>
                  <div className="flex">
                    <ItemEdit id={item.id} onClick={editCampus} />
                    <ItemDelete id={item.id} onClick={removeCampus} />
                  </div>
                </TableRow>
              </tr>
            ))}
        </Table>
      </div>
      <Footer />
    </>
  );
}

export default Campuses;
