import React from "react";
import TextFieldLabel from "../../../components/TextField/TextFieldLabel";
import PrimaryButton from "../../../components/Buttons/PrimaryButton";

const RegisterForm = ({ userData, phone, handleSubmit }) => (<form onSubmit={handleSubmit}>
    <h1 className="text-2xl mb-3">Registrar nuevo jugador</h1>
    <TextFieldLabel
        title="Nombre"
        type="text"
        name="name"
        id="name"
        placeholder="Tu nombre"
        noVoid
        required
    />
    <TextFieldLabel
        title="Apellidos"
        type="text"
        name="lastName"
        id="lastName"
        placeholder="Tus apellidos"
        noVoid
        required
    />
    <TextFieldLabel
        title="Correo electrónico"
        type="email"
        name="email"
        id="email"
        placeholder="ejemplo@correo.com"
        noVoid
        required
    />
    <input type="hidden" name="referenceCode" value={userData.id} />
    <input type="hidden" name="phone" value={phone} />
    <TextFieldLabel
        title="Contraseña para la cuenta"
        type="password"
        name="password"
        id="password"
        placeholder="No se permiten @!*.%"
        noVoid
        required
    />
    <PrimaryButton
        type="submit"
        className="w-full focus:ring-4 focus:outline-none focus:ring-primary-300 font-medium rounded-lg text-sm px-5 py-2.5 mt-8"
        text="Registrar a el nuevo usuario"
    />

</form>);

export default RegisterForm;