import "./FrontPage.css";
import NavBar from "../../components/NavBar/NavBar.js";
import Slider from "../../components/Slider/Slider.js";
import PrimaryButton from "../../components/Buttons/PrimaryButton.js";
import Footer from "../../components/Footer/Footer.js";
import NewsCard from "../../components/Card/NewsCard";
import { useEffect, useState } from "react";
import { getActivesNews } from "../../api/news";
import Alert from "../../components/PushMessage/Alert";
import Loading from "../../components/Loading/Loading";
import useUser from "../../hooks/auth/useUser";
import api from "../../api/api";
import { getSponsors } from "../../api/sponsors";
import { useNavigate } from "react-router-dom";
import { getObtenerDatos } from "../../api/frontPage";

function FrontPage() {
  const [stateAlert, setStateAlert] = useState(false);
  const [stateLoading, setStateLoading] = useState(false);
  const [news, setNews] = useState(false);
  const [sponsors, setSponsors] = useState(false);
  const [datosPadel, setDatosPadel] = useState(false);
  const [userData, setUserData] = useUser();
  const navigate = useNavigate();

  const ActivesNew = () => {
    setStateAlert(false);
    setStateLoading(true);

    getActivesNews((json, error) => {
      setStateLoading(false);
      if (json) {
        if (json.code == "ok") {
          setNews(json.data);
          //console.log(json);
        } else {
          setStateAlert({
            type: "war",
            message: json.message,
          });
        }
      } else {
        setStateAlert({
          type: "err",
          message: "Ocurrio un error!, " + error,
        });
      }
    });
  };

  const Sponsor = () => {
    setStateAlert(false);
    setStateLoading(true);

    getSponsors((json, error) => {
      setStateLoading(false);
      if (json) {
        if (json.code == "ok") {
          setSponsors(json.data);
          //console.log(json);
        } else {
          setStateAlert({
            type: "war",
            message: json.message,
          });
        }
      } else {
        setStateAlert({
          type: "err",
          message: "Ocurrio un error!, " + error,
        });
      }
    });
  };

  const ObtenerDatos = () => {
    setStateAlert(false);
    setStateLoading(true);

    getObtenerDatos((json, error) => {
      setStateLoading(false);
      if (json) {
        if (json.code == "ok") {
          setDatosPadel(json.data);
          //console.log(json);
        } else {
          setStateAlert({
            type: "war",
            message: json.message,
          });
        }
      } else {
        setStateAlert({
          type: "err",
          message: "Ocurrio un error!, " + error,
        });
      }
    });
  };

  useEffect(() => {
    ActivesNew();
    Sponsor();
    ObtenerDatos();
  }, []);

  const handleNavigate = () => {
    navigate('/enrollment')
  }

  return (
    <>
      {stateLoading && <Loading />}
      {stateAlert && <Alert {...stateAlert} />}
      <NavBar />
      <Slider
        images={[
          require("../../assets/slider 1.png"),
          require("../../assets/slider 2.png"),
          require("../../assets/slider 3.png"),
          require("../../assets/slider 4.png"),
          require("../../assets/slider 5.png"),
        ]}
      >
        <div className="absolute z-30 flex top-10 left-8 slider-main">
          <h1></h1>
          <p></p>
          <PrimaryButton
            text="Inscribirse"
            className="rounded-full button_inscription"
            to={'/enrollment'}
            handleCLick={handleNavigate}
          />
          <PrimaryButton
            text="Inscritos"
            className="rounded-full button_already_registered"
          />
        </div>
      </Slider>
      <div className="w-full mt-4 mb-16 flex justify-center">
        <div className=" rounded overflow-hidden shadow-full">
          <div className="px-6 py-4">
            <div className="font-bold text-xl mb-2 text-cyan-800">
              SUPER LIGA PROVINCIAL POR PAREJAS (Fase de liga y primeros cruces
              en tu zona)
            </div>
            <p className="text-gray-700 text-base">
              Ya está ABIERTA LA INSCRIPCIÓN para la edición 45!!!
            </p>
            <div className="text-gray-800 font-bolds text-m mt-2">
              Con 2 opciones de welcome pack:
              <ul className="mt-3 text-gray-600 ml-4">
                <li>
                  <strong className="text-violet-500">25€</strong>: 2 pares de
                  calcetines técnicos <em>Víbora</em>.
                </li>
                <li>
                  <strong className="text-violet-500">45€</strong>: chaqueta{" "}
                  <em>Bullpadel</em>.
                </li>
              </ul>
              <p className="mt-3 text-gray-500">
                En ambas opciones se ofrecen
                <strong className="text-yellow-500"> 32€</strong> en bonos.
                Además, habrá premios para las cuatro mejores parejas de cada
                categoría.
              </p>
            </div>
            <img
              className="mt-10 rounded-lg"
              src="https://padelamigos.es/wp-content/uploads/2022/12/45-PadelAmigos-arriba-600x263.jpg"
              alt="PadelAmigos"
            />
            <img
              className="mt-10 rounded-lg"
              src="https://padelamigos.es/wp-content/uploads/2022/12/45-PadelAmigos-abajo-416x400.jpg"
              alt="Padel Amigos"
            />
          </div>
        </div>
      </div>
      <div className="w-full mb-20 inline">
        <h1
          className="font-bold text-3xl text-center"
          style={{
            color: "#0a3380",
            paddingLeft: "60px",
            paddingRight: "60px",
          }}
        >
          <span className="text-yellow-400"> 5 razones </span>
          para formar parte de la gran familia de PadelAmigos
        </h1>
        <div className="mt-6 flex flex-wrap justify-center">
          <RazonItem
            image="https://padelamigos.es/wp-content/uploads/2019/12/iconos-padelamigos-13-75x75.png"
            title="DIVERSIÓN"
            text="Disfruta de una atmósfera distendida."
          />
          <RazonItem
            image="https://padelamigos.es/wp-content/uploads/2019/12/iconos-padelamigos-14-75x75.png"
            title="RESPETO"
            text="En un ambiente sano y cordial."
          />
          <RazonItem
            image="https://padelamigos.es/wp-content/uploads/2019/12/iconos-padelamigos-15-75x75.png"
            title="DEPORTE"
            text="Compite y, si puedes, ¡gana! 😉."
          />
          <RazonItem
            image="https://padelamigos.es/wp-content/uploads/2019/12/iconos-padelamigos-17-75x75.png"
            title="SALUD"
            text="¡Combate la vida sedentaria!."
          />
          <RazonItem
            image="https://padelamigos.es/wp-content/uploads/2019/12/iconos-padelamigos-16-75x75.png"
            title="REGALOS"
            text="Excelente calidad. Inscripciones, VIP y premios."
          />
        </div>
      </div>
      <div className="w-full mt-6 flex flex-wrap justify-center bg-blue-600">
        <AboutItem title={datosPadel?.years} text="Años" />
        <AboutItem title={datosPadel?.editions} text="Ediciones" />
        <AboutItem title={datosPadel?.users} text="Pádel Amigos" />
      </div>
      <div className="w-full inline">
        <h1
          className="mt-6 font-bold text-2xl text-center"
          style={{
            color: "#0a3380",
            paddingLeft: "60px",
            paddingRight: "60px",
          }}
        >
          ACTUALIDAD
        </h1>
        <div className="mt-10 mb-6 flex flex-shrink flex-wrap justify-center Xpadding-3 ">
          {news &&
            news.map((item, index) => (
              <NewsCard
                key={index}
                image={api.resources + item.photo}
                title={item.title}
                des={item.details}
                time={new Date(item.updated_at || item.update_at).toDateString()}
              />
            ))}
        </div>
        <div className="hidden w-full Xflex mb-10">
          <PrimaryButton className="mx-auto rounded-full" text="Ver mas" />
        </div>
      </div>
      <Sponsors
        title="PATROCINADORES"
        images={sponsors && sponsors.map(item => (
          api.resources + item.photo
        ))}
      />
      {/*<MapGoogle />*/}
      <Footer />
    </>
  );
}

//Componentes de la pagina que solo se usa aqui

function RazonItem(props) {
  return (
    <div className="text-center w-40 m-10">
      <div className="flex justify-content">
        <img
          src={props.image}
          className="mx-auto"
          alt=""
          loading="lazy"
          width="75"
          height="75"
        />
      </div>
      <h6 className="mt-2 font-bold">{props.title}</h6>
      <p className="mt-1">{props.text}</p>
    </div>
  );
}

function AboutItem(props) {
  return (
    <div className="text-center w-40 m-10">
      <h6 className="text-6xl mt-2 font-bold text-yellow-400">{props.title}</h6>
      <p className="text-2xl mt-1 text-white">{props.text}</p>
    </div>
  );
}

function Sponsors(props) {
  return (
    <div className="w-full inline">
      <h1
        className="mt-6 font-bold text-2xl text-center"
        style={{
          color: "#0a3380",
          paddingLeft: "60px",
          paddingRight: "60px",
        }}
      >
        {props.title}
      </h1>
      <div className="mt-10 mb-6 flex flex-wrap justify-center padding-3">
        {props.images &&
          props.images.map((item, index) => (
            <div key={index} className="pt-4 pb-20 max-w-[200px]">
              <img src={item} />
            </div>
          ))}
      </div>
    </div>
  );
}

