import React, { useEffect, useState } from 'react';
import "./css/bottomSheet.css"
import FLoatingActionButton from '../Buttons/FloatingActionButton';
import NotificationIcon from '../Icon/NotificationIcon';
import Ping from '../Icon/Ping';

const BottomSheet = ({ children, className, title }) => {
    const [isOpen, setIsOpen] = useState(false);

    function toggleSheet() {
        setIsOpen(!isOpen);
    }

    useEffect(() => {
        document.body.style.overflow = `${isOpen ? 'hidden' : 'auto'}`
    }, [isOpen])
    return (
        <div>
            <FLoatingActionButton className={`block md:hidden h-12 w-12 m-4 bg-blue-500 rounded-full shadow-lg z-50 ${isOpen && 'hidden'}`} handleClick={toggleSheet}>
                <NotificationIcon className="text-white" />
                <Ping className={'absolute right-0 top-0'} />
            </FLoatingActionButton>
            <div className={`bottom-sheet ${isOpen ? 'open' : ''} scale-x-90 ${className}`}>
                {/* Contenido del dialog */}
                <div className="bottom-sheet-header">
                    <h1 className="text-sm text-gray-500 flex flex-col">
                        <span className="w-full py-2 truncate  px-4">
                            {title}
                        </span>
                    </h1>
                </div>
                <div className="bottom-sheet-content">
                    {
                        children
                    }
                </div>
            </div>
            <div className={`bottom-sheet-overlay ${isOpen ? 'open' : ''}`} onClick={toggleSheet}></div>
        </div>
    );
}

export default BottomSheet;