import React from "react";
import NavBar from "../../components/NavBar/NavBar";
import Footer from "../../components/Footer/Footer";
import PrimaryButton from "../../components/Buttons/PrimaryButton";
import { useNavigate, useParams } from "react-router-dom";
import Goal from "../../components/Icon/Goal";


const Finish = () => {

    const navigate = useNavigate();
    const { enrollmentId } = useParams();
    return (
        <div className="h-screen flex flex-col">
            <NavBar />
            <div className="bg-gray-100 grow flex flex-col items-center justify-center px-4 text-ceter">
                <Goal className="h-1/6 w-1/6 mb-5 text-blue-800" />
                <h1 className="text-2xl h-fit text-center">Felicitaciones la inscripci&oacute;n ha finalizado exitosamente</h1>

                <small className="text-orange-600 mt-2">Inscripci&oacute;n pendiente de pago</small>
                <div className="flex flex-col md:flex-row justify-center" >
                    <PrimaryButton
                        type="submit"
                        id="payNow"
                        className="rounded-full  mb-4 mt-5 w-64 mx-5"
                        text={'Volver al home'}
                        handleCLick={() => navigate('/', { replace: -1 })}
                    />
                    <PrimaryButton
                        type="submit"
                        id="payNow"
                        className="rounded-full mb-2 md:mb-4 md:mt-5 px-4 w-64 mx-5"
                        text={'Pagar'}
                        handleCLick={() => navigate(`/enrollment/pay/` + enrollmentId, { replace: -1 })}
                    />
                </div>
            </div>
            <Footer />
        </div>
    );
}



export default Finish;