import Api from "./api.js";
import { get, post, put, del } from "./request.js";

// callback (data, error)

const callbacks = (callback) => {
  return {
    ready: (json) => {
      if (json) callback(json, null);
      else
        callback(
          null,
          " Posiblemente acurrio error del servidor!, intentelo mas tarde."
        );
    },
    error: (error) => callback(null, error),
  };
};

export function getAccidentInsurances(callback) {
  get(Api.getAccidentInsurances, {
    ...callbacks(callback),
  });
}

export function getAccidentInsuranceById(id, token, callback) {
  get(Api.getAccidentInsuranceById + id, {
    headers: {
      Authorization: `Bearer ${token}`,
      Accept: "application/json",
    },
    ...callbacks(callback),
  });
}

export function newAccidentInsurance(data, token, callback) {
  post(Api.newAccidentInsurance, {
    headers: {
      'Content-Type': "application/json",
      Authorization: `Bearer ${token}`,
      Accept: "application/json",
    },
    body: data,
    ...callbacks(callback),
  });
}

export function updateAccidentInsurance(data, token, callback) {
  put(Api.updateAccidentInsurance, {
    headers: {
      'Content-Type': "application/json",
      Authorization: `Bearer ${token}`,
      Accept: "application/json",
    },
    body: data,
    ...callbacks(callback),
  });
}

export function deleteAccidentInsurance(id, token, callback) {
  del(Api.deleteAccidentInsurance, {
    headers: {
      'Content-Type': "application/json",
      Authorization: `Bearer ${token}`,
      Accept: "application/json",
    },
    body: { accidentInsuranceId: id },
    ...callbacks(callback),
  });
}
