import NavBar from "../../components/NavBar/NavBar";
import Footer from "../../components/Footer/Footer";
import ModalAccept from "../../components/Modal/ModalAccept";
import PrimaryButton from "../../components/Buttons/PrimaryButton";
import TextFieldLabel from "../../components/TextField/TextFieldLabel.js";
import SelectField from "../../components/SelectField/SelectField";
import { useState, useEffect } from "react";
import useUser from "../../hooks/auth/useUser";
import Loading from "../../components/Loading/Loading";
import Alert from "../../components/PushMessage/Alert";
import { getMunicipalities } from "../../api/municipalities";
import { getGenders } from "../../api/genders";
import { getPositions } from "../../api/positions";
import { getLevels } from "../../api/levels";
import api from "../../api/api";
import TitlePage from "../../components/TitlePage/TitlePage";
import { updateUser } from "../../api/users";

function UsersUpdate(props) {
  const [municipalities, setMunicipalities] = useState(false);
  const [genders, setGenders] = useState(false);
  const [positions, setPositions] = useState(false);
  const [levels, setLevels] = useState(false);
  const [modalAccept, setModalAccept] = useState(false);
  const [stateAlert, setStateAlert] = useState(false);
  const [stateLoading, setStateLoading] = useState(false);
  const [userData, setUserData] = useUser();

  const Municipalities = () => {
    setStateAlert(false);
    setStateLoading(true);
    getMunicipalities((json, error) => {
      setStateLoading(false);

      if (json) {
        if (json.code == "ok") {
          setMunicipalities(json.data);
          //console.log(json.data);
        } else {
          setStateAlert({
            type: "war",
            message: json.message,
          });
        }
      } else {
        setStateAlert({
          type: "err",
          message: "Ocurrio un error!, " + error,
        });
      }
    });
  };

  const Genders = () => {
    setStateAlert(false);
    setStateLoading(true);
    getGenders((json, error) => {
      setStateLoading(false);

      if (json) {
        if (json.code == "ok") {
          setGenders(json.data);
          //console.log(json.data);
        } else {
          setStateAlert({
            type: "war",
            message: json.message,
          });
        }
      } else {
        setStateAlert({
          type: "err",
          message: "Ocurrio un error!, " + error,
        });
      }
    });
  };

  const Positions = () => {
    setStateAlert(false);
    setStateLoading(true);
    getPositions((json, error) => {
      setStateLoading(false);

      if (json) {
        if (json.code == "ok") {
          setPositions(json.data);
          //console.log(json.data);
        } else {
          setStateAlert({
            type: "war",
            message: json.message,
          });
        }
      } else {
        setStateAlert({
          type: "err",
          message: "Ocurrio un error!, " + error,
        });
      }
    });
  };

  const Levels = () => {
    setStateAlert(false);
    setStateLoading(true);
    getLevels((json, error) => {
      setStateLoading(false);

      if (json) {
        if (json.code == "ok") {
          setLevels(json.data);
          //console.log(json.data);
        } else {
          setStateAlert({
            type: "war",
            message: json.message,
          });
        }
      } else {
        setStateAlert({
          type: "err",
          message: "Ocurrio un error!, " + error,
        });
      }
    });
  };

  useEffect(() => {
    Municipalities();
    Genders();
    Positions();
    Levels();
    window.scrollTo(0, 0);
  }, []);

  const submit = (form) => {
    form.preventDefault();
    const formValues = convertToNumbers(
      Object.fromEntries(new FormData(form.target).entries())
    );
    formValues.documentId = props?.data?.document_id;
    setModalAccept({
      ok: () => {
        setModalAccept(false);
        setStateLoading(true);
        updateUser(props.data.id, formValues, userData.token, (json, error) => {
          setStateLoading(false);
          if (json.code == "ok") {
            setStateAlert({
              type: "ok",
              message: "Usuario actualizado con exito!",
            });
            setTimeout(() => props.onUpdate(), 1000);
          } else {
            setStateAlert({
              type: "err",
              message: json.message || error,
            });
          }
        });
      },
      cancel: () => setModalAccept(false),
    });
  };

  const convertToNumbers = (formValues) => {
    const result = {};
    for (const [key, value] of Object.entries(formValues)) {
      result[key] = isNaN(value) ? value : Number(value);
    }
    return result;
  };

  return (
    <>
      {stateLoading && <Loading />}
      {stateAlert && <Alert {...stateAlert} />}
      {modalAccept && <ModalAccept {...modalAccept} />}
      <NavBar />
      <div className="z-40 align-center absolute pl-2 pr-2 w-full h-full text-center bg-gray-100 pb-4">
        <div className="flex justify-end items-end mt-4 mr-4">
          <svg
            xmlns="http://www.w3.org/2000/svg"
            fill="none"
            viewBox="0 0 24 24"
            strokeWidth={1.5}
            stroke="currentColor"
            className="w-6 h-6 cursor-pointer"
            onClick={props.onCancel}
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              d="M6 18L18 6M6 6l12 12"
            />
          </svg>
        </div>
        <TitlePage text="Editar usuario" />
        <div className="flex justify-center">
          <form onSubmit={submit}>
            <div className="p-2">
              <div className="bg-gray-200 rounded-full w-40 h-40">
                <img
                  className="rounded-full w-40 h-40"
                  style={{ objectFit: "cover" }}
                  src={api.resources + props.data?.avatar}
                  alt=" "
                />
              </div>
            </div>
            <div className="pt-6 grid grid-cols-2 max-[600px]:grid-cols-1 max-[600px]:p-4 XXmd:grid-cols-3 md:max-w-xl gap-4 w-screen max-w-md">
              <div className="w-full">
                <TextFieldLabel
                  title="Nombre"
                  type="text"
                  name="name"
                  placeholder="Nombre de usuario"
                  text={props.data.name}
                  required
                  noVoid
                />
              </div>
              <div className="w-full">
                <TextFieldLabel
                  title="Apellidos"
                  type="text"
                  name="lastName"
                  placeholder="Apellidos del usuario"
                  text={props.data.last_name}
                  required
                  noVoid
                />
              </div>
              <div className="w-full">
                <TextFieldLabel
                  title="Correo electrónico"
                  type="text"
                  name="email"
                  placeholder="Correo del usuario"
                  text={props.data.email}
                  required
                  noVoid
                />
              </div>
              <div className="w-full">
                <TextFieldLabel
                  title="Teléfono"
                  type="phone"
                  name="phone"
                  placeholder="Teléfono del usuario"
                  text={props.data.phone}
                  required
                  noVoid
                />
              </div>
              <div className="w-full">
                <TextFieldLabel
                  title="Fecha de nacimiento"
                  type="date"
                  name="birthDate"
                  placeholder="Fecha de nacimiento"
                  text={props.data.birth_date}
                  required
                  noVoid
                />
              </div>
              <div className="w-full">
                <SelectField
                  title="Municipio"
                  name="municipalityId"
                  default="Seleccione el municipio"
                  body={
                    municipalities
                      ? municipalities.map((item) => ({
                          value: item.id,
                          label: item.name,
                          selected: item.id === props.data.municipality_id,
                        }))
                      : [{ value: 0, label: "Cargando..." }]
                  }
                  required
                />
              </div>
              <div className="w-full">
                <SelectField
                  title="Género"
                  name="genderId"
                  default="Seleccione el género"
                  body={
                    genders
                      ? genders.map((item) => ({
                          value: item.id,
                          label: item.name,
                          selected: item.id === props.data.gender_id,
                        }))
                      : [{ value: 0, label: "Cargando..." }]
                  }
                  required
                />
              </div>
              <div className="w-full">
                <SelectField
                  title="Posición pádel"
                  name="positionId"
                  default="Seleccione la posición de padel"
                  body={
                    positions
                      ? positions.map((item) => ({
                          value: item.id,
                          label: item.position,
                          selected: item.id === props.data.position_id,
                        }))
                      : [{ value: 0, label: "Cargando..." }]
                  }
                  required
                />
              </div>
              <div className="w-full">
                <SelectField
                  title="Nivel de padel"
                  name="levelId"
                  default="Seleccione el nivel de padel"
                  body={
                    levels
                      ? levels.map((item) => ({
                          value: item.id,
                          label: item.level,
                          selected: item.id === props.data.level_id,
                        }))
                      : [{ value: 0, label: "Cargando..." }]
                  }
                  required
                />
              </div>
              <div className="w-full">
                <TextFieldLabel
                  title="Documento de identidad"
                  type="text"
                  name="documentValue"
                  placeholder="Documento de identidad"
                  text={props.data?.document.value}
                  required
                  noVoid
                />
              </div>
            </div>
            <div className="w-full mt-10">
              <PrimaryButton
                type="submit"
                className="rounded-lg"
                text="Guardar cambios"
              />
            </div>
          </form>
        </div>
      </div>
      <Footer />
    </>
  );
}

export default UsersUpdate;
