import React, { useRef } from "react";
import TextFieldLabel from "../../../components/TextField/TextFieldLabel";
import SelectField from "../../../components/SelectField/SelectField";
import TextArea from "../../../components/TextField/TextArea";
import InputFile from "../../../components/InputFile";
import { Link } from "react-router-dom";
import CheckField from "../../../components/CheckField/CheckField";

const BasicInformationForm = ({ categories, setPhone, timesRef }) => {

    const binaryOptions = [{ value: 1, type: 'Sí' }, { value: 0, type: 'No' }];
    const file = useRef(null);
    return <>
        <div className="md:grid grid-cols-2 gap-2">
            <TextFieldLabel
                title="Nombre Art&iacute;stico de pareja"
                type="text"
                name="name"
                id="name"
                placeholder="Nombre"
                noVoid
                required
            />
            <SelectField
                title="Categor&iacute;a"
                name="category"
                id="category"
                default="Seleccione la categoría"
                body={
                    categories.length
                        ? categories.map((item) => ({
                            value: item.id,
                            label: item.name,
                        }))
                        : [{ value: 0, label: "Cargando..." }]
                }
                required
            />
            <TextFieldLabel
                title="Tel&eacute;fono de su compa&ntilde;ero"
                type="tel"
                name="telephone"
                id="telephone"
                placeholder="+34 xxx xxx xxx"
                required
                noVoid={true}
                onChange={(e) => setPhone(e.target.value)}
            />
            <SelectField
                title="¿Reserva pista?"
                name="reserveTrack"
                id="reserveTrack"
                default="Seleccione si desea reservar pista"
                body={

                    binaryOptions.map((item) => ({
                        value: item.value,
                        label: item.type,
                    }))

                }
                required
            />

            <SelectField
                title="¿Jugar en jornada inaugural?"
                name="playOpeningDay"
                id="playOpeningDay"
                default="Seleccione si desea jugar en jornada inaugural"
                body={
                    binaryOptions.map((item) => ({
                        value: item.value,
                        label: item.type,
                    }))

                }
                required
            />

            <InputFile inputRef={file} />
            
            <div className="col-span-2">
                <label htmlFor="observations">Observaciones</label>
                <TextArea
                    title="Observaciones"
                    name="observations"
                    id="observations"
                    rows={7}
                />
            </div>
        </div>
    </>
}

export default BasicInformationForm;