import TableColumn from "./TableColumn.js";
import TableRow from "./TableRow.js";
import Table from "./Table.js";
import ItemCouple from "./ItemCouple.js";
import ItemActions from "./ItemActions.js";
import ItemResults from "./ItemResults.js";

function TableJornada(props) {
  return (
    <Table
      title={props.title}
      columns={
        <TableColumn
          columns={[
            "Nombre Pareja 1",
            "Resultados",
            "Nombre Pareja 2",
            "Horario y Sede",
            "Acciones",
          ]}
        />
      }
    >
      {props.rows &&
        props.rows.map((item) => (
          <tr>
            <ItemCouple image={item.couple1.image} name={item.couple1.name} />
            <ItemResults
              set1={item.results.set1}
              set2={item.results.set2}
              other={item.results.other}
            />
            <ItemCouple image={item.couple2.image} name={item.couple2.name} />
            <TableRow>
              <div>{item.time}</div>
              <div className="font-semibold">{item.campus}</div>
            </TableRow>
            <ItemActions
              editState={item.actions.edit}
              commentState={item.actions.comment}
              videoState={item.actions.video}
              clickEdit={item.actions.clickEdit}
              clickComment={item.actions.clickComments}
              clickVideo={item.actions.clickVideo}
            />
          </tr>
        ))}
    </Table>
  );
}

export default TableJornada;
