import Api from "./api.js";
import { get, post, put, del } from "./request.js";

//callback (json, error)

const callbacks = (callback) => {
  return {
    ready: (json) => {
      if (json) callback(json, null);
      else
        callback(
          null,
          " Posiblemente acurrio error del servidor!, intentelo mas tarde."
        );
    },
    error: (error) => callback(null, error),
  };
};

export function getCategories(callback) {
  get(Api.getCategories, {
    ...callbacks(callback),
  });
}

export function getAvailableCategoriesByEdition(callback) {
  get(Api.getAvailableCategoriesByEdition, {
    ...callbacks(callback),
  });
}

export function getCategoryById(id, token, callback) {
  get(Api.getCategoryById + id, {
    headers: {
      'Content-Type': "application/json",
      Authorization: `Bearer ${token}`,
      Accept: "application/json",
    },
    ...callbacks(callback),
  });
}

export function newCategory(data, token, callback) {
  post(Api.newCategory, {
    headers: {
      'Content-Type': "application/json",
      Authorization: `Bearer ${token}`,
      Accept: "application/json",
    },
    body: data,
    ...callbacks(callback),
  });
}

export function updateCategory(data, token, callback) {
  put(Api.updateCategory, {
    headers: {
      'Content-Type': "application/json",
      Authorization: `Bearer ${token}`,
      Accept: "application/json",
    },
    body: data,
    ...callbacks(callback),
  });
}

export function deleteCategory(id, token, callback) {
  del(Api.deleteCategory, {
    headers: {
      'Content-Type': "application/json",
      Authorization: `Bearer ${token}`,
      Accept: "application/json",
    },
    body: { categoryId: id },
   ...callbacks(callback),
  });
}