import Api from "./api.js";
import { get, post, put, del } from "./request.js";

// callback (data, error)

const callbacks = (callback) => {
  return {
    ready: (json) => {
      if (json) callback(json, null);
      else
        callback(
          null,
          " Posiblemente acurrio error del servidor!, intentelo mas tarde."
        );
    },
    error: (error) => callback(null, error),
  };
};

export function getProvinces(callback) {
  get(Api.getProvinces, {
    ...callbacks(callback),
  });
}

export function getProvinceById(id, token, callback) {
  get(Api.getProvinceById + id, {
    headers: {
      Authorization: `Bearer ${token}`,
      Accept: "application/json",
      'Content-Type': "application/json",
    },
    ...callbacks(callback),
  });
}

export function newProvince(data, token, callback) {
  post(Api.newProvince, {
    headers: {
      Authorization: `Bearer ${token}`,
      Accept: "application/json",
      'Content-Type': "application/json",
    },
    body: data,
    ...callbacks(callback),
  });
}

export function updateProvince(data, token, callback) {
  put(Api.updateProvince, {
    headers: {
      Authorization: `Bearer ${token}`,
      Accept: "application/json",
      'Content-Type': "application/json",
    },
    body: data,
    ...callbacks(callback),
  });
}

export function deleteProvince(id, token, callback) {
  del(Api.deleteProvince, {
    headers: {
      Authorization: `Bearer ${token}`,
      Accept: "application/json",
      'Content-Type': "application/json",
    },
    body: { provinceId: id },
    ...callbacks(callback),
  });
}
